import axios from 'axios'
import { useCallback, useEffect } from 'react'
import { useAppStore } from '../index'

export const useBalance = () => {
  const { balance } = useAppStore()
  const { setBalance } = balance
  
  const changeBalance = useCallback((amount: number) => {
    setBalance(amount)
    axios.post('http://localhost:5000/balance', {
      amount: amount
    })
  }, [setBalance])
  
  useEffect(() => {
    axios.get('http://localhost:5000/balance')
      .then(res => {
        const balance = res.data.amount
        changeBalance(balance)
      })
  }, [changeBalance])
  
  return { balance, changeBalance }
}