import classNames from 'classnames'
import React, { useContext, useLayoutEffect, useState } from 'react'
import { useClickOutside } from 'react-click-outside-hook'
import { DropdownContext } from '../../../context/DropdownContext'
import {
  DropdownDividerInterface,
  DropdownInterface,
  DropdownItemInterface,
  DropdownMenuInterface,
  DropdownToggleInterface
} from './Dropdown.interface'
import './Dropdown.scss'

const useDropdownContext = () => {
  const context = useContext(DropdownContext)
  
  if (!context) throw new Error('DropdownContext used outside Dropdown component')
  
  return context
}

export const Dropdown = ({componentRef, classes, relative, children, onClick, show = false, ...restProps}: DropdownInterface) => {
  const [isToggle, setIsToggle] = useState(show)
  const [ref, hasClickedOutside] = useClickOutside()
  
  useLayoutEffect(() => {
    if (hasClickedOutside) setIsToggle(false)
  }, [hasClickedOutside])
  
  return (
    <DropdownContext.Provider value={ {isToggle, setIsToggle} }>
      <div ref={ ref } onClick={ onClick }
           className={ classNames('dropdown', classes, {'toggle': isToggle}, {'relative': relative}) } { ...restProps }>
        { children }
      </div>
    </DropdownContext.Provider>
  )
}

const DropdownToggle = ({classes, onClick, children, ...restProps}: DropdownToggleInterface) => {
  const {isToggle, setIsToggle} = useDropdownContext()
  
  const onClickFn = () => {
    if (onClick) onClick()
    setIsToggle(!isToggle)
  }
  
  return (
    <div
      className={ classNames('dropdown-toggle', classes) } { ...restProps }
      onClick={ onClickFn }
    >
      { children }
    </div>
  )
}

const DropdownMenu = ({classes, children, ...restProps}: DropdownMenuInterface) => {
  return (
    <div className={ classNames('dropdown-menu', classes) } { ...restProps }>
      { children }
    </div>
  )
}

const DropdownItem = ({classes, children, onClick, noToggleOnClick = false}: DropdownItemInterface) => {
  const {isToggle, setIsToggle} = useDropdownContext()

  const onClickFn = () => {
    if (!noToggleOnClick) {
      if (onClick) onClick()
      setIsToggle(!isToggle)
    }
  }
  
  return (
    <div className={ classNames('dropdown-item', classes) } onClick={ () => onClickFn() }>
      { children }
    </div>
  )
}

const DropdownDivider = ({classes, children, ...restProps}: DropdownDividerInterface) => {
  return (
    <div className={ classNames('dropdown-divider', classes) } { ...restProps }>
      { children }
    </div>
  )
}

Dropdown.Toggle = DropdownToggle
Dropdown.Menu = DropdownMenu
Dropdown.Item = DropdownItem
Dropdown.Divider = DropdownDivider