const SearchInput = ({ value, onChange, hideFullField = false, onClick }: any) => {
  return (
    <div
      className={
        classNames(
          'inventory-filter-block--input-block',
          'input-compound',
          hideFullField && 'hide-full-input'
        )
      }
      onClick={ onClick }
    >
      <span><SearchIcon/></span>
      <input
        type="text"
        className={
          classNames(
            'input'
          )
        }
        placeholder="Название предмета"
        value={ value }
        onChange={ (e) => onChange(e.target.value) }
        onFocus={ (e: any) => e.target.parentNode.classList.add('input-focus') }
        onBlur={ (e: any) => e.target.parentNode.classList.remove('input-focus') }
      />
    </div>
  )
}

interface iDropdownSelect {
  className: string | string[]
  options: any
  title: string
  onChange: any
  selected: any
}

const DropdownSelect = ({ className, options, title, selected, onChange }: iDropdownSelect) => {
  const dropdownRef = useRef<any>(null)
  
  const [isShow, setShow] = useState<boolean>(false)
  
  const toggleOption = (item: any) => {
    onChange(item.value === selected?.value ? null : item)
  }
  
  const handleClickOutside = () => setShow(false)
  useDetectOutsideClick(dropdownRef, handleClickOutside)
  
  return (
    <div
      className={
        classNames(
          'dropdown-menu',
          className
        )
      }
      ref={ dropdownRef }
    >
      <div
        className="dropdown-menu--head"
        onClick={
          (e: any) => {
            if (e.target.classList.contains('dropdown-menu--reset')) {
              e.preventDefault()
              toggleOption('')
            } else {
              setShow(!isShow)
            }
          }
        }
      >
        <div className="dropdown-menu--title">
          { selected ? selected.title : title }
        </div>
        <div className="dropdown-menu--controls">
          { selected &&
          <div
            className="dropdown-menu--reset">
            <AddIcon/>
          </div>
          }
          <div className={
            classNames(
              'dropdown-menu--arrow',
              isShow && 'active'
            )
          }
          />
        </div>
      </div>
      {
        isShow && <div className="dropdown-menu--options">
          {
            options.map((option: { title: string, value: string }) => {
              const isSelected = selected?.value === option.value
              
              return (
                <div
                  className={
                    classNames(
                      'dropdown-menu--option',
                      isSelected && 'selected'
                    )
                  }
                  key={ option.value }
                  onClick={ () => toggleOption(option) }
                >
                  { option.title }
                </div>
              )
            })
          }
        </div>
      }
    </div>
  )
}

const PriceSwitcher = ({ priceFilter, onPriceFilterChange }: any) => {
  const newState = priceFilter?.value === 'low' ? { title: 'По возрастанию', value: 'high' } : { title: 'По уменьшению', value: 'low' }
  return (
    <Button
      size="normal"
      color="secondary"
      classes={
        classNames(
          'price-switcher-filter',
          priceFilter?.value
        )
      }
      onClick={ () => onPriceFilterChange(newState) }
    >
      <SortIcon/>
    </Button>
  )
}

