import React from 'react'
import { CartItem } from '../ItemCard/CartItem'
import { MobileCard } from '../ItemCard/MobileCard'
import { NoItemsFound } from '../NoItemsFound/NoItemsFound'
import './ItemsList.scss'
import { TabletCard } from '../ItemCard/TabletCard'
import { LaptopCard } from '../ItemCard/LaptopCard'
import { DesktopSmallCard } from '../ItemCard/DesktopSmallCard'
import { DesktopLargeCard } from '../ItemCard/DesktopLargeCard'

interface ItemsListInterface {
  responsiveSize: 'mobile' | 'tablet' | 'laptop' | 'desktop-small' | 'desktop-large' | 'full'
  items: any
  onRemove: any
}

export const ItemsList = ({ responsiveSize, onRemove, items }: ItemsListInterface) => {
  return items.length ? items.map((item: any) => {
    const { id, icon, stickers, lock, type, shortExterior, exterior, name, price, steamPrice, float, tag, inspectLink } = item
    
    switch (responsiveSize) {
      case 'full':
        return (
          <CartItem
            id={ id }
            image={ icon }
            visual={ stickers }
            mainName={ type }
            secondaryName={ name }
            price={ price }
            steamPrice={ steamPrice }
            onRemove={ onRemove }
            exterior={ exterior }
            float={ float }
            inspectLink={ inspectLink }
            lock={ lock }
          />
        )
      case 'mobile':
        return (
          <MobileCard
            id={ id }
            image={ icon }
            visual={ stickers }
            mainName={ type }
            secondaryName={ name }
            price={ price }
            steamPrice={ steamPrice }
            onRemove={ onRemove }
            exterior={ exterior }
            tag={ tag }
            float={ float }
            inspectLink={ inspectLink }
            lock={ lock }
          />
        )
      case 'tablet':
        return (
          <TabletCard
            id={ id }
            image={ icon }
            visual={ stickers }
            mainName={ type }
            secondaryName={ name }
            price={ price }
            steamPrice={ steamPrice }
            onRemove={ onRemove }
            exterior={ exterior }
            tag={ tag }
            float={ float }
            inspectLink={ inspectLink }
            lock={ lock }
          />
        )
      case 'laptop':
        return (
          <LaptopCard
            id={ id }
            image={ icon }
            visual={ stickers }
            mainName={ type }
            secondaryName={ name }
            price={ price }
            steamPrice={ steamPrice }
            onRemove={ onRemove }
            exterior={ exterior }
            tag={ tag }
            float={ float }
            inspectLink={ inspectLink }
            lock={ lock }
          />
        )
      case 'desktop-small':
        return (
          <DesktopSmallCard
            id={ id }
            image={ icon }
            visual={ stickers }
            mainName={ type }
            secondaryName={ name }
            price={ price }
            steamPrice={ steamPrice }
            onRemove={ onRemove }
            exterior={ exterior }
            tag={ tag }
            float={ float }
            inspectLink={ inspectLink }
            lock={ lock }
          />
        )
      case 'desktop-large':
        return (
          <DesktopLargeCard
            id={ id }
            image={ icon }
            visual={ stickers }
            mainName={ type }
            secondaryName={ name }
            price={ price }
            steamPrice={ steamPrice }
            onRemove={ onRemove }
            shortExterior={shortExterior}
            exterior={ exterior }
            tag={ tag }
            float={ float }
            inspectLink={ inspectLink }
            lock={ lock }
          />
        )
    }
  }) : <NoItemsFound/>
}