import React, { useCallback, useContext, useState } from 'react'
import classNames from 'classnames'
import './Checkbox.scss'
import { CheckboxContext } from '../../../context/CheckboxContext'
import { CheckboxInputInterface, CheckboxInterface, CheckboxLabelInterface } from './Checkbox.interface'

const useCheckContext = () => {
  const context = useContext(CheckboxContext)
  
  if (!context) throw new Error('CheckboxContext used outside Checkbox component')
  
  return context
}

export const Checkbox = ({type, children, classes, id, size, checked = false, color, onChange, disabled, ...restProps}: CheckboxInterface) => {
  const [isChecked, setIsChecked] = useState(checked)
  const [indeterminate, setIndeterminate] = useState<any>(0)
  
  return (
    <CheckboxContext.Provider value={ {id, isChecked, setIsChecked, indeterminate, setIndeterminate} }>
      <div
        className={
          classNames(
            'checkbox',
            size ? `checkbox-${ size ? size : '' }` : '',
            color ? `checkbox-${ color ? color : '' }` : '',
            classes,
            type ? `checkbox-type-${ type ? type : '' }` : '',
          )
        }
      >
        { children }
      </div>
    </CheckboxContext.Provider>
  )
}

const CheckboxInput = ({ind}: CheckboxInputInterface) => {
  const {id, isChecked, setIsChecked, indeterminate, setIndeterminate} = useCheckContext()
  
  const indetSetter = useCallback((el) => {
    if (el && indeterminate === 2) {
      el.indeterminate = true
    }
  }, [indeterminate])
  
  const advance = () => {
    setIndeterminate(prev => (prev + 1) % 3)
  }
  
  if (ind) {
    return (
      <input
        id={ id }
        type="checkbox"
        checked={ indeterminate === 1 }
        ref={ indetSetter }
        onClick={ advance }
      />
    )
  }
  
  return (
    <input
      id={ id }
      type="checkbox"
      onChange={ () => setIsChecked(!isChecked) }
      checked={ isChecked }
    />
  )
}

const CheckboxLabel = ({children}: CheckboxLabelInterface) => {
  const {id} = useCheckContext()
  
  return (
    <label htmlFor={ id }>
      { children }
    </label>
  )
}

Checkbox.Input = CheckboxInput
Checkbox.Label = CheckboxLabel