import moment from 'moment'
import React from 'react'
import { ItemCard } from '../ItemCard/ItemCard'
import './ItemsList.scss'
import classNames from 'classnames'
import { ItemList } from '../ItemList/ItemList'

type ItemsList = {
  isInstantSell: boolean
  items: any
  size: string
  onItemActionClick: any
  selectedItems: any
  type: 'card' | 'list'
}

export const ItemsList = ({ type, onItemActionClick, items, size, isInstantSell, selectedItems }: ItemsList) => {
  return (
    <div
      className={
        classNames(
          'inventory-items-list',
          type === 'list' ? 'list' : 'cards'
        )
      }
    >
      {
        items && items.length > 0
          ? (
            items.map((item: any) => {
              const {
                id,
                game,
                exterior,
                inspectLink,
                image,
                stickers,
                weaponName,
                skinName,
                price,
                steamPrice,
                tag,
                float,
                unlockAt,
                status
              } = item
              
              const selected = !!selectedItems.filter((selectedItem: { id: any }) => selectedItem.id === id).length
              
              if (type === 'card') {
                return <ItemCard
                  selected={ selected }
                  onItemActionClick={ onItemActionClick }
                  instantSell={ isInstantSell }
                  id={ id }
                  size={ size }
                  game={ 'cs:go' }
                  exterior={ exterior }
                  inspectLink={ inspectLink }
                  image={ image }
                  stickers={ stickers }
                  weaponName={ weaponName }
                  skinName={ skinName }
                  price={ price }
                  steamPrice={ steamPrice }
                  tag={ tag }
                  float={ float }
                  lock={ unlockAt }
                  status={ status }
                />
              } else {
                return <ItemList
                  selected={ selected }
                  onItemActionClick={ onItemActionClick }
                  instantSell={ isInstantSell }
                  id={ id }
                  size={ size }
                  game={ 'cs:go' }
                  exterior={ exterior }
                  inspectLink={ inspectLink }
                  image={ image }
                  stickers={ stickers }
                  weaponName={ weaponName }
                  skinName={ skinName }
                  price={ price }
                  steamPrice={ steamPrice }
                  tag={ tag }
                  float={ float }
                  lock={ unlockAt }
                  status={ status }
                />
              }
            })
          )
          : (
            <div>Предметов нет</div>
          )
      }
    </div>
  )
}