import classNames from 'classnames'
import React, { useEffect, useLayoutEffect, useState } from 'react'
import { useMediaQuery } from 'react-responsive'
import StickyBox from 'react-sticky-box'
import { toast } from 'react-toastify'
import { ReactComponent as ArrowIcon } from '../../assets/icons/arrow.svg'
import { ReactComponent as CardIcon } from '../../assets/icons/card.svg'
import { ReactComponent as FilterIcon } from '../../assets/icons/filter.svg'
import { ReactComponent as ListIcon } from '../../assets/icons/list.svg'
import { ReactComponent as SortIcon } from '../../assets/icons/sort.svg'
import { Button } from '../../components/Controls/Controls'
import { FromDesktop, Laptop, Mobile, Tablet } from '../../components/Responsive/Responsive'
import { SidebarFilter } from '../../components/SidebarFilters/SidebarFilter'
import { csgoItems } from '../../utilities/items/csgo'
import { dotaItems } from '../../utilities/items/dota'
import { ItemsList } from '../SellerPage/LocalComponents/ItemsList/ItemsList'
import './MarketPage.scss'
import { GameSelector } from '../../newComponents/GameSelector/GameSelector'
import { ReactComponent as CSIcon } from '../../assets/icons/games/csgo.svg'
import { ReactComponent as DotaIcon } from '../../assets/icons/games/dota.svg'
import { ReactComponent as PubgIcon } from '../../assets/icons/games/pubg.svg'

type userStatistics = {
  registrationDate: number
  handOverTime: number
  probability: number
}
export const MarketPage = () => {
  document.title = 'Market • skinsmart'
  
  const [items, setItems] = useState(csgoItems())
  const [itemsDota, setItemsDota] = useState(dotaItems())
  
  const [isOpen, setIsOpen] = useState(true)
  const toggleSidebar = () => setIsOpen(!isOpen)
  
  const handleBuy = () => {
    toast.success('Вы купили предмет', { autoClose: 10000 })
  }
  const handleAddToCard = () => {
    toast.success('Вы добавили предмет в корзину', { autoClose: 10000 })
  }
  
  const [userStatistics, setUserStatistics] = useState<userStatistics>({
    registrationDate: 1609440340,
    handOverTime: 3000,
    probability: 95
  })
  
  const [userDays, setUserDays] = useState<number>(0)
  
  const getUserDays = (unix: number) => {
    const today = new Date()
    const userDate = new Date(unix * 1000)
    
    const utc1 = Date.UTC(userDate.getFullYear(), userDate.getMonth(), userDate.getDate())
    const utc2 = Date.UTC(today.getFullYear(), today.getMonth(), today.getDate())
    
    return Math.floor((utc2 - utc1) / (1000 * 60 * 60 * 24))
  }
  const declOfNum = (number: number, words: string[]) => words[(number % 100 > 4 && number % 100 < 20) ? 2 : [2, 0, 1, 1, 1, 2][(number % 10 < 5) ? number % 10 : 5]]
  
  useEffect(() => {
    const days = getUserDays(userStatistics.registrationDate)
    setUserDays(days)
  }, [userStatistics.registrationDate])
  
  const isPageTooSmallToShowSidebar = useMediaQuery({ maxWidth: 1400 })
  
  useEffect(() => {
    setIsOpen(!isPageTooSmallToShowSidebar)
  }, [isPageTooSmallToShowSidebar])
  
  const sidebarCoversPage = useMediaQuery({ maxWidth: 1024 })
  
  useLayoutEffect(() => {
    if (isOpen && sidebarCoversPage) document.body.style.overflow = 'hidden'
    if (!isOpen) document.body.style.overflow = 'auto'
  }, [isOpen, isPageTooSmallToShowSidebar, sidebarCoversPage])
  
  const [gameFilter, setGameFilter] = useState<any>({
    selectedGame: {
      title: 'CS:GO',
      icon: <CSIcon/>
    },
    options: ['CS:GO', 'Dota 2', 'PUBG']
  })
  
  const onGameChange = (game: string) => {
    setGameFilter({
      ...gameFilter,
      selectedGame: game
    })
  }
  
  return (
    <>
      <Mobile>
        <div
          className={
            classNames(
              'market-page',
              'mobile',
              (!isOpen && 'hide-sidebar')
            )
          }
        >
          <SidebarFilter
            type="small"
            isOpen={ isOpen }
            toggleSidebar={ toggleSidebar }
          />
          <div className="advanced-search-block">
            <div className="block">
              <div className="game-title">
                CS:GO
              </div>
              <div className="game-items-count">
                343 053 предметов
              </div>
            </div>
            <div className="filters">
              <Button
                size="small"
                color="secondary"
                square={ true }
                classes="filter filter-sidebar-btn"
                onClick={ toggleSidebar }
              >
                <FilterIcon/>
              </Button>
              <Button
                size="small"
                color="secondary"
                square={ true }
                classes="filter filter-sort-btn"
              >
                <SortIcon/>
              </Button>
            </div>
          </div>
          <div className="market-content">
            <div className="items-block">
              <ItemsList
                size="mobile"
                items={ items }
                buyHandler={ handleBuy }
                addToCartHandler={ handleAddToCard }
              />
            </div>
          </div>
        </div>
      </Mobile>
      <Tablet>
        <div
          className={
            classNames(
              'market-page',
              'tablet',
              (!isOpen && 'hide-sidebar')
            )
          }
        >
          <SidebarFilter
            type="small"
            isOpen={ isOpen }
            toggleSidebar={ toggleSidebar }
          />
          <div className="advanced-search-block">
            <div className="block">
              <div className="game-title">
                CS:GO
              </div>
              <div className="game-items-count">
                343 053 предметов
              </div>
            </div>
            <div className="filters">
              <Button
                size="small"
                color="secondary"
                square={ true }
                classes="filter filter-sidebar-btn"
                onClick={ toggleSidebar }
              >
                <FilterIcon/>
              </Button>
              <Button
                size="small"
                color="secondary"
                square={ true }
                classes="filter filter-sort-btn"
              >
                <SortIcon/>
              </Button>
            </div>
          </div>
          <div className="market-content">
            <div className="items-block">
              <ItemsList
                size="mobile"
                items={ items }
                buyHandler={ handleBuy }
                addToCartHandler={ handleAddToCard }
              />
            </div>
          </div>
        </div>
      </Tablet>
      <Laptop>
        <div className={
          classNames(
            'market-page',
            'laptop',
            (!isOpen && 'hide-sidebar')
          )
        }>
          <StickyBox
            className={
              classNames(
                'sticky-sidebar',
                (!isOpen && 'hide-sidebar')
              )
            }
            offsetTop={ 105 }
            offsetBottom={ 0 }
          >
            <SidebarFilter
              type="normal"
              isOpen={ isOpen }
              toggleSidebar={ toggleSidebar }
            />
          </StickyBox>
          <div
            className={
              classNames(
                'market-content',
                (!isOpen && 'hide-sidebar')
              )
            }
          >
            <div className="market-filter-block">
              <div className="left">
                <Button size="normal" color="outline" classes="hide-btn" onClick={ toggleSidebar }>
                  <span className="arrow"><ArrowIcon/></span>
                  { isOpen ? 'Показать' : 'Скрыть' }
                </Button>
                <div className="divider"/>
                <GameSelector/>
                <div className="divider"/>
                <div className="filters-amount">
                  <FilterIcon/>
                  <div>Фильтров: <span className="attention-text">3</span></div>
                </div>
              </div>
              <div className="right">
                <div className="view-btns">
                  <div className="list-btn"><ListIcon/></div>
                  <div className="card-btn active"><CardIcon/></div>
                </div>
              </div>
            </div>
            <div className="items-block">
              <ItemsList
                size="non-mobile"
                items={ items }
                buyHandler={ handleBuy }
                addToCartHandler={ handleAddToCard }
              />
            </div>
          </div>
        </div>
      </Laptop>
      <FromDesktop>
        <div className={
          classNames(
            'market-page',
            'desktop'
          )
        }>
          <StickyBox
            className={
              classNames(
                'sticky-sidebar',
                (!isOpen && 'hide-sidebar')
              )
            }
            offsetTop={ 105 }
            offsetBottom={ 0 }
          >
            <SidebarFilter
              type="normal"
              isOpen={ isOpen }
              toggleSidebar={ toggleSidebar }
            />
          </StickyBox>
          <div
            className={
              classNames(
                'market-content',
                (!isOpen && 'sidebar-toggled')
              )
            }
          >
            <div className="market-filter-block">
              <div className="left">
                <Button size="normal" color="outline" classes="hide-btn" onClick={ toggleSidebar }>
                  <span className="arrow"><ArrowIcon/></span>
                  { isOpen ? 'Показать' : 'Скрыть' }
                </Button>
                <div className="divider"/>
                <GameSelector/>
                <div className="divider"/>
                <div className="filters-amount">
                  <FilterIcon/>
                  <div>Фильтров: <span className="attention-text">3</span></div>
                </div>
              </div>
              <div className="right">
                <div className="view-btns">
                  <div className="list-btn"><ListIcon/></div>
                  <div className="card-btn active"><CardIcon/></div>
                </div>
              </div>
            </div>
            <div className="items-block">
              <ItemsList
                size="non-mobile"
                items={ items }
                buyHandler={ handleBuy }
                addToCartHandler={ handleAddToCard }
              />
            </div>
          </div>
        </div>
      </FromDesktop>
    </>
  )
}