import React from 'react'
import { ItemBlockInterface, ItemImageInterface, ItemInterface } from './Item.interface'
import classNames from 'classnames'
import './Item.scss'

export const Item = ({id, classes, children, onClick, type}: ItemInterface) => {
  return (
    <div
      className={
        classNames(
          'item',
          type,
          classes
        )
      }
    >
      { children }
    </div>
  )
}

const ItemBlock = ({classes, children}: ItemBlockInterface) => {
  return (
    <div
      className={
        classNames(
          'item-block',
          classes
        )
      }
    >
      { children }
    </div>
  )
}
const ItemImage = ({classes, src, children}: ItemImageInterface) => {
  if (src) {
    return (
      <div
        className={
          classNames(
            'item-image',
            classes
          )
        }
      >
        <img
          src={ src }
          alt=""
        />
      </div>
    )
  }
  
  return (
    <div
      className={
        classNames(
          'item-image',
          classes
        )
      }
    >
      { children }
    </div>
  )
}

const ItemDivider = () => {
  return (
    <div className="item-divider"/>
  )
}

Item.Block = ItemBlock
Item.Image = ItemImage
Item.Divider = ItemDivider