import React from 'react'
import './Loader.scss'

export const Loader = () => {
  return (
    <div className="loader-container">
      <div className="spinner">
        <div className="bounce1"/>
        <div className="bounce2"/>
        <div className="bounce3"/>
      </div>
      <div className="loader-text">
        LOADING
      </div>
    </div>
  )
}