import React from 'react'
import { ReactComponent as CSGO } from '../assets/icons/games/csgo.svg'
import { ReactComponent as DOTA } from '../assets/icons/games/dota.svg'
import { ReactComponent as PUBG } from '../assets/icons/games/pubg.svg'

export const useGameIcon = (game: string) => {
  switch (game) {
    case 'CS:GO':
      return <CSGO/>
    case 'DOTA 2':
      return <DOTA/>
    case 'PUBG':
      return <PUBG/>
  }
}