import React, { useState } from 'react'
import './DateSelector.scss'
import classNames from 'classnames'

interface iDateSelector {
  items: any
  onSelect: any
  defaultState: any
  className?: string
}

export const DateSelector = ({ className, items, defaultState, onSelect }: iDateSelector) => {
  const [activeIndex, setActiveIndex] = useState(items.map((item: number) => item === defaultState).indexOf(true))
  
  const onClick = (index: number) => {
    setActiveIndex(index)
    onSelect(items[index])
  }
  
  const translation: any = {
    week: 'неделя',
    month: 'месяц',
    '6 months': 'полгода',
    year: 'год'
  }
  
  return (
    <div
      className={
        classNames(
          'date-selector',
          className
        )
      }
    >
      {
        items.map((item: number, index: number) => {
          console.log(item)
          return (
            <div
              className={
                classNames(
                  (activeIndex === index && 'active'),
                  'date-selector-tab'
                )
              }
              key={ index }
              onClick={ () => onClick(index) }
            >
              { translation[item] }
            </div>
          )
        })
      }
    </div>
  )
}