import classNames from 'classnames'
import React, { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react'
import { createPortal } from 'react-dom'
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close.svg'
import { ReactComponent as SteamIcon } from '../../../../assets/icons/socials/steam.svg'
import { ReactComponent as BrowserIcon } from '../../../../assets/icons/browser.svg'
import { ReactComponent as RobotIcon } from '../../../../assets/icons/robot.svg'
import { Loader } from '../../../../components/Loader/Loader'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import './TradeModal.scss'
import { Button } from '../../../../components/Controls/Controls'

const modalElement: any = document.getElementById('modal-root')

export const TradeModal = ({ defaultOpened = false, size }: any, ref: any) => {
  const [isOpen, setIsOpen] = useState(defaultOpened)
  const close = useCallback(() => setIsOpen(false), [])
  
  useImperativeHandle(ref, () => ({
    open: () => setIsOpen(true),
    close
  }), [close])
  
  const handleEscape = useCallback((event) => {
    if (event.keyCode === 27) close()
  }, [close])
  
  useEffect(() => {
    if (isOpen) document.addEventListener('keydown', handleEscape, false)
    
    return (
      document.removeEventListener('keydown', handleEscape, false)
    )
  }, [handleEscape, isOpen])
  
  const modalBlockRef = useRef<any>(null)
  
  const handleClickOutside = () => {
    close()
  }
  
  useDetectOutsideClick(modalBlockRef, handleClickOutside)
  
  switch (size) {
    case 'mobile': {
      return createPortal(
        isOpen
          ? (
            <div
              className={
                classNames(
                  'modal-wrapper-trade-modal',
                  'mobile',
                  isOpen && 'active'
                )
              }
            >
              <div
                className={
                  classNames(
                    'modal-block'
                  )
                }
                ref={ modalBlockRef }
              >
                <div className="close-button" onClick={ close }>
                  <CloseIcon/>
                </div>
                <div className="modal-body">
                  <div className="trade-modal-title">
                    Подтвердите обмен
                  </div>
                  <div className="trade-modal-list">
                    <div className="trade-modal-list-head">
                      <div className="trade-modal-list-cell">
                        Номер бота
                      </div>
                      <div className="trade-modal-list-cell">
                        Регистрация
                      </div>
                    </div>
                    <div className="trade-modal-list-body">
                      <div className="trade-modal-list-row">
                        <div className="row">
                          <div className="bot-number">
                            <RobotIcon/>
                            54665
                          </div>
                          <div className="registration-date">
                            12.12.2012
                          </div>
                        </div>
                        <div className="row">
                          <div className="bot-level">
                            23 <span>LVL</span>
                          </div>
                          <div className="buttons">
                            <Button size="small" color="primary">Steam</Button>
                            <Button size="small" color="success">Браузер</Button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
          : null,
        modalElement
      )
    }
    case 'tablet': {
      return createPortal(
        isOpen
          ? (
            <div
              className={
                classNames(
                  'modal-wrapper-trade-modal',
                  'tablet',
                  isOpen && 'active'
                )
              }
            >
              <div
                className={
                  classNames(
                    'modal-block'
                  )
                }
                ref={ modalBlockRef }
              >
                <div className="close-button" onClick={ close }>
                  <CloseIcon/>
                </div>
                <div className="modal-body">
                  <div className="trade-modal-title">
                    Подтвердите обмен
                  </div>
                  <div className="trade-modal-list">
                    <div className="trade-modal-list-head">
                      <div className="trade-modal-list-cell">
                        Номер бота
                      </div>
                      <div className="trade-modal-list-cell">
                        Регистрация
                      </div>
                      <div className="trade-modal-list-cell">
                        Подтвердить обмен
                      </div>
                    </div>
                    <div className="trade-modal-list-body">
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          54665
                        </div>
                        <div className="bot-level">
                          23 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          12.12.2012
                        </div>
                        <div className="buttons">
                          <Button size="small" color="primary"><SteamIcon/> Steam</Button>
                          <Button size="small" color="success"><BrowserIcon/> Браузер</Button>
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator accepted">
                          <span className="indicator-circle"/>
                          Вы приняли трейд
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator sending">
                          <Loader/>
                          Отправляем трейд
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator canceled">
                          <span className="indicator-circle"/>
                          Трейд отменен
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator timed-out">
                          <span className="indicator-circle"/>
                          Время истекло
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
          : null,
        modalElement
      )
    }
    default: {
      return createPortal(
        isOpen
          ? (
            <div
              className={
                classNames(
                  'modal-wrapper-trade-modal',
                  'fullscreen',
                  isOpen && 'active'
                )
              }
            >
              <div
                className={
                  classNames(
                    'modal-block'
                  )
                }
                ref={ modalBlockRef }
              >
                <div className="close-button" onClick={ close }>
                  <CloseIcon/>
                </div>
                <div className="modal-body">
                  <div className="trade-modal-title">
                    Подтвердите обмен
                  </div>
                  <div className="trade-modal-list">
                    <div className="trade-modal-list-head">
                      <div className="trade-modal-list-cell">
                        Номер бота
                      </div>
                      <div className="trade-modal-list-cell">
                        Регистрация
                      </div>
                      <div className="trade-modal-list-cell">
                        Подтвердить обмен
                      </div>
                    </div>
                    <div className="trade-modal-list-body">
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          54665
                        </div>
                        <div className="bot-level">
                          23 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          12.12.2012
                        </div>
                        <div className="buttons">
                          <Button size="small" color="primary"><SteamIcon/> Steam</Button>
                          <Button size="small" color="success"><BrowserIcon/> Браузер</Button>
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator accepted">
                          <span className="indicator-circle"/>
                          Вы приняли трейд
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator sending">
                          <Loader/>
                          Отправляем трейд
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator canceled">
                          <span className="indicator-circle"/>
                          Трейд отменен
                        </div>
                      </div>
                      <div className="trade-modal-list-row">
                        <div className="bot-number">
                          <RobotIcon/>
                          4564
                        </div>
                        <div className="bot-level success">
                          55 <span>LVL</span>
                        </div>
                        <div className="registration-date">
                          01.01.2001
                        </div>
                        <div className="indicator timed-out">
                          <span className="indicator-circle"/>
                          Время истекло
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
          : null,
        modalElement
      )
    }
  }
}

export default forwardRef(TradeModal)