import classNames from 'classnames'
import moment from 'moment'
import React from 'react'
import { NavLink } from 'react-router-dom'
import { ReactComponent as BumpIcon } from '../../../../assets/icons/bump.svg'
import { ReactComponent as ClockIcon } from '../../../../assets/icons/clock.svg'
import { ReactComponent as EyeIcon } from '../../../../assets/icons/eye.svg'
import { ReactComponent as LockIcon } from '../../../../assets/icons/lock.svg'
import { ReactComponent as EditIcon } from '../../../../assets/icons/pencil.svg'
import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg'
import { ReactComponent as TagIcon } from '../../../../assets/icons/tag-item.svg'
import { ReactComponent as ThunderboltIcon } from '../../../../assets/icons/thunderbolt.svg'
import { ReactComponent as WithdrawIcon } from '../../../../assets/icons/withdraw.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { Floatbar } from '../../../../components/Floatbar/Floatbar'
import { ItemSticker } from '../../../../newComponents/ItemSticker/ItemSticker'
import './ItemCard.scss'
import { ReactComponent as CheckIcon } from '../../../../assets/icons/check.svg'

type ItemCard = {
  instantSell: boolean
  id: number
  size: string
  game: string
  exterior?: string
  inspectLink?: string
  image: any
  stickers?: {
    title: string
    wear: number
    icon: any
  }[]
  weaponName: string
  skinName: string
  price: number
  steamPrice: number
  tag?: string
  float?: number
  lock: number
  status: string
  onItemActionClick: any
  selected: any
}

const WeaponName = ({ name }: { name: string }) => {
  const words = name.toLowerCase().split(' ')
  
  const isStatTrak = words.includes('stattrak')
  const isSouvenir = words.includes('souvenir')
  
  const regexp = new RegExp(/\b(?!StatTrak|Souvenir)\b\S+/gm)
  const weaponName = name.match(regexp) ? name.match(regexp) : [0]
  
  return (
    <div
      className={
        classNames(
          'weapon-name',
          (isStatTrak && 'st'),
          (isSouvenir && 'souvenir'),
        )
      }
    >
      { name }
    </div>
  )
}

export const ItemCard = (
  {
    instantSell,
    id,
    size,
    game,
    exterior,
    inspectLink,
    image,
    stickers,
    weaponName,
    skinName,
    price,
    steamPrice,
    tag,
    float,
    lock,
    status,
    onItemActionClick,
    selected
  }: ItemCard) => {
  const beautifulFloat = float?.toFixed(3)
  const declOfNum = (number: number, words: string[]) => words[(number % 100 > 4 && number % 100 < 20) ? 2 : [2, 0, 1, 1, 1, 2][(number % 10 < 5) ? number % 10 : 5]]
  const discount = Math.round(((price / steamPrice) - 1) * 100)
  
  const getShortExterior = (exterior: string | undefined) => {
    if (!exterior) return ''
    
    switch (exterior.toLowerCase()) {
      case 'factory new':
        return 'fn'
      case 'minimal wear':
        return 'mw'
      case 'field-tested':
        return 'ft'
      case 'well-worn':
        return 'ww'
      case 'battle-scarred':
        return 'bs'
    }
  }
  
  const getUnlockTime = (unlockDate: number) => {
    const today = moment.unix(Date.now() / 1000)
    const unlockedAt = moment.unix(unlockDate)
    
    const duration = Math.ceil(moment.duration(unlockedAt.diff(today)).asMinutes())
    
    const result = duration < 60
      ? { duration, unit: 'minutes' }
      : duration < 1440
        ? { duration: Math.ceil(duration / 60), unit: 'hours' }
        : { duration: Math.ceil(duration / 60 / 24), unit: 'days' }
    
    return result
  }
  const lockData: any = getUnlockTime(lock)
  
  const depositItem = () => {
    console.log('send offer')
    console.log('deposit item')
    onItemActionClick('deposit', {
      id, price, exterior, inspectLink, image, stickers, weaponName, skinName, steamPrice, tag, float, lock, status
    })
  }
  const withdrawItem = () => {
    console.log('remove item from market')
    console.log('withdraw item')
    onItemActionClick('withdraw')
    onItemActionClick('deposit', {
      id, price, exterior, inspectLink, image, stickers, weaponName, skinName, steamPrice, tag, float, lock, status
    })
  }
  const sellItem = () => {
    console.log('send offer')
    console.log('sell item')
    onItemActionClick('sell', {
      id, price, exterior, inspectLink, image, stickers, weaponName, skinName, steamPrice, tag, float, lock, status
    })
  }
  const removeItemFromMarket = () => {
    console.log('remove item from market')
  }
  const bumpItem = () => {
    console.log('Bump item')
  }
  
  switch (size) {
    default:
      return (
        <div
          className={
            classNames(
              'item-card-inventory',
              'fullscreen',
              (selected && 'selected')
            )
          }
          key={ id }
        >
          {
            selected && <div className="selected-overlay">
              <div className="icon">
                <CheckIcon/>
              </div>
            </div>
          }
          <div
            className="item-top-container">
            <div
              className={
                classNames(
                  'item-lock',
                  lockData.duration <= 0 && 'unlocked'
                )
              }
            >
              { lockData.duration <= 0 ? <LockIcon/> : <ClockIcon/> }
              { lockData.duration <= 0 ? 'Разблок.' : lockData.duration } { lockData.duration <= 0 ? '' : lockData.unit === 'minutes' ? 'мин' : lockData.unit === 'hours' ? 'ч' : 'дн' }
            </div>
            <div className="item-exterior">
              { getShortExterior(exterior) }
            </div>
            <div className="item-links">
              <a href={ inspectLink } className="inspect-link">
                <EyeIcon/>
              </a>
              <NavLink to={ `/market/csgo/${ weaponName }/${ skinName }` } className="search-link">
                <SearchIcon/>
              </NavLink>
            </div>
          </div>
          <div className="item-visual">
            <div className="item-image">
              <img src={ image } alt=""/>
            </div>
            <div className="item-stickers">
              {
                stickers && stickers.map((sticker) => {
                  const { title, wear, icon } = sticker
                  
                  return (
                    <ItemSticker sticker={ icon } stickerTitle={ title } stickerWear={ wear }/>
                  )
                })
              }
            </div>
            {
              status === 'on sale' && (
                <div
                  className="item-withdraw-btn"
                  onClick={ withdrawItem }
                >
                  <WithdrawIcon/>
                  Вывести
                </div>
              )
            }
          </div>
          <div className="item-float-container">
            {
              float && (
                <>
                  <div className="float">{ beautifulFloat }</div>
                  <Floatbar fv={ float }/>
                </>
              )
            }
          </div>
          <div className="item-info-container">
            { tag &&
            <div className="item-tag">
              <div className="tag">
                { tag }
              </div>
              <div className="icon">
                <TagIcon/>
              </div>
            </div>
            }
            <div className="item-name-info">
              <WeaponName name={ weaponName }/>
              <div className="skin-name">
                { skinName }
              </div>
            </div>
            <div className="item-price-info">
              {
                instantSell
                  ?
                  (
                    <div className="price instant">
                      Рек.цена:
                      <span className="currency">$</span>
                      { (price * 1.25).toFixed(2) }
                    </div>
                  )
                  :
                  status === 'market'
                    ? (
                      <div className="price on-sale">
                        <div className="edit-price">
                          <EditIcon/>
                        </div>
                        <span className="currency">$</span>
                        { price.toFixed(2) }
                        <span className="discount">{ discount }%</span>
                      </div>
                    )
                    : (
                      <div className="price inventory">
                        Рек.цена:
                        <span className="currency">$</span>
                        { price.toFixed(2) }
                      </div>
                    )
              }
              <div className="steam-price">
                Цена в Steam: ${ steamPrice.toFixed(2) }
              </div>
            </div>
          </div>
          <div className="action-buttons">
            {
              instantSell
                ? (
                  <Button
                    size="normal"
                    color="success"
                    classes="main-action instant-sell"
                    onClick={ sellItem }
                  >
                    Продать за ${ price }
                  </Button>
                )
                : status === 'market'
                  ? (
                    <>
                      <Button
                        size="normal"
                        color="secondary"
                        classes="second-action"
                        square={ true }
                        onClick={ bumpItem }
                      >
                        <BumpIcon/>
                      </Button>
                      <Button
                        size="normal"
                        color="danger"
                        classes="main-action"
                        onClick={ removeItemFromMarket }
                      >
                        Снять с продажи
                      </Button>
                    </>
                  )
                  : (
                    <>
                      <Button
                        size="normal"
                        color="secondary"
                        classes="second-action"
                        square={ true }
                        onClick={ sellItem }
                      >
                        <ThunderboltIcon/>
                      </Button>
                      <Button
                        size="normal"
                        color="primary"
                        classes="main-action"
                        onClick={ () => {
                          if (status === 'steam') {
                            depositItem()
                          } else {
                            sellItem()
                          }
                        } }
                      >
                        {
                          status === 'steam' ? 'Депозит' : 'Продать'
                        }
                      </Button>
                    </>
                  )
            }
          </div>
        </div>
      )
  }
}