import React, { useContext, useState } from 'react'
import './Switch.scss'
import classNames from 'classnames'
import { SwitchInterface, SwitchLabelInterface } from './Switch.interface'
import { SwitchContext } from '../../../context/Switch.context'

const useSwitchContext = () => {
  const context = useContext(SwitchContext)
  
  if (!context) throw new Error('SwitchContext used outside Switch component')
  
  return context
}

export const Switch = ({children, classes, id, size, checked = false, ref, color, onChange, disabled, ...restProps}: SwitchInterface) => {
  const [isChecked, setIsChecked] = useState(checked)
  return (
    <SwitchContext.Provider value={ {id, isChecked, setIsChecked} }>
      <div
        className={
          classNames(
            'switch',
            size ? `switch-${ size ? size : '' }` : '',
            color ? `switch-${ color ? color : '' }` : '',
            classes
          )
        }
        ref={ ref }
      >
        { children }
      </div>
    </SwitchContext.Provider>
  )
}

const SwitchInput = () => {
  const {id, isChecked, setIsChecked} = useSwitchContext()
  
  return (
    <input
      id={ id }
      type="checkbox"
      onChange={ () => setIsChecked(!isChecked) }
      checked={ isChecked }
    />
  )
}

const SwitchLabel = ({children}: SwitchLabelInterface) => {
  const {id} = useSwitchContext()
  
  return (
    <label htmlFor={ id }>
      { children }
    </label>
  )
}

Switch.Input = SwitchInput
Switch.Label = SwitchLabel