import React from 'react'
import { NavLink } from 'react-router-dom'
import { Desktop, FullScreen, Laptop, LargeDesktop, Mobile, Tablet } from '../../../../components/Responsive/Responsive'
import './DepositCard.scss'

interface iDepositCard {
  amount: number
  show: boolean
}

const numberIsNegative = (x: number) => {
  const isNegative = Math.sign(x)
  
  return (isNegative < 0) ? (x / -1) : x
}

export const DepositCard = ({ amount, show }: iDepositCard) => {
  const beautifulNumber = Math.round(numberIsNegative(amount) * 100) / 100
  
  if (show) {
    return (
      <>
        <Mobile>
          <div className="deposit-card mobile-card">
            <div className="deposit-card--title">
              Недостаточно средств
            </div>
            <div className="deposit-card--description">
              Пожалуйста, пополните свой баланс, чтобы вы могли купить эти предметы.
            </div>
            <NavLink to="/balance" className="deposit-card--link">
              Депозит ${ beautifulNumber }
            </NavLink>
          </div>
        </Mobile>
        <Tablet>
          <div className="deposit-card tablet-card">
            <div className="deposit-card--title">
              Недостаточно средств
            </div>
            <div className="deposit-card--description">
              Пожалуйста, пополните свой баланс, чтобы вы могли купить эти предметы.
            </div>
            <NavLink to="/balance" className="deposit-card--link">
              Депозит ${ beautifulNumber }
            </NavLink>
          </div>
        </Tablet>
        <Laptop>
          <div className="deposit-card laptop-card">
            <div className="deposit-card--title">
              Недостаточно средств
            </div>
            <div className="deposit-card--description">
              Пожалуйста, пополните свой баланс, чтобы вы могли купить эти предметы.
            </div>
            <NavLink to="/balance" className="deposit-card--link">
              Депозит ${ beautifulNumber }
            </NavLink>
          </div>
        </Laptop>
        <Desktop>
          <div className="deposit-card desktop-card">
            <div className="deposit-card--title">
              Недостаточно средств
            </div>
            <div className="deposit-card--description">
              Пожалуйста, пополните свой баланс, чтобы вы могли купить эти предметы.
            </div>
            <NavLink to="/balance" className="deposit-card--link">
              Депозит ${ beautifulNumber }
            </NavLink>
          </div>
        </Desktop>
        <LargeDesktop>
          <div className="deposit-card large-desktop-card">
            <div className="deposit-card--title">
              Недостаточно средств
            </div>
            <div className="deposit-card--description">
              Пожалуйста, пополните свой баланс, чтобы вы могли купить эти предметы.
            </div>
            <NavLink to="/balance" className="deposit-card--link">
              Депозит ${ beautifulNumber }
            </NavLink>
          </div>
        </LargeDesktop>
        <FullScreen>
          <div className="deposit-card fullscreen-card">
            <div className="deposit-card--title">
              Недостаточно средств
            </div>
            <div className="deposit-card--description">
              Пожалуйста, пополните свой баланс, чтобы вы могли купить эти предметы.
            </div>
            <NavLink to="/balance" className="deposit-card--link">
              Депозит ${ beautifulNumber }
            </NavLink>
          </div>
        </FullScreen>
      </>
    )
  } else {
    return (
      <></>
    )
  }
}