import moment from 'moment'

export const getTimePassedSince = (time: number) => {
  const today = moment.unix(Date.now() / 1000)
  const date = moment.unix(time)
  
  const duration = -Math.ceil(moment.duration(date.diff(today)).asMinutes())
  
  const result = duration < 0
    ? { duration: 0, unit: 'future' }
    : duration < 60
      ? { duration, unit: 'minutes' }
      : duration < 1440
        ? { duration: Math.ceil(duration / 60), unit: 'hours' }
        : { duration: Math.ceil(duration / 60 / 24), unit: 'days' }
  
  return result
}