import React, { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react'
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import './PasswordChangeModal.scss'
import { createPortal } from 'react-dom'
import classNames from 'classnames'
import { ReactComponent as SecurityIcon } from '../../../../assets/icons/lock.svg'
import { useForm, SubmitHandler } from 'react-hook-form'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'

interface iPasswordInput {
  val: string
  onChange: any
  placeholder: string
  type: 'password' | 'text'
  title: string
}

const PasswordChangeModalInput = ({ title, type, val, onChange, placeholder }: iPasswordInput) => {
  const handleFocus = (e: any) => e.target.classList.add('input-focus')
  const handleBlur = (e: any) => e.target.classList.remove('input-focus')
  
  return (
    <div className="password-change-modal-input-block">
      <div className="password-change-modal-title">
        { title }
      </div>
      <input
        className="password-change-modal-input"
        type={ type }
        value={ val }
        onChange={ onChange }
        placeholder={ placeholder }
        onFocus={ handleFocus }
        onBlur={ handleBlur }
      />
    </div>
  )
}

interface iPasswordChangeModal {
  defaultOpened?: boolean
  oldPassword: string
  handleOldPasswordChange: any
  newPassword: string
  handleNewPasswordChange: any
  repeatPassword: string
  handleRepeatPasswordChange: any
  FACode: string
  handleFACodeChange: any
}

interface FormValues {
  oldPassword: string
  newPassword: string
  repeatPassword: string
  faCode: string
}

export const PasswordChangeModal = (
  {
    defaultOpened = false,
    oldPassword,
    newPassword,
    repeatPassword,
    FACode,
    handleOldPasswordChange,
    handleNewPasswordChange,
    handleRepeatPasswordChange,
    handleFACodeChange
  }: iPasswordChangeModal,
  ref: any
) => {
  const modalElement: any = document.getElementById('modal-root')
  
  const modalBlockRef = useRef<any>(null)
  
  const [isOpen, setOpen] = useState<boolean>(defaultOpened)
  
  const close = useCallback(() => setOpen(false), [])
  
  const handleClickOutside = () => close()
  
  const handleEscape = useCallback((event) => {
    if (event.keyCode === 27) close()
  }, [close])
  
  useDetectOutsideClick(modalBlockRef, handleClickOutside)
  
  useImperativeHandle(ref, () => ({
    open: () => setOpen(true),
    close
  }), [close])
  
  useEffect(() => {
    if (isOpen) document.addEventListener('keydown', handleEscape, false)
    
    return (
      document.removeEventListener('keydown', handleEscape, false)
    )
  }, [handleEscape, isOpen])
  
  const { register, handleSubmit, errors, formState, setError } = useForm({
    mode: 'onChange',
    reValidateMode: 'onChange',
    defaultValues: {
      oldPassword: '',
      newPassword: '',
      repeatPassword: '',
      faCode: ''
    },
    criteriaMode: 'all',
    shouldFocusError: true,
    shouldUnregister: true
  })
  
  const history = useHistory()
  
  const onSubmit: SubmitHandler<FormValues> = (data) => {
    if (errors.oldPassword || errors.newPassword || errors.repeatPassword || errors.faCode) {
      toast.error('Заполните все поля', { autoClose: 10000 })
    }
    
    if (!errors.oldPassword && !errors.newPassword && !errors.repeatPassword && !errors.faCode) {
      const userDataFromLocalStorage = localStorage.getItem('user')
      const userData = userDataFromLocalStorage && JSON.parse(userDataFromLocalStorage)
      const userPassword = userData.password
      
      if (userPassword) {
        if (userPassword === oldPassword) {
          toast.success('Пароль успешно изменен', { autoClose: 5000 })
          setTimeout(() => toast.info('Вы будете перенаправлены на форму входа', { autoClose: 5000 }), 1500)
          setTimeout(() => {
            history.push('/auth')
          }, 5000)
        } else {
          toast.error('Ваш пароль не совпадает с введенным', { autoClose: 5000 })
        }
      } else {
        toast.error('Как вы здесь оказались? Ваших данных нет в локальном хранилище. Вам нужно зарегистрироваться', { autoClose: 10000 })
      }
    }
  }
  
  const handleFocus = (e: any) => e.target.classList.add('input-focus')
  const handleBlur = (e: any) => e.target.classList.remove('input-focus')
  
  console.log(formState)
  
  return createPortal(
    isOpen ? (
      <div
        className={
          classNames(
            'password-modal-wrapper',
            isOpen && 'active'
          )
        }
      >
        <div
          className={
            classNames(
              'modal-block'
            )
          }
          ref={ modalBlockRef }
        >
          <div
            className="close-button"
            onClick={ close }
          >
            <CloseIcon/>
          </div>
          <form onSubmit={ handleSubmit(onSubmit) } className="modal-body">
            <div className="password-modal-icon">
              <SecurityIcon/>
            </div>
            <div className="password-modal-title">
              Смена пароля
            </div>
            <div className="modal-inputs">
              <div className="d-flex">
                <div className="password-change-modal-input-block">
                  <div className="password-change-modal-title">
                    Ваш пароль
                  </div>
                  <input
                    name="oldPassword"
                    ref={
                      register({
                        required: true,
                        minLength: 8,
                        maxLength: 24,
                      })
                    }
                    className={
                      classNames(
                        'password-change-modal-input',
                        errors.oldPassword && 'input-error',
                        formState.dirtyFields.oldPassword && formState.touched.oldPassword && !errors.oldPassword && 'input-success'
                      )
                    }
                    type="password"
                    value={ oldPassword }
                    onChange={ (e: { target: { value: any } }) => handleOldPasswordChange(e.target.value) }
                    placeholder="Введите ваш пароль"
                    onFocus={ handleFocus }
                    onBlur={ handleBlur }
                  />
                  {
                    !formState.isValid && errors.oldPassword && formState.dirtyFields.oldPassword && (
                      <div className="input-error-text">
                        { errors.oldPassword.type === 'minLength'
                          ? 'Слишком короткий пароль'
                          : ''
                        }
                      </div>
                    )
                  }
                </div>
                <div className="password-change-modal-input-block">
                  <div className="password-change-modal-title">
                    2FA Код
                  </div>
                  <input
                    name="faCode"
                    ref={
                      register({
                        required: true
                      })
                    }
                    className={
                      classNames(
                        'password-change-modal-input',
                        errors.faCode && 'input-error',
                        formState.dirtyFields.faCode && formState.touched.faCode && !errors.faCode && 'input-success'
                      )
                    }
                    type="text"
                    value={ FACode }
                    onChange={ (e: { target: { value: any } }) => handleFACodeChange(e.target.value) }
                    placeholder="Введите ваш 2FA Код"
                    onFocus={ handleFocus }
                    onBlur={ handleBlur }
                  />
                  {
                    !formState.isValid && errors.faCode && formState.touched.faCode && (
                      <div className="input-error-text">
                        { errors.faCode && 'Введите код' }
                      </div>
                    )
                  }
                </div>
              </div>
              <div className="d-flex">
                <div className="password-change-modal-input-block">
                  <div className="password-change-modal-title">
                    Новый пароль
                  </div>
                  <input
                    name="newPassword"
                    ref={
                      register({
                        required: true,
                        minLength: 8,
                        maxLength: 24,
                      })
                    }
                    className={
                      classNames(
                        'password-change-modal-input',
                        errors.newPassword && 'input-error',
                        formState.dirtyFields.newPassword && formState.touched.newPassword && !errors.newPassword && 'input-success'
                      )
                    }
                    type="password"
                    value={ newPassword }
                    onChange={ (e: { target: { value: any } }) => handleNewPasswordChange(e.target.value) }
                    placeholder="Введите новый пароль"
                    onFocus={ handleFocus }
                    onBlur={ handleBlur }
                  />
                  {
                    !formState.isValid && errors.newPassword && formState.dirtyFields.newPassword && (
                      <div className="input-error-text">
                        { errors.newPassword.type === 'minLength'
                          ? 'Слишком короткий пароль'
                          : errors.newPassword.type === 'maxLength'
                          && 'Слишком длинный пароль'
                        }
                      </div>
                    )
                  }
                </div>
                <div className="password-change-modal-input-block">
                  <div className="password-change-modal-title">
                    Повторите новый пароль
                  </div>
                  <input
                    name="repeatPassword"
                    ref={
                      register({
                        required: true,
                        minLength: 8,
                        maxLength: 24,
                      })
                    }
                    className={
                      classNames(
                        'password-change-modal-input',
                        errors.repeatPassword && 'input-error',
                        formState.dirtyFields.repeatPassword && formState.touched.repeatPassword && !errors.repeatPassword && 'input-success'
                      )
                    }
                    type="password"
                    value={ repeatPassword }
                    onChange={ (e: { target: { value: any } }) => handleRepeatPasswordChange(e.target.value) }
                    placeholder="Введите новый пароль"
                    onFocus={ handleFocus }
                    onBlur={ handleBlur }
                  />
                  {
                    !formState.isValid && errors.repeatPassword && formState.dirtyFields.repeatPassword && (
                      <div className="input-error-text">
                        { errors.repeatPassword.type === 'minLength'
                          ? 'Слишком короткий пароль'
                          : errors.repeatPassword.type === 'maxLength'
                          && 'Слишком длинный пароль'
                        }
                      </div>
                    )
                  }
                </div>
              </div>
            </div>
            <div className="modal-buttons">
              <Button
                size="normal"
                color="secondary"
                onClick={ close }
              >
                Отмена
              </Button>
              <Button
                size="normal"
                color="primary"
                btnType="submit"
                disabled={ !formState.isValid }
                classes={
                  classNames(
                    'modal-button-submit',
                    !formState.isValid && 'disabled'
                  )
                }
              >
                Изменить
              </Button>
            </div>
          </form>
        </div>
      </div>
    ) : null,
    modalElement
  )
}

export default forwardRef(PasswordChangeModal)