import React, { useRef } from 'react'
import { ReactComponent as ExpandIcon } from '../../../../assets/icons/expand.svg'
import ItemImageModal from '../ItemImageModal/ItemImageModal'
import './ItemImage.scss'

interface iItemImage {
  img: string
}

export const ItemImage = ({ img }: iItemImage) => {
  const imageModalRef = useRef<any>(null)
  
  return (
    <div className="item-image-slider-image">
      <ItemImageModal defaultOpened={ false } ref={ imageModalRef } image={ img }/>
      <div
        className="cover"
        onClick={ () => imageModalRef.current.open() }
      >
        <ExpandIcon/>
      </div>
      <img
        src={ img }
        alt=""
      />
    </div>
  )
}