import React, { useEffect, useState } from 'react'
import { Button } from '../Controls/Controls'
import { AmountSuggestionInterface } from './AmountSuggestion.interface'
import './AmountSuggestion.scss'

export const AmountSuggestion = ({ suggestions, onSelect, active, featuredAmount }: AmountSuggestionInterface) => {
  const [activeIndex, setActiveIndex] = useState(suggestions.map((item: number) => item === featuredAmount).indexOf(true))
  const [firstClick, setFirstClick] = useState<boolean>(false)
  
  useEffect(() => {
    if (!active) setActiveIndex(-1)
    if (active && !firstClick) setActiveIndex(-1)
  }, [active, firstClick])
  
  const onClick = (index: number) => {
    const selectedService = suggestions[activeIndex]
    setActiveIndex(index)
    setFirstClick(true)
    
    if (suggestions[index] === selectedService) {
      setActiveIndex(-1)
    } else {
      onSelect(suggestions[index])
    }
  }
  
  return (
    <>
      {
        suggestions.map((suggestion: number, index: number) => {
          return (
            <Button
              size="large"
              color={ activeIndex === index ? 'primary' : 'secondary' }
              classes="balance-amount-button"
              key={ index }
              onClick={ () => onClick(index) }
            >
              $ { suggestion.toFixed(2) }
            </Button>
          )
        })
      }
    </>
  )
}