import classNames from 'classnames'
import React from 'react'
import { Collapse } from 'react-collapse'
import { ReactComponent as ArrowIcon } from '../../../../assets/icons/arrow.svg'

interface iSidebarFilterContainer {
  isCollapsed: boolean
  title: string
  children: any
  onReset: any
  onCollapse: any
  className?: string
}

export const SidebarFilterContainer = ({ isCollapsed, title, children, onReset, onCollapse, className }: iSidebarFilterContainer) => {
  return (
    <div className={
      classNames(
        'sidebar-filter-container',
        className
      )
    }
    >
      <div className="sidebar-filter-container-head">
        <div className="sidebar-filter-container-title">
          { title }
        </div>
        <div className="sidebar-filter-container-buttons">
          <div
            className="reset-filter-btn"
            onClick={ onReset }
          >
            Сбросить
          </div>
          <div
            className={
              classNames(
                'sidebar-filter-container-collapse-btn',
                isCollapsed && 'collapsed'
              )
            }
            onClick={ onCollapse }
          >
            <ArrowIcon/>
          </div>
        </div>
      </div>
      <Collapse isOpened={ isCollapsed }>
        { children }
      </Collapse>
    </div>
  )
}
