import React, { useState } from 'react'
import './Chat.scss'
import Moment from 'react-moment'
import { ReactComponent as CloseIcon, ReactComponent as RemoveIcon } from '../../../../assets/icons/close.svg'
import { ReactComponent as ImageIcon } from '../../../../assets/icons/image.svg'
import { ReactComponent as LockIcon } from '../../../../assets/icons/lock.svg'
import { ReactComponent as SendIcon } from '../../../../assets/icons/send.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import classNames from 'classnames'
import moment from 'moment'

interface iChat {
  createTime: any
  subject: string
  title: string
  status: string
  id: number
  files: any[]
  messages: any[]
  removeFileFromMessage: any
  sendMessage: any
  openImageModal: any
  handleCloseTicket: any
  showCloseModalButton: boolean
  handleCloseModal: any
}

const ChatInput = ({ value, onChange, onFocus, onBlur }: any) => {
  return (
    <input
      type="text"
      placeholder="Введите ваше сообщение"
      value={ value }
      onChange={ e => onChange(e) }
      onFocus={ onFocus }
      onBlur={ onBlur }
    />
  )
}

const Files = ({ files, removeFileFromMessage }: any) => {
  return (
    <div className="attached">
      { files && files.length > 0 && files.map((file: any) => {
        return (
          <div className="file" key={ Math.random() }>
            <img src={ URL.createObjectURL(file) } alt=""/>
            <span>{ file.name }</span>
            <div className="remove" onClick={ () => removeFileFromMessage(file) }><RemoveIcon/></div>
          </div>
        )
      }) }
    </div>
  )
}

export const Chat = (
  {
    handleCloseTicket,
    openImageModal,
    removeFileFromMessage,
    sendMessage,
    createTime,
    subject,
    title,
    status,
    id,
    files,
    messages,
    showCloseModalButton,
    handleCloseModal
  }: iChat) => {
  const [chatMessage, setChatMessage] = useState<string>('')
  const chatHandler = (e: any) => setChatMessage(e.target.value)
  
  const clearInput = () => {
    setChatMessage('')
  }
  
  const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
  const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')
  
  return (
    <>
      <div className="chat-head">
        <div className="info">
          <div className="top-info">
            <div className="creation-date">
              Тикет создан
              <span>
                <Moment unix format="DD.MM.YY">{ createTime }</Moment>
              </span>
            </div>
            <div className="top-info-divider">|</div>
            <div className="category">
              { subject }
            </div>
          </div>
          <div className="bottom-info">
            <div className="ticket-title">
              { title }
            </div>
          </div>
        </div>
        <div className="buttons">
          {
            status !== 'closed'
              ? <Button
                size="normal"
                color="secondary"
                classes={
                  classNames(
                    'close-ticket-button'
                  )
                }
                onClick={ () => handleCloseTicket(id) }
              >
                <LockIcon/>
                <span>Закрыть тикет</span>
              </Button>
              : <Button
                size="normal"
                color="secondary"
                square={ true }
                classes={
                  classNames(
                    'close-ticket-button',
                    'active'
                  )
                }
              >
                <LockIcon/>
              </Button>
          }
          {
            showCloseModalButton && (
              <Button
                size="normal"
                color="secondary"
                classes={
                  classNames(
                    'ticket-modal-close'
                  )
                }
                square
                onClick={ handleCloseModal }
              >
                <CloseIcon/>
              </Button>
            )
          }
        </div>
      </div>
      <div className="chat-body">
        <div className="chat-container">
          {
            messages && messages.map((msg: any) => {
              const { message, time, customer, files } = msg
              
              return (
                <div
                  className={
                    classNames(
                      'message',
                      customer === undefined ? 'close' : customer ? 'customer' : 'staff'
                    )
                  }
                  key={ `message-${ customer === undefined ? 'close' : customer ? 'customer' : 'staff' }-${ moment(time).format('HH:mm:ss') }` }
                >
                  {
                    message && message.length > 0 && (
                      <div className="body">
                        {
                          message.split('\n').map((msg: React.ReactNode, key: string | number | null | undefined) => (
                            <div key={ key }>{ msg }</div>
                          ))
                        }
                      </div>
                    )
                  }
                  {
                    (files && files.length > 0 && (
                      <div className="files">
                        {
                          files.map((file: any) => {
                            return (
                              <img src={ file || URL.createObjectURL(file) } alt=""/>
                            )
                          })
                        }
                      </div>
                    ))
                  }
                  <div className="date">
                    <Moment local unix format="D MMMM YYYY">
                      { time }
                    </Moment> в <Moment unix local format="HH:mm">
                    { time }
                  </Moment>
                  </div>
                </div>
              )
            })
          }
        </div>
        <Files files={ files } removeFileFromMessage={ removeFileFromMessage }/>
        <div className="input-button">
          {
            status === 'closed'
              ? <div className="input-closed">Данный чат закрыт. Если возникли новые вопросы, откройте новый тикет</div>
              : (
                <>
                  <ChatInput
                    onChange={ chatHandler }
                    value={ chatMessage }
                    onFocus={ handleFocusParent }
                    onBlur={ handleBlurParent }
                  />
                  <div className="buttons">
                    <div className="icon" onClick={ openImageModal }>
                      <ImageIcon/>
                    </div>
                    <Button
                      size="normal"
                      color="primary"
                      classes="send-button"
                      onClick={ () => {
                        sendMessage(chatMessage, files)
                        clearInput()
                      } }
                    >
                      <SendIcon/>
                    </Button>
                  </div>
                </>
              )
          }
        </div>
      </div>
    </>
  )
}