import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg'
import React from 'react'

interface iSearchInput {
  value: string
  onChange: any
  placeholder: string
}

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

export const SearchInput = ({ value, onChange, placeholder }: iSearchInput) => {
  
  return (
    <div className="sidebar-search-input">
      <SearchIcon/>
      <input
        type="text"
        placeholder={ placeholder }
        value={ value }
        onChange={ onChange }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}