import moment from 'moment'

export const getUnlockTime = (unlockDate: number) => {
  const today = moment.unix(Date.now() / 1000)
  const unlockAt = moment.unix(unlockDate)
  
  const duration = Math.ceil(moment.duration(unlockAt.diff(today)).asMinutes())
  
  const result = duration < 60
    ? { duration, unit: 'minutes' }
    : duration < 1440
      ? { duration: Math.ceil(duration / 60), unit: 'hours' }
      : { duration: Math.ceil(duration / 60 / 24), unit: 'days' }
  
  return result
}