import { TRangeTuple, useRangeSlider } from '@strv/react-sliders'
import React from 'react'
import { ReactComponent as ArrowSliderIcon } from '../../assets/icons/arrow-slider.svg'
import './RangeSlider.scss'

interface iRangeSlider {
  value: TRangeTuple
  onChange: any
}

export const RangeSlider = ({ value, onChange }: iRangeSlider) => {
  const { getRailProps, getTrackProps, getMinHandleProps, getMaxHandleProps } = useRangeSlider({
    value,
    min: 0,
    max: 100,
    onChange: onChange
  })
  
  return (
    <div className="range-slider">
      <span
        className="range-slider-rail"
        { ...getRailProps() }
      />
      <div className="range-slider-component">
        <div
          className="range-slider-track-box box-1"
          style={ {
            width: value[0] + '%'
          } }
        />
        <span className="range-slider-track" { ...getTrackProps() }/>
        <div
          className="range-slider-track-box box-2"
          style={ {
            width: 100 - value[1] + '%'
          } }
        />
        <span className="range-slider-handle handle-1"{ ...getMinHandleProps() }>
          <ArrowSliderIcon/>
        </span>
        <span className="range-slider-handle handle-2" { ...getMaxHandleProps() }>
          <ArrowSliderIcon/>
        </span>
      </div>
    </div>
  )
}