import i18next from 'i18next'
import React, { useRef, useState } from 'react'
import './LanguageSelector.scss'
import { useLanguage } from '../../../../newHooks/useLanguage'
import { Dropdown } from '../../../Controls/Dropdown/Dropdown'
import classNames from 'classnames'

export const LanguageSelector = () => {
  // const currentLanguage = i18next.language
  const availableLanguages = i18next.languages
  const { currentLanguage, changeLanguage } = useLanguage()
  
  return (
    <>
      <Dropdown
        classes={
          classNames(
            'language-selector'
          )
        }
      >
        <Dropdown.Toggle>
          <div className="language-flag">
            {
              currentLanguage === 'en-US'
                ? <img
                  src={ `https://raw.githubusercontent.com/hjnilsson/country-flags/master/png100px/us.png` }
                  alt=""/>
                : <img
                  src={ `https://raw.githubusercontent.com/hjnilsson/country-flags/master/png100px/${ currentLanguage === 'en' ? 'us' : currentLanguage }.png` }
                  alt=""/>
            }
          </div>
          <div className="language-title">
            {
              currentLanguage === 'en-US'
                ? 'us'
                : currentLanguage === 'en' ? 'us' : currentLanguage
            }
          </div>
          <div className="arrow"/>
        </Dropdown.Toggle>
        <Dropdown.Menu classes="languages">
          {
            availableLanguages?.map((language) => {
              return (
                <Dropdown.Item
                  key={ language }
                  classes="language"
                  onClick={ () => {
                    changeLanguage(language)
                    i18next.changeLanguage(language)
                  } }
                >
                  <div className="language-flag">
                    {
                      language === 'en-us'
                        ? <img
                          src={ `https://raw.githubusercontent.com/hjnilsson/country-flags/master/png100px/us.png` }
                          alt=""/>
                        : <img
                          src={ `https://raw.githubusercontent.com/hjnilsson/country-flags/master/png100px/${ language === 'en' ? 'us' : language }.png` }
                          alt=""/>
                    }
                  </div>
                  <div className="language-title">
                    { language === 'en' ? 'us' : language }
                  </div>
                </Dropdown.Item>
              )
            })
          }
        </Dropdown.Menu>
      </Dropdown>
    </>
  )
}