import classNames from 'classnames'
import moment from 'moment'
import React, { useEffect, useLayoutEffect, useState } from 'react'
import { ReactComponent as ArrowIcon } from '../../../../assets/icons/arrow.svg'
import { ReactComponent as TrashIcon } from '../../../../assets/icons/trash.svg'
import { UsePagination } from '../../../../hooks/UsePagination'
import './UserOrdersTable.scss'

interface iUserOrdersTable {
  data: any[]
  removeOrder: any
  size?: any
}

const Pagination = ({ count, page, onChange, neighbours, _DATA }: any) => {
  const prev = count !== 0 ? _DATA.currentPage !== 1 : false
  const next = count !== 0 ? _DATA.currentPage !== _DATA.maxPage : false
  
  const range = (from: number, to: number, step = 1) => {
    let i = from
    const range = []
    
    while (i <= to) {
      range.push(i)
      i += step
    }
    
    return range
  }
  
  const startPage = Math.max(1, page - neighbours)
  const endPage = Math.min(count, page + neighbours)
  
  const [pages, setPages] = useState<any>(range(startPage, endPage))
  
  const totalNumbers = (neighbours * 2) + 3
  const hasLeftSpill = startPage > 2
  const hasRightSpill = (totalNumbers - endPage) > 1
  const spillOffset = totalNumbers - (pages.length + 2)
  
  useLayoutEffect(() => {
    switch (true) {
      // handle: (1) < {5 6} [7] {8 9} (10)
      case (hasLeftSpill && !hasRightSpill): {
        const extraPages = range(startPage - spillOffset, startPage - 1)
        setPages([...extraPages, ...pages])
        break
      }
      
      // handle: (1) {2 3} [4] {5 6} > (10)
      case (!hasLeftSpill && hasRightSpill): {
        const extraPages = range(endPage + 1, endPage + spillOffset)
        setPages([...pages, ...extraPages])
        break
      }
      
      // handle: (1) < {4 5} [6] {7 8} > (10)
      case (hasLeftSpill && hasRightSpill):
      default: {
        setPages([...pages])
        break
      }
    }
  }, [])
  
  return (
    <div className="pagination">
      <div
        className={
          classNames(
            'pagination-item',
            'pagination-item-arrow',
            prev && 'pagination-item-arrow-active'
          )
        }
        onClick={ () => {
          prev && onChange(page - 1)
        } }
      >
        <ArrowIcon/>
      </div>
      <div className="pagination-item-info">
        Страница { page } из { count }
      </div>
      <div
        className={
          classNames(
            'pagination-item',
            'pagination-item-arrow',
            next && 'pagination-item-arrow-active'
          )
        }
        onClick={ () => {
          next && onChange(page + 1)
        } }
      >
        <ArrowIcon/>
      </div>
    </div>
  )
}

export const UserOrdersTable = ({ data, removeOrder, size }: iUserOrdersTable) => {
  const [page, setPage] = useState<number>(1)
  const PER_PAGE = 5
  const count = Math.ceil(data?.length / PER_PAGE)
  const _DATA = UsePagination(data, PER_PAGE)
  const handleChange = (p: any) => {
    if (count > 0 && p > 0) {
      setPage(p)
      _DATA.jump(p)
    }
  }
  
  useEffect(() => {
    if (count === 0) {
      setPage(0)
      _DATA.jump(0)
    }
    
    if (page === 0 && data?.length > 0) {
      setPage(1)
      _DATA.jump(1)
    }
  }, [_DATA, count, data, page])
  
  switch (size) {
    case 'mobile': {
      return <>
        <div className="orders-history-transactions mobile">
          {
            _DATA.currentData()
              ? (
                <div className="table">
                  {
                    _DATA.currentData().map((item: any) => {
                      return (
                        <div className="orders-history-transactions-card mobile">
                          <div className="block">
                            <div className="item-img item-block">
                              <img src={ item.img } alt=""/>
                            </div>
                            <div className="item-info item-block">
                              <div className="item-item">{ item.game === 'CS:GO' ? item.item : item.character }</div>
                              <div className="item-name">{ item.game === 'CS:GO' ? item.name : item.item }</div>
                              <div className="item-game">{ item.game }</div>
                            </div>
                          </div>
                          <div className="block">
                            <div className="item-info-price item-block">
                              <div className="item-price"><span>Цена: </span> ${ item.price }</div>
                              <div className="item-quantity"><span>Кол-во: </span> { item.quantity }</div>
                              <div className="item-id"><span>ID: </span> ${ item.id }</div>
                            </div>
                            <div className="item-order-info item-block">
                              {
                                (!item.date && (
                                  <>
                                    <div className="item-chance"><span>Шанс: </span> { item.chance }%</div>
                                    <div className="item-position"><span>#: </span> { item.position }</div>
                                  </>
                                ))
                              }
                              {
                                (item.date && (
                                    <div className="date">
                                      { moment.unix(item.date).format('DD.MM.YY HH:mm') }
                                    </div>)
                                )
                              }
                            </div>
                          </div>
                          {
                            (!item.date && <div className="item-action item-block" onClick={ () => removeOrder(item.id) }>
                              <TrashIcon/>
                            </div>)
                          }
                        </div>
                      )
                    })
                  }
                </div>
              )
              : (
                <div className="empty">Нет заявок</div>
              )
          }
        </div>
        <Pagination
          count={ count }
          page={ page }
          onChange={ handleChange }
          neighbours={ data?.length > 15 ? 5 : 1 }
          _DATA={ _DATA }
        /></>
    }
    
    case 'tablet': {
      return <>
        <div className="orders-history-transactions tablet">
          {
            _DATA.currentData()
              ? (
                <div className="table">
                  {
                    _DATA.currentData().map((item: any) => {
                      return (
                        <div className="orders-history-transactions-card">
                          <div className="item-img item-block">
                            <img src={ item.img } alt=""/>
                          </div>
                          <div className="item-info item-block">
                            <div className="item-item">{ item.game === 'CS:GO' ? item.item : item.character }</div>
                            <div className="item-name">{ item.game === 'CS:GO' ? item.name : item.item }</div>
                            <div className="item-game">{ item.game }</div>
                          </div>
                          <div className="item-info-price item-block">
                            <div className="item-price"><span>Цена: </span> ${ item.price }</div>
                            <div className="item-quantity"><span>Кол-во: </span> { item.quantity }</div>
                            <div className="item-id"><span>ID: </span> ${ item.id }</div>
                          </div>
                          <div className="item-order-info item-block">
                            <div className="item-chance"><span>Шанс: </span> { item.chance }%</div>
                            <div className="item-position"><span>Позиция: </span> { item.position }</div>
                          </div>
                          <div className="item-action item-block">
                            {
                              item.date
                                ? (
                                  <div className="date">
                                    <span>
                                      { moment.unix(item.date).format('DD.MM.YY') }
                                    </span>
                                    <span>
                                      { moment.unix(item.date).format('HH:mm') }
                                    </span>
                                  </div>
                                )
                                : (
                                  <div className="trash-icon" onClick={ () => removeOrder(item.id) }>
                                    <TrashIcon/>
                                  </div>
                                )
                            }
                          </div>
                        </div>
                      )
                    })
                  }
                </div>
              )
              : (
                <div className="empty">Нет заявок</div>
              )
          }
        </div>
        <Pagination
          count={ count }
          page={ page }
          onChange={ handleChange }
          neighbours={ data?.length > 15 ? 5 : 1 }
          _DATA={ _DATA }
        /></>
    }
    
    case 'laptop': {
      return <>
        <div className="orders-history-transactions laptop">
          {
            (data && data.length > 0)
              ? (
                <table>
                  <thead>
                  <tr>
                    <th>
                    
                    </th>
                    <th style={ { textAlign: 'left' } }>
                      Название предмета
                    </th>
                    <th>
                      Игра
                    </th>
                    <th>
                      ID
                    </th>
                    <th>
                      Стоимость
                    </th>
                    <th>
                      Кол-во
                    </th>
                    <th>
                      Позиция
                    </th>
                    <th>
                      Шанс
                    </th>
                    <th>
                    
                    </th>
                  </tr>
                  </thead>
                  <tbody>
                  {
                    _DATA.currentData() && _DATA.currentData().map((item: any) => {
                      return (
                        <tr>
                          <td>
                            <img src={ item.img } alt=""/>
                          </td>
                          {
                            item.game === 'CS:GO'
                              ? (
                                <td style={ { textAlign: 'left' } }>
                                  <span>{ item.item } ({ item.exterior })</span>
                                  { item.name }
                                </td>
                              )
                              : (
                                <td style={ { textAlign: 'left' } }>
                                  <span>{ item.character } ({ item.rarity })</span>
                                  { item.item }
                                </td>
                              )
                          }
                          <td>
                            { item.game }
                          </td>
                          <td>
                            { item.id }
                          </td>
                          <td>
                            ${ item.price }
                          </td>
                          <td>{ item.quantity }</td>
                          <td>{ item.position }</td>
                          <td>{ item.chance }</td>
                          {
                            item.date
                              ? <td>{ moment.unix(item.date).format('DD.MM.YY HH:mm') }</td>
                              : (
                                <td className="trash-icon" onClick={ () => removeOrder(item.id) }>
                                  <TrashIcon/>
                                </td>
                              )
                          }
                        </tr>
                      )
                    })
                  }
                  </tbody>
                </table>
              )
              : (
                <div className="empty">Нет заявок</div>
              )
          }
        </div>
        <Pagination
          count={ count }
          page={ page }
          onChange={ handleChange }
          neighbours={ data?.length > 15 ? 5 : 1 }
          _DATA={ _DATA }
        /></>
    }
    
    default: {
      return <div className="orders-history-transactions">
        {
          (data && data.length > 0)
            ? (
              <table>
                <thead>
                <tr>
                  <th>
                  
                  </th>
                  <th style={ { textAlign: 'left' } }>
                    Название предмета
                  </th>
                  <th>
                    Игра
                  </th>
                  <th>
                    ID
                  </th>
                  <th>
                    Стоимость
                  </th>
                  <th>
                    Кол-во
                  </th>
                  <th>
                    Позиция
                  </th>
                  <th>
                    Шанс
                  </th>
                  <th>
                  
                  </th>
                </tr>
                </thead>
                <tbody>
                {
                  _DATA.currentData() && _DATA.currentData().map((item: any) => {
                    return (
                      <tr>
                        <td>
                          <img src={ item.img } alt=""/>
                        </td>
                        {
                          item.game === 'CS:GO'
                            ? (
                              <td style={ { textAlign: 'left' } }>
                                <span>{ item.item } ({ item.exterior })</span>
                                { item.name }
                              </td>
                            )
                            : (
                              <td style={ { textAlign: 'left' } }>
                                <span>{ item.character } ({ item.rarity })</span>
                                { item.item }
                              </td>
                            )
                        }
                        <td>
                          { item.game }
                        </td>
                        <td>
                          { item.id }
                        </td>
                        <td>
                          ${ item.price }
                        </td>
                        <td>{ item.quantity }</td>
                        <td>{ item.position }</td>
                        <td>{ item.chance }</td>
                        {
                          item.date
                            ? <td>{ moment.unix(item.date).format('DD.MM.YY HH:mm') }</td>
                            : (
                              <td className="trash-icon" onClick={ () => removeOrder(item.id) }>
                                <TrashIcon/>
                              </td>
                            )
                        }
                      </tr>
                    )
                  })
                }
                </tbody>
              </table>
            )
            : (
              <div className="empty">Нет заявок</div>
            )
        }
        <Pagination
          count={ count }
          page={ page }
          onChange={ handleChange }
          neighbours={ data?.length > 15 ? 5 : 1 }
          _DATA={ _DATA }
        />
      </div>
    }
  }
}