import classNames from 'classnames'
import { observer } from 'mobx-react-lite'
import React, { useRef, useState } from 'react'
import { useDetectOutsideClick } from '../../hooks/useDetectOutsideClick'
import { UseGame } from '../../newHooks/UseGame'
import { useGameIcon } from '../../newHooks/useGameIcon'
import './GameSelector.scss'

export const GameSelector = observer(
  () => {
    const gameSelectorRef = useRef<any>(null)
    const [isOpened, setIsOpened] = useState<boolean>(false)
    const handleClickOutside = () => setIsOpened(false)
    useDetectOutsideClick(gameSelectorRef, handleClickOutside)
    
    const { game, options, changeGame } = UseGame()
    
    return (
      <div
        className="game-selector"
        ref={ gameSelectorRef }
      >
        <div
          className="game-selector--selected-game"
          onClick={ () => setIsOpened(!isOpened) }
        >
          { useGameIcon(game) }
        </div>
        {
          isOpened && (
            <div className="game-selector--games">
              {
                options.map((option: string) => {
                  const isSelected = game === option
                  
                  return (
                    <div
                      className={
                        classNames(
                          'game-selector--game',
                          isSelected && 'selected'
                        )
                      }
                      key={ option }
                      onClick={ () => {
                        changeGame(option)
                        setIsOpened(false)
                      } }
                    >
                      { useGameIcon(option) }
                    </div>
                  )
                })
              }
            </div>
          )
        }
      </div>
    )
  }
)