import React, { useContext, useLayoutEffect, useState } from 'react'
import './Select.scss'
import classNames from 'classnames'
import { SelectContext } from './Select.context'
import {
  SelectDividerInterface,
  SelectInputInterface,
  SelectInterface,
  SelectItemInterface,
  SelectMenuInterface,
  SelectToggleInterface
} from './Select.interface'
import { useClickOutside } from 'react-click-outside-hook'

const useSelectContext = () => {
  const context = useContext(SelectContext)
  
  if (!context) throw new Error('SelectContext used outside Select component')
  
  return context
}

export const Select = ({classes, relative, children, onClick, show = false, ...restProps}: SelectInterface) => {
  const [isToggle, setIsToggle] = useState(show)
  const [ref, hasClickedOutside] = useClickOutside()
  
  useLayoutEffect(() => {
    if (hasClickedOutside) setIsToggle(false)
  }, [hasClickedOutside])
  
  return (
    <SelectContext.Provider value={ {isToggle, setIsToggle} }>
      <div
        ref={ ref }
        onClick={ onClick }
        className={
          classNames('select', classes,
            {'toggle': isToggle},
            {'relative': relative})
        }
        { ...restProps }
      >
        { children }
      </div>
    </SelectContext.Provider>
  )
}

const SelectToggle = ({classes, children, withInput = false, ...restProps}: SelectToggleInterface) => {
  const {isToggle, setIsToggle} = useSelectContext()
  
  return (
    <div
      className={
        classNames('select-toggle', classes)
      }
      onClick={ () => setIsToggle(!isToggle) }
      { ...restProps }
    >
      { children }
    </div>
  )
}

const SelectMenu = ({classes, children, ...restProps}: SelectMenuInterface) => {
  return (
    <div
      className={ classNames('select-menu', classes) }
      { ...restProps }
    >
      { children }
    </div>
  )
}

const SelectInput = ({placeholder, classes, size, color}: SelectInputInterface) => {
  return (
    <input
      className={ classNames(
        'select-input',
        size ? `select-input-${ size ? size : '' }` : '',
        color ? `select-input-${ color ? color : '' }` : '',
        classes
      ) }
      type="text"
      placeholder={ placeholder }
      color={ color }
    />
  )
}

const SelectItem = ({classes, onClick,  children, ...restProps}: SelectItemInterface) => {
  const {isToggle, setIsToggle} = useSelectContext()
  
  const onClickFn = () => {
    if (onClick) onClick()
    // setIsToggle(!isToggle)
  }
  
  return (
    <div
      className={ classNames('select-item', classes) }
      onClick={ onClickFn }
      { ...restProps }>
      { children }
    </div>
  )
}

const SelectDivider = ({classes, ...restProps}: SelectDividerInterface) => {
  return (
    <div className={ classNames('select-divider', classes) } { ...restProps }/>
  )
}

Select.Toggle = SelectToggle
Select.Input = SelectInput
Select.Menu = SelectMenu
Select.Item = SelectItem
Select.Divider = SelectDivider