import React from 'react'
import './ItemImages.scss'
import { ItemImage } from '../ItemImage/ItemImage'

interface iItemImages {
  images: string[]
}

export const ItemImages = ({ images }: iItemImages) => {
  return (
    <div className="item-images-slider">
      {
        (!images && ('Скриншоты предмета отсутствуют'))
      }
      {
        images.map((image) => (
          <ItemImage img={ image }/>
        ))
      }
    </div>
  )
}