import classNames from 'classnames'
import React, { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react'
import { createPortal } from 'react-dom'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useHistory } from 'react-router-dom'
import { toast } from 'react-toastify'
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close.svg'
import { ReactComponent as SecurityIcon } from '../../../../assets/icons/lock.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import './CodeModal.scss'

interface iCodeModal {
  defaultOpened?: boolean
  status: boolean
}

interface DeactivateFormValues {
  password: string
  code: string | number
}

interface ActivateFormValues {
  password: string
  code: string | number
}

interface iModalInput {
  value: string
  onChange: any
  placeholder: string
  ref: any
  name: string
  type: 'text' | 'password'
  className?: string
  classNameError?: string
  classNameSuccess?: string
}

const ModalInput = ({ value, onChange, placeholder, name, ref, type, className, classNameError, classNameSuccess }: iModalInput) => {
  return (
    <input
      name={ name }
      type={ type }
      placeholder={ placeholder }
      value={ value }
      onChange={ onChange }
      ref={ ref }
      className={
        classNames(
          className,
          classNameError,
          classNameSuccess
        )
      }
      onFocus={ (e) => e.target.classList.add('input-focus') }
      onBlur={ (e) => e.target.classList.remove('input-focus') }
    />
  )
}

export const CodeModal = (
  {
    defaultOpened = false,
    status
  }: iCodeModal,
  ref: any
) => {
  const modalElement: any = document.getElementById('modal-root')
  
  const modalBlockRef = useRef<any>(null)
  
  const [isOpen, setOpen] = useState<boolean>(defaultOpened)
  
  const close = useCallback(() => setOpen(false), [])
  
  const handleClickOutside = () => close()
  
  const handleEscape = useCallback((event) => {
    if (event.keyCode === 27) close()
  }, [close])
  
  useDetectOutsideClick(modalBlockRef, handleClickOutside)
  
  useImperativeHandle(ref, () => ({
    open: () => setOpen(true),
    close
  }), [close])
  
  useEffect(() => {
    if (isOpen) document.addEventListener('keydown', handleEscape, false)
    
    return (
      document.removeEventListener('keydown', handleEscape, false)
    )
  }, [handleEscape, isOpen])
  
  const { register, handleSubmit, errors, formState } = useForm({
    mode: 'onChange',
    reValidateMode: 'onChange',
    defaultValues: {
      activation_password: '',
      activation_code: '',
      deactivation_password: '',
      deactivation_code: ''
    },
    criteriaMode: 'all',
    shouldFocusError: true,
    shouldUnregister: true
  })
  
  const history = useHistory()
  
  const deactivate: SubmitHandler<DeactivateFormValues> = (data) => {
    console.log(data)
    localStorage.setItem('securityStatus', JSON.stringify(false))
    toast.success('2FA успешно отключена', { autoClose: 5000 })
  }
  
  const activate: SubmitHandler<ActivateFormValues> = (data) => {
    console.log(data)
    localStorage.setItem('securityStatus', JSON.stringify(true))
    toast.success('2FA успешно подключена', { autoClose: 5000 })
  }
  
  const handleFocus = (e: any) => e.target.classList.add('input-focus')
  const handleBlur = (e: any) => e.target.classList.remove('input-focus')
  
  const [deactivationForm, setDeactivationForm] = useState<any>({
    password: '',
    code: ''
  })
  
  const [activationForm, setActivationForm] = useState<any>({
    password: '',
    code: ''
  })
  
  const [activationCode, setActivationCode] = useState<string | number>('')
  
  return createPortal(
    isOpen ? (
      <div
        className={
          classNames(
            'code-modal-wrapper',
            isOpen && 'active'
          )
        }
      >
        <div
          className={
            classNames(
              'modal-block'
            )
          }
          ref={ modalBlockRef }
        >
          <div
            className="close-button"
            onClick={ close }
          >
            <CloseIcon/>
          </div>
          {
            status
              ? (
                <form onSubmit={ handleSubmit(deactivate) } className="modal-body">
                  <div className="modal-icon deactivation">
                    <SecurityIcon/>
                  </div>
                  <div className="modal-title">
                    Отключение 2FA защиты
                  </div>
                  <div className="modal-inputs">
                    <div className="d-flex">
                      <div className="modal-input-block">
                        <div className="modal-input-block--title">
                          Ваш пароль
                        </div>
                        <input
                          name="deactivation_password"
                          ref={
                            register({
                              required: true,
                              minLength: 8,
                              maxLength: 24,
                            })
                          }
                          className={
                            classNames(
                              'modal-input-block--input',
                              errors.deactivation_password && 'input-error',
                              formState.dirtyFields.deactivation_password && formState.touched.deactivation_password && !errors.deactivation_password && 'input-success'
                            )
                          }
                          type="password"
                          value={ deactivationForm.password }
                          onChange={ (e: { target: { value: any } }) => setDeactivationForm({ ...deactivationForm, password: e.target.value }) }
                          placeholder="Введите ваш пароль"
                          onFocus={ handleFocus }
                          onBlur={ handleBlur }
                        />
                        {
                          !formState.isValid && errors.deactivation_password && formState.dirtyFields.deactivation_password && (
                            <div className="input-error-text">
                              { errors.deactivation_password.type === 'minLength'
                                ? 'Слишком короткий пароль'
                                : errors.deactivation_password.type === 'maxLength'
                                && 'Слишком длинный пароль'
                              }
                            </div>
                          )
                        }
                      </div>
                      <div className="modal-input-block">
                        <div className="modal-input-block--title">
                          2FA код
                        </div>
                        <input
                          name="deactivation_code"
                          ref={
                            register({
                              required: true,
                              minLength: 6,
                              maxLength: 6,
                            })
                          }
                          className={
                            classNames(
                              'modal-input-block--input',
                              errors.deactivation_code && 'input-error',
                              formState.dirtyFields.deactivation_code && formState.touched.deactivation_code && !errors.deactivation_code && 'input-success'
                            )
                          }
                          type="text"
                          value={ deactivationForm.code }
                          onChange={ (e: { target: { value: any } }) => setDeactivationForm({ ...deactivationForm, code: e.target.value }) }
                          placeholder="Введите 2FA код"
                          onFocus={ handleFocus }
                          onBlur={ handleBlur }
                        />
                        {
                          !formState.isValid && errors.deactivation_code && formState.dirtyFields.deactivation_code && (
                            <div className="input-error-text">
                              { errors.deactivation_code.type === 'minLength'
                                ? 'Слишком короткий пароль'
                                : errors.deactivation_code.type === 'maxLength'
                                && 'Слишком длинный пароль'
                              }
                            </div>
                          )
                        }
                      </div>
                    </div>
                  </div>
                  <div className="modal-buttons">
                    <Button
                      size="normal"
                      color="secondary"
                      onClick={ close }
                    >
                      Закрыть окно
                    </Button>
                    <Button
                      size="normal"
                      color={ status ? 'danger' : 'primary' }
                      btnType="submit"
                      disabled={ !formState.isValid }
                      classes={
                        classNames(
                          'modal-button-submit',
                          status ? 'modal-button-disable' : 'modal-button-activate',
                          !formState.isValid && 'disabled'
                        )
                      }
                    >
                      {
                        status ? 'Отключить Защиту' : 'Активировать защиту'
                      }
                    </Button>
                  </div>
                </form>
              )
              : (
                <form onSubmit={ handleSubmit(activate) } className="modal-body">
                  <div className="modal-icon activation">
                    <SecurityIcon/>
                  </div>
                  <div className="modal-title">
                    Активация 2FA
                  </div>
                  <div className="modal-inputs">
                    <div className="d-flex">
                      <div className="modal-input-block">
                        <div className="modal-input-block--title">
                          Ваш пароль
                        </div>
                        <input
                          name="activation_password"
                          ref={
                            register({
                              required: true,
                              minLength: 8,
                              maxLength: 24,
                            })
                          }
                          className={
                            classNames(
                              'modal-input-block--input',
                              errors.activation_password && 'input-error',
                              formState.dirtyFields.activation_password && formState.touched.activation_password && !errors.activation_password && 'input-success'
                            )
                          }
                          type="password"
                          value={ activationForm.password }
                          onChange={ (e: { target: { value: any } }) => setActivationForm({ ...activationForm, password: e.target.value }) }
                          placeholder="Введите ваш пароль"
                          onFocus={ handleFocus }
                          onBlur={ handleBlur }
                        />
                        {
                          !formState.isValid && errors.activation_password && formState.dirtyFields.activation_password && (
                            <div className="input-error-text">
                              { errors.activation_password.type === 'minLength'
                                ? 'Слишком короткий пароль'
                                : errors.activation_password.type === 'maxLength'
                                && 'Слишком длинный пароль'
                              }
                            </div>
                          )
                        }
                      </div>
                      <div className="modal-input-block">
                        <div className="modal-input-block--title">
                          2FA код
                        </div>
                        <input
                          name="activation_code"
                          ref={
                            register({
                              required: true,
                              minLength: 6,
                              maxLength: 6,
                            })
                          }
                          className={
                            classNames(
                              'modal-input-block--input',
                              errors.activation_code && 'input-error',
                              formState.dirtyFields.activation_code && formState.touched.activation_code && !errors.activation_code && 'input-success'
                            )
                          }
                          type="text"
                          value={ activationForm.code }
                          onChange={ (e: { target: { value: any } }) => setActivationForm({ ...activationForm, code: e.target.value }) }
                          placeholder="Введите 2FA код"
                          onFocus={ handleFocus }
                          onBlur={ handleBlur }
                        />
                        {
                          !formState.isValid && errors.activation_code && formState.dirtyFields.activation_code && (
                            <div className="input-error-text">
                              { errors.activation_code.type === 'required'
                              && 'Введите 2FA код'
                              }
                            </div>
                          )
                        }
                      </div>
                    </div>
                  </div>
                  <div className="modal-buttons">
                    <Button
                      size="normal"
                      color="secondary"
                      onClick={ close }
                    >
                      Закрыть окно
                    </Button>
                    <Button
                      size="normal"
                      color={ status ? 'danger' : 'primary' }
                      btnType="submit"
                      disabled={ !formState.isValid }
                      classes={
                        classNames(
                          'modal-button-submit',
                          status ? 'modal-button-disable' : 'modal-button-activate',
                          !formState.isValid && 'disabled'
                        )
                      }
                    >
                      {
                        status ? 'Отключить Защиту' : 'Активировать защиту'
                      }
                    </Button>
                  </div>
                </form>
              )
          }
        </div>
      </div>
    ) : null,
    modalElement
  )
}

export default forwardRef(CodeModal)