import React, { useState } from 'react'
import { SidebarFilterContainer } from '../SidebarFilterContainer/SidebarFilterContainer'
import './ExteriorFilter.scss'
import classNames from 'classnames'
import NumberFormat from 'react-number-format'
import InputRange from 'react-input-range'
import { RangeSlider } from '../../../../newComponents/RangeSlider/RangeSlider'

interface iExteriorFilter {
  isCollapsed: boolean
  onCollapse: any
  onReset: any
  values: {
    fn: true | false | 'indeterminate'
    mw: true | false | 'indeterminate'
    ft: true | false | 'indeterminate'
    ww: true | false | 'indeterminate'
    bs: true | false | 'indeterminate'
    none: true | false | 'indeterminate'
  }
  min: number
  max: number
  onExteriorChange: any
  onMinChange: any
  onMaxChange: any
  onRangeChange: any
}

interface iCheckbox {
  title: string
  name: string
  checked: boolean
  indeterminate?: boolean
  className?: string
  style?: React.CSSProperties
  disabled?: boolean
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void
}

interface iExteriorInput {
  rules: any
  prepend: string
  value: number
  onChange: any
  errors: any
}

const Checkbox = ({ title, name, checked, indeterminate = false, disabled = false, onChange }: iCheckbox) => {
  return (
    <label>
      <input
        name={ name }
        className="checkbox"
        type="checkbox"
        ref={ input => {
          if (input) {
            input.checked = checked
            input.indeterminate = indeterminate as boolean
            input.disabled = disabled as boolean
          }
        } }
        onChange={ onChange }
      />
      <span>{ title }</span>
    </label>
  )
}

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

const ExteriorInput = ({ rules, value, onChange, prepend, errors }: iExteriorInput) => {
  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'exterior-input-group'
        )
      }
    >
      <div className="exterior-input-prepend">
        { prepend }
      </div>
      <NumberFormat
        className="exterior-input"
        isAllowed={ rules }
        isNumericString={ true }
        allowNegative={ false }
        allowLeadingZeros={ false }
        decimalScale={ 2 }
        fixedDecimalScale={ true }
        allowEmptyFormatting={ false }
        thousandSeparator=" "
        value={ value }
        onValueChange={ (number) => onChange(number.value) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}

export const ExteriorFilter = (
  {
    isCollapsed,
    onCollapse,
    onReset,
    values,
    onExteriorChange,
    min,
    max,
    onMinChange,
    onMaxChange,
    onRangeChange
  }: iExteriorFilter) => {
  const MIN_LIMIT: number = 0
  const MAX_LIMIT: number = 100
  
  const [minErrors, setMinErrors] = useState<boolean>(false)
  const [maxErrors, setMaxErrors] = useState<boolean>(false)
  
  const minInputValueLimit = (inputObj: { floatValue: number }) => {
    const { floatValue } = inputObj
    
    if (floatValue >= MIN_LIMIT && floatValue <= MAX_LIMIT && floatValue < max) {
      setMinErrors(false)
      return inputObj
    } else {
      setMinErrors(true)
      return inputObj
    }
  }
  
  const maxInputValueLimit = (inputObj: { floatValue: number }) => {
    const { floatValue } = inputObj
    
    if (floatValue >= MIN_LIMIT && floatValue <= MAX_LIMIT && floatValue > min) {
      setMaxErrors(false)
      return inputObj
    } else {
      setMaxErrors(true)
      return inputObj
    }
  }
  
  return (
    <SidebarFilterContainer
      title="Экстерьер"
      isCollapsed={ isCollapsed }
      onCollapse={ onCollapse }
      onReset={ onReset }
      className="exterior-filter"
    >
      <div className="checkboxes">
        {
          Object.entries(values).map((item) => {
            return (
              <Checkbox
                title={ item[0] }
                name={ `checkbox-${ item[0] }` }
                checked={ item[1] === true }
                indeterminate={ item[1] === 'indeterminate' }
                onChange={ () => onExteriorChange(item[0], item[1]) }
              />
            )
          })
        }
      </div>
      <div className="exterior-range">
        <RangeSlider
          value={ [min * 100, max * 100] }
          onChange={ (data: number[]) => {
            const min = data[0]
            const max = data[1]
            
            onRangeChange({ min: min / 100, max: max / 100 })
          } }
        />
      </div>
      <div className="exterior-inputs">
        <ExteriorInput
          value={ min }
          onChange={ onMinChange }
          prepend={ 'От' }
          errors={ minErrors }
          rules={ minInputValueLimit }
        />
        <ExteriorInput
          value={ max }
          onChange={ onMaxChange }
          prepend={ 'До' }
          errors={ maxErrors }
          rules={ maxInputValueLimit }
        />
      </div>
    </SidebarFilterContainer>
  )
}