import React from 'react'
import { useTranslation } from 'react-i18next'
import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg'
import { GameSelector } from '../../../../newComponents/GameSelector/GameSelector'
import { Checkbox } from '../../../Controls/Checkbox/Checkbox'
import { Button, Input } from '../../../Controls/Controls'
import { Dropdown } from '../../../Controls/Dropdown/Dropdown'
import { Select } from '../../../Controls/Select/Select'
import { Switch } from '../../../Controls/Switch/Switch'
import './AdvancedSearch.scss'

export const AdvancedSearch = () => {
  const { t } = useTranslation()
  
  return (
    <Dropdown classes="advanced-search-button">
      <Dropdown.Toggle>
        <Button size="medium" color="secondary">
          { t('header.filter.advanced-search') }
          <span className="arrow"/>
        </Button>
      </Dropdown.Toggle>
      <Dropdown.Menu>
        <Dropdown.Item noToggleOnClick={ true } classes="advanced-search-filters">
          <div className="filters">
            <div className="row content-between">
              <Input
                classes="advanced-search"
                size="normal"
              >
                <Input.Prepend>
                  <SearchIcon/>
                </Input.Prepend>
                <Input.Field
                  placeholder={ t('header.filter.search-placeholder') + ' CS:GO' }
                />
              </Input>
              <GameSelector/>
            </div>
            <div className="row content-between input-cols sm-col">
              <div className="col">
                <div className="advanced-filter-title">
                  { t('filters.advanced-filters.price.title') }
                </div>
                <div className="advanced-filter-inputs">
                  <Input
                    id="min-price"
                    classes="advanced-filter-input"
                    size="normal"
                  >
                    <Input.Prepend>
                    <span>
                      { t('filters.advanced-filters.price.min') }
                      <span className="currency">$</span>
                    </span>
                    </Input.Prepend>
                    <Input.Field
                      type="number"
                      step={ 0.25 }
                      placeholder={ '0.00' }
                    />
                  </Input>
                  <Input
                    id="max-price"
                    classes="advanced-filter-input"
                    size="normal"
                  >
                    <Input.Prepend>
                      <span>{ t('filters.advanced-filters.price.max') }<span className="currency">$</span></span>
                    </Input.Prepend>
                    <Input.Field
                      type="number"
                      step={ 0.25 }
                      placeholder={ '0.00' }
                    />
                  </Input>
                </div>
              </div>
              <div className="col">
                <div className="advanced-filter-title">
                  { t('filters.advanced-filters.exterior.title') }
                </div>
                <div className="advanced-filter-inputs">
                  <Input
                    id="min-exterior"
                    classes="advanced-filter-input"
                    size="normal"
                  >
                    <Input.Prepend>
                      { t('filters.advanced-filters.exterior.min') }
                    </Input.Prepend>
                    <Input.Field
                      type="number"
                    />
                  </Input>
                  <Input
                    id="max-exterior"
                    classes="advanced-filter-input"
                    size="normal"
                  >
                    <Input.Prepend>
                      { t('filters.advanced-filters.exterior.max') }
                    </Input.Prepend>
                    <Input.Field
                      type="number"
                    />
                  </Input>
                </div>
              </div>
              <div className="col">
                <div className="advanced-filter-title">
                  { t('filters.advanced-filters.trade-lock.title') }
                </div>
                <div className="advanced-filter-inputs">
                  <Input
                    id="min-lock-days"
                    classes="advanced-filter-input input-days"
                    size="normal"
                  >
                    <Input.Prepend>
                      <span>{ t('filters.advanced-filters.trade-lock.min') }</span>
                    </Input.Prepend>
                    <Input.Field
                      type="number"
                      step={ 1 }
                      min={ 0 }
                      max={ 8 }
                    />
                    <Input.Append>
                      <span>{ t('filters.advanced-filters.trade-lock.days') }</span>
                    </Input.Append>
                  </Input>
                  <Input
                    id="max-lock-days"
                    classes="advanced-filter-input input-days"
                    size="normal"
                  >
                    <Input.Prepend>
                      <span>{ t('filters.advanced-filters.trade-lock.max') }</span>
                    </Input.Prepend>
                    <Input.Field
                      type="number"
                      step={ 1 }
                      min={ 0 }
                      max={ 8 }
                    />
                    <Input.Append>
                      <span>{ t('filters.advanced-filters.trade-lock.days') }</span>
                    </Input.Append>
                  </Input>
                </div>
              </div>
              <div className="col">
                <div className="advanced-filter-title content-between">
                  { t('filters.advanced-filters.stickers.title') }
                  <div className="amount">
                    { t('filters.advanced-filters.stickers.amount') }
                    <span className="arrow"/>
                  </div>
                </div>
                <div className="advanced-filter-inputs">
                  <Input
                    id="stickers-search"
                    classes="advanced-filter-input big-input"
                    size="normal"
                  >
                    <Input.Prepend>
                      <SearchIcon/>
                    </Input.Prepend>
                    <Input.Field
                      type="text"
                      placeholder={ t('filters.advanced-filters.stickers.search-input') }
                    />
                  </Input>
                </div>
              </div>
            </div>
            <div className="row content-between sm-col">
              <div className="col">
                <Select>
                  <Select.Toggle>
                    <span>Редкость <span className="count">( 1 )</span></span>
                  </Select.Toggle>
                  <Select.Menu>
                    <Select.Item>
                      <Checkbox size="normal" id="1">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Базового класса</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="2">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Армейское качество</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="3">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Ширпотреб</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                  </Select.Menu>
                </Select>
              </div>
              <div className="col">
                <Select>
                  <Select.Toggle>
                    <span>Тип оружия <span className="count">( 1 )</span></span>
                  </Select.Toggle>
                  <Select.Menu>
                    <Select.Item>
                      <Checkbox size="normal" id="4">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Базового класса</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="5">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Армейское качество</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="6">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Ширпотреб</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                  </Select.Menu>
                </Select>
              </div>
              <div className="col">
                <Select>
                  <Select.Toggle>
                    <span>Фаза ножа <span className="count">( 1 )</span></span>
                  </Select.Toggle>
                  <Select.Menu>
                    <Select.Item>
                      <Checkbox size="normal" id="7">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Базового класса</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="8">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Армейское качество</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="9">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Ширпотреб</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                  </Select.Menu>
                </Select>
              </div>
              <div className="col">
                <Select>
                  <Select.Toggle>
                    <span>Коллекция <span className="count">( 1 )</span></span>
                  </Select.Toggle>
                  <Select.Menu>
                    <Select.Item>
                      <Checkbox size="normal" id="10">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Базового класса</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="11">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Армейское качество</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                    <Select.Item>
                      <Checkbox size="normal" id="12">
                        <Checkbox.Label>
                          <Checkbox.Input/>
                          <span>Ширпотреб</span>
                        </Checkbox.Label>
                      </Checkbox>
                    </Select.Item>
                  </Select.Menu>
                </Select>
              </div>
            </div>
            <div className="row content-between sm-col align-center">
              <Switch id={ '0' } classes="advanced-filter-switcher">
                <Switch.Input/>
                <Switch.Label>
                  StatTrakTM
                </Switch.Label>
              </Switch>
              <Switch id={ '1' } classes="advanced-filter-switcher">
                <Switch.Input/>
                <Switch.Label>
                  Сувенирные
                </Switch.Label>
              </Switch>
              <Switch id={ '2' } classes="advanced-filter-switcher">
                <Switch.Input/>
                <Switch.Label>
                  Стикеры
                </Switch.Label>
              </Switch>
              <Switch id={ '3' } classes="advanced-filter-switcher">
                <Switch.Input/>
                <Switch.Label>
                  Неймтег
                </Switch.Label>
              </Switch>
              <Switch id={ '4' } classes="advanced-filter-switcher">
                <Switch.Input/>
                <Switch.Label>
                  Только предметы без блокировки
                </Switch.Label>
              </Switch>
              <Button
                size="normal"
                classes="reset-filters"
                color="primary"
              >
                Сбросить фильтры
              </Button>
            </div>
          </div>
        </Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
  )
}