import classNames from 'classnames'
import React from 'react'
import { ReactComponent as BumpIcon } from '../../../../assets/icons/bump.svg'
import { ReactComponent as ClockIcon } from '../../../../assets/icons/clock.svg'
import { ReactComponent as LockIcon } from '../../../../assets/icons/lock.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { ItemSticker } from '../../../../newComponents/ItemSticker/ItemSticker'
import { EditPriceOnClick } from '../EditPriceOnClick/EditPriceOnClick'
import { ItemSellByEditOnClick } from '../ItemSellByEditOnClick/ItemSellByEditOnClick'
import './TransactionItem.scss'

interface iTransactionItem {
  id: number | string
  image: string
  visual: [
    {
      icon?: any
      title?: any
      wear?: any
    }
  ]
  status: any
  mainName: string
  additionalName?: string
  secondaryName: string
  price: number
  changeItemPrice: any
  sellItem: any
  bumpItem: any
  withdrawItem: any
  originalPrice: number
  method: string
  float: number
  dateGet: string
  lock: number
  lastBump: string
  dateWithdrawn?: any
  dateSold?: string
}

export const TransactionItem = (
  {
    id,
    image,
    visual,
    status,
    mainName,
    additionalName,
    secondaryName,
    price,
    changeItemPrice,
    sellItem,
    bumpItem,
    withdrawItem,
    originalPrice,
    method,
    float,
    dateGet,
    lock,
    lastBump,
    dateWithdrawn,
    dateSold
  }: iTransactionItem) => {
  
  return (
    <div
      className="transaction-item"
    >
      <div className="transaction-visual">
        <div className="item-visual-main">
          <img
            src={ image }
            alt={ `${ mainName } (${ additionalName }) ${ secondaryName }` }
          />
        </div>
        <div className="item-visual-secondary">
          {
            Array.from({ length: 4 }, (_, i) =>
              <ItemSticker
                sticker={ visual[i]?.icon }
                stickerTitle={ visual[i]?.title }
                stickerWear={ visual[i]?.wear }
              />
            )
          }
        </div>
      </div>
      <div className="transaction-item-info">
        <div className="item-name-main">
          { mainName }
          <span className="item-name-main-additional">
            { additionalName }
          </span>
        </div>
        <div className="item-name-secondary">
          { secondaryName }
        </div>
        <div className="item-status">
          Статус: <span>{ status }</span>
        </div>
      </div>
      <div className="transaction-item-divider"/>
      <div className="transaction-price-method">
        {
          (() => {
            switch (status) {
              case 'inventory':
                return (
                  <ItemSellByEditOnClick
                    initialValue={ originalPrice }
                    endEditing={ (val: number) => {
                      sellItem(id, val)
                    } }
                  />
                )
              case 'sold':
                return (
                  <div className="price">
                    <span className="currency">$</span>100.49
                  </div>
                )
              case 'market':
                return (
                  <EditPriceOnClick
                    initialValue={ price }
                    endEditing={ (val: number) => {
                      if (price !== val) {
                        changeItemPrice(id, val)
                      }
                    } }
                  />
                )
              case 'withdrawn':
                return (
                  <div className="mb"/>
                )
              case 'need-attention':
                return (
                  <EditPriceOnClick
                    initialValue={ price }
                    endEditing={ (val: number) => {
                      if (price !== val) {
                        changeItemPrice(id, val)
                      }
                    } }
                  />
                )
            }
          })()
        }
        <div className="method">
          Метод: <span>{ method } (${ originalPrice })</span>
        </div>
      </div>
      <div className="transaction-item-divider"/>
      <div className="transaction-information">
        <div className="transaction-information-block">
          <div className="item-id">
            ID: <span>{ id }</span>
          </div>
          <div className="item-float">
            Float: <span>{ float }</span>
          </div>
        </div>
        <div className="transaction-information-block">
          <div className="item-date">
            Получен: <span>{ dateGet }</span>
          </div>
          {
            status === 'sold'
              ? (
                <div className="sell-date">
                  Продан: <span>{ dateSold }</span>
                </div>
              )
              : status === 'withdrawn'
              ? (
                <div className="withdrawn-date">
                  Выведен: <span>{ dateWithdrawn }</span>
                </div>
              )
              : (
                (
                  <div
                    className={
                      classNames(
                        'item-lock',
                        lock === 0 ? 'unlocked' : 'locked'
                      )
                    }
                  >
                    {
                      lock === 0
                        ? <LockIcon/>
                        : <ClockIcon/>
                    }
                    {
                      lock === 0 && 'Разблокирован'
                    }
                    {
                      lock !== 0 && 'Дней до вывода:'
                    }
                    <span>
                    {
                      lock !== 0 && lock
                    }
                  </span>
                  </div>
                )
              )
          }
        </div>
      </div>
      <div className="transaction-item-divider"/>
      <div className="transaction-actions">
        {
          (() => {
            switch (status) {
              case 'inventory':
                return (
                  <>
                    <Button
                      size="normal"
                      color="secondary"
                      classes="withdraw"
                      disabled={ false }
                      onClick={ () => withdrawItem(id) }
                    >
                      <BumpIcon/>
                      <span>Вывести</span>
                    </Button>
                  </>
                )
              case 'sold':
                return (
                  <>
                    <div className="bump">
                      <div className="last-bump-title">
                        Последний бамп
                      </div>
                      <div className="last-bump-date">
                        {
                          lastBump
                        }
                      </div>
                    </div>
                    <Button
                      size="normal"
                      color="secondary"
                      disabled={ true }
                    >
                      <BumpIcon/>
                      <span>Бамп</span>
                    </Button>
                  </>
                )
              case 'market':
                return (
                  <>
                    <div className="bump">
                      <div className="last-bump-title">
                        Последний бамп
                      </div>
                      <div className="last-bump-date">
                        {
                          lastBump
                        }
                      </div>
                    </div>
                    <Button
                      size="normal"
                      color="secondary"
                      disabled={ false }
                      onClick={ () => bumpItem(id) }
                    >
                      <BumpIcon/>
                      <span>Бамп</span>
                    </Button>
                  </>
                )
              case 'withdrawn':
                return (
                  <>
                    <div className="bump">
                      <div className="last-bump-title">
                        Последний бамп
                      </div>
                      <div className="last-bump-date">
                        {
                          lastBump
                        }
                      </div>
                    </div>
                    <Button
                      size="normal"
                      color="secondary"
                      disabled={ true }
                    >
                      <BumpIcon/>
                      <span>Бамп</span>
                    </Button>
                  </>
                )
              case 'need-attention':
                return (
                  <>
                    <div className="bump">
                      <div className="last-bump-title">
                        Последний бамп
                      </div>
                      <div className="last-bump-date">
                        {
                          lastBump
                        }
                      </div>
                    </div>
                    <Button
                      size="normal"
                      color="secondary"
                      disabled={ false }
                      onClick={ () => bumpItem(id) }
                    >
                      <BumpIcon/>
                      <span>Бамп</span>
                    </Button>
                  </>
                )
            }
          })()
        }
      </div>
    </div>
  )
}