import React, { useState } from 'react'
import { Button } from '../../../../components/Controls/Button/Button'
import { AutocompleteInput } from '../AutocompleteInput/AutocompleteInput'
import { PriceInput } from '../PriceInput/PriceInput'
import { SearchQuantityInput } from '../SearchQuantityInput/SearchQuantityInput'
import './OrderContainer.scss'
import { GameSelector } from '../GameSelector/GameSelector'

interface iOrderContainer {
  game: string
  onGameChange: any
  searchInput: string
  searchInputHandler: any
  quantity: number
  quantityHandler: any
  price: number
  priceHandler: any
  makeOrder: any
  toggleOrderSidebar: any
  games: any
  itemSelected: any
  onItemSelect: any
  size: any
}

export const OrderContainer = (
  {
    size,
    itemSelected,
    onItemSelect,
    game,
    games,
    onGameChange,
    toggleOrderSidebar,
    searchInput,
    searchInputHandler,
    quantity,
    quantityHandler,
    price,
    priceHandler,
    makeOrder
  }: iOrderContainer) => {
  
  const handleFocus = (e: any) => e.target.classList.add('input-focus')
  const handleBlur = (e: any) => e.target.classList.remove('input-focus')
  
  switch (size) {
    case 'tablet': {
      return <div className="orders-form tablet">
        <GameSelector
          game={ game }
          games={ games }
          onGameChange={ onGameChange }
        />
        <AutocompleteInput
          selected={ itemSelected }
          onItemSelect={ onItemSelect }
          value={ searchInput }
          onChange={ searchInputHandler }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <Button
          size="normal"
          color="primary"
          classes="make-order"
          onClick={ () => {
            toggleOrderSidebar()
            makeOrder({ itemId: 3, price, quantity })
          } }
        >
          Поиск
        </Button>
      </div>
    }
    case 'laptop': {
      return <div className="orders-form laptop">
        <GameSelector
          game={ game }
          games={ games }
          onGameChange={ onGameChange }
        />
        <AutocompleteInput
          selected={ itemSelected }
          onItemSelect={ onItemSelect }
          value={ searchInput }
          onChange={ searchInputHandler }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <SearchQuantityInput
          value={ quantity }
          onChange={ (n: number) => quantityHandler(n) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <PriceInput
          value={ price }
          onChange={ (price: any) => priceHandler(price?.value) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <Button
          size="normal"
          color="primary"
          classes="make-order"
          onClick={ () => {
            toggleOrderSidebar()
            makeOrder({ itemId: 3, price, quantity })
          } }
        >
          Создать
        </Button>
      </div>
    }
    case 'desktop-small': {
      return <div className="orders-form desktop-small">
        <GameSelector
          game={ game }
          games={ games }
          onGameChange={ onGameChange }
        />
        <AutocompleteInput
          selected={ itemSelected }
          onItemSelect={ onItemSelect }
          value={ searchInput }
          onChange={ searchInputHandler }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <SearchQuantityInput
          value={ quantity }
          onChange={ (n: number) => quantityHandler(n) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <PriceInput
          value={ price }
          onChange={ (price: any) => priceHandler(price?.value) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <Button
          size="normal"
          color="primary"
          classes="make-order"
          onClick={ () => {
            toggleOrderSidebar()
            makeOrder({ itemId: 3, price, quantity })
          } }
        >
          Создать
        </Button>
      </div>
    }
    case 'desktop-large': {
      return <div className="orders-form desktop-large">
        <GameSelector
          game={ game }
          games={ games }
          onGameChange={ onGameChange }
        />
        <AutocompleteInput
          selected={ itemSelected }
          onItemSelect={ onItemSelect }
          value={ searchInput }
          onChange={ searchInputHandler }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <SearchQuantityInput
          value={ quantity }
          onChange={ (n: number) => quantityHandler(n) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <PriceInput
          value={ price }
          onChange={ (price: any) => priceHandler(price?.value) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <Button
          size="normal"
          color="primary"
          classes="make-order"
          onClick={ () => {
            toggleOrderSidebar()
            makeOrder({ itemId: 3, price, quantity })
          } }
        >
          Создать запрос
        </Button>
      </div>
    }
    default: {
      return <div className="orders-form">
        <GameSelector
          game={ game }
          games={ games }
          onGameChange={ onGameChange }
        />
        <AutocompleteInput
          selected={ itemSelected }
          onItemSelect={ onItemSelect }
          value={ searchInput }
          onChange={ searchInputHandler }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <span>Кол-во:</span>
        <SearchQuantityInput
          value={ quantity }
          onChange={ (n: number) => quantityHandler(n) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <span>Цена:</span>
        <PriceInput
          value={ price }
          onChange={ (price: any) => priceHandler(price?.value) }
          onFocus={ handleFocus }
          onBlur={ handleBlur }
        />
        <Button
          size="normal"
          color="primary"
          classes="make-order"
          onClick={ () => {
            toggleOrderSidebar()
            makeOrder({ itemId: 3, price, quantity })
          } }
        >
          Создать запрос
        </Button>
      </div>
    }
  }
}