import classNames from 'classnames'
import React, { useState } from 'react'
import InputRange from 'react-input-range'
import NumberFormat from 'react-number-format'
import { SidebarFilterContainer } from '../SidebarFilterContainer/SidebarFilterContainer'
import './LockFilter.scss'

interface iLockFilter {
  onRangeChange: any
  values: { min: number, max: number }
  isCollapsed: boolean
  onCollapse: any
  onReset: any
  min: number
  max: number
  onMinChange: any
  onMaxChange: any
}

interface iLockInput {
  prepend: string
  append: string
  value: number
  onChange: any
  rules: any
  errors: any
}

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

const LockInput = ({ prepend, append, value, onChange, rules, errors }: iLockInput) => {
  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'lock-input-group'
        )
      }
    >
      <div className="lock-input-prepend">
        { prepend }
      </div>
      <NumberFormat
        className="lock-input"
        isAllowed={ rules }
        isNumericString={ true }
        allowNegative={ false }
        allowLeadingZeros={ false }
        decimalScale={ 0 }
        fixedDecimalScale={ true }
        allowEmptyFormatting={ false }
        thousandSeparator=" "
        value={ value }
        onValueChange={ (number) => onChange(number.value) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
      <div className="lock-input-append">
        { append }
      </div>
    </div>
  )
}

export const LockFilter = ({ onRangeChange, values, isCollapsed, onCollapse, onReset, min, max, onMinChange, onMaxChange }: iLockFilter) => {
  const MIN_LIMIT: number = 0
  const MAX_LIMIT: number = 8
  const [minErrors, setMinErrors] = useState<boolean>(false)
  const [maxErrors, setMaxErrors] = useState<boolean>(false)
  
  const minInputValueLimit = (inputObj: { floatValue: number }) => {
    const { floatValue } = inputObj
    
    if (floatValue >= MIN_LIMIT && floatValue <= MAX_LIMIT && floatValue < max) {
      setMinErrors(false)
      return inputObj
    } else {
      setMinErrors(true)
      return inputObj
    }
  }
  
  const maxInputValueLimit = (inputObj: { floatValue: number }) => {
    const { floatValue } = inputObj
    
    if (floatValue >= MIN_LIMIT && floatValue <= MAX_LIMIT && floatValue > min) {
      setMaxErrors(false)
      return inputObj
    } else {
      setMaxErrors(true)
      return inputObj
    }
  }
  
  const declOfNum = (number: number, words: string[]) => words[(number % 100 > 4 && number % 100 < 20) ? 2 : [2, 0, 1, 1, 1, 2][(number % 10 < 5) ? number % 10 : 5]]
  
  return (
    <SidebarFilterContainer
      title="Блокировка"
      isCollapsed={ isCollapsed }
      onCollapse={ onCollapse }
      onReset={ onReset }
      className="lock-filter"
    >
      <div className="inputs">
        <LockInput
          value={ min }
          onChange={ onMinChange }
          prepend="От"
          append={ declOfNum(min, ['дня', 'дней', 'дней']) }
          rules={ minInputValueLimit }
          errors={ minErrors }
        />
        <LockInput
          value={ max }
          onChange={ onMaxChange }
          prepend="До"
          append={ declOfNum(max, ['дня', 'дней', 'дней']) }
          rules={ maxInputValueLimit }
          errors={ maxErrors }
        />
      </div>
      <InputRange
        minValue={ MIN_LIMIT }
        maxValue={ MAX_LIMIT }
        value={ values }
        onChange={ (value) => onRangeChange(value) }
      />
    </SidebarFilterContainer>
  )
}