import React from 'react'
import './SearchQuantityInput.scss'
import classNames from 'classnames'

export const SearchQuantityInput = ({ value, onChange, onFocus, onBlur }: any) => {
  return (
    <input
      min="1"
      type="number"
      className={
        classNames(
          'orders-form-item-search-quantity'
        )
      }
      placeholder="Количество"
      value={ value }
      onChange={ (e) => onChange(e.target.value) }
      onFocus={ onFocus }
      onBlur={ onBlur }
    />
  )
}