import React from 'react'
import './SwitchButton.scss'

interface iSwitchButton {
  active: boolean
  onChange: (status: boolean) => void
}

export const SwitchButton = ({ active, onChange }: iSwitchButton) => {
  return (
    <div className="switch-button">
      <div
        key={ Math.random() }
        className={ active ? 'switch-button--active' : 'switch-button--inactive' }
        onClick={ () => onChange(true) }
      >
        Депозит
      </div>
      <div
        key={ Math.random() }
        className={ !active ? 'switch-button--active' : 'switch-button--inactive' }
        onClick={ () => onChange(false) }
      >
        Вывод
      </div>
    </div>
  )
}