import React from 'react'
import { NavLink, Route, Switch } from 'react-router-dom'
import { Button } from './components/Controls/Controls'
import { Footer } from './components/Footer/Footer'
import { Header } from './components/Header/Header'
import { MobileFooter } from './components/MobileFooter/MobileFooter'
import { MobileHeader } from './components/MobileHeader/MobileHeader'
import { Mobile, NonTablet, UnderLaptopSmall, Tablet } from './components/Responsive/Responsive'
import { AuthPage } from './pages/AuthPage/AuthPage'
import { BalancePage } from './pages/BalancePage/BalancePage'
import { CartPage } from './pages/CartPage/CartPage'
import { FAQPage } from './pages/FAQPage/FAQPage'
import { HomePage } from './pages/HomePage/HomePage'
import { InventoryPage } from './pages/InventoryPage/InventoryPage'
import { ItemPage } from './pages/ItemPage/ItemPage'
import { MarketPage } from './pages/MarketPage/MarketPage'
import { OrdersPage } from './pages/OrdersPage/OrdersPage'
import { ProfilePage } from './pages/ProfilePage/ProfilePage'
import { SellerPage } from './pages/SellerPage/SellerPage'
import { SupportPage } from './pages/SupportPage/SupportPage'
import { TicketPage } from './pages/TicketPage/TicketPage'
import { ErrorPage } from './pages/ErrorPage/ErrorPage'
import { TransactionsPage } from './pages/TransactionsPage/TransactionsPage'

export const useRoutes = (isAuthenticated: boolean) => {
  return (
    <>
      <Switch>
        <Route path={ `/(market|seller)` }>
          <Mobile>
            <MobileHeader/>
          </Mobile>
          <Tablet>
            <MobileHeader/>
          </Tablet>
          <NonTablet>
            <Header filterEnabled={ false } navigationEnabled={ true }/>
          </NonTablet>
        </Route>
        <Route path="/">
          <UnderLaptopSmall>
            <MobileHeader/>
          </UnderLaptopSmall>
          <NonTablet>
            <Header filterEnabled={ true } navigationEnabled={ true }/>
          </NonTablet>
        </Route>
      </Switch>
      <div className="content">
        <Switch>
          <Route
            path="/auth"
            exact
            component={ AuthPage }
          />
          <Route
            path="/balance"
            exact
            component={ BalancePage }
          />
          <Route
            path="/cart"
            exact
            component={ CartPage }
          />
          <Route
            path="/faq"
            exact
            component={ FAQPage }
          />
          <Route
            path="/home"
            exact
            component={ HomePage }
          />
          <Route
            path="/inventory"
            exact
            component={ InventoryPage }
          />
          <Route
            path="/item"
            exact
            component={ ItemPage }
          />
          <Route
            path="/market"
            exact
            component={ MarketPage }
          />
          <Route
            path="/orders/:id"
            component={ OrdersPage }
          />
          <Route
            path="/seller"
            exact
            component={ SellerPage }
          />
          <Route
            path="/support"
            exact
            component={ SupportPage }
          />
          <Route
            path="/ticket"
            exact
            component={ TicketPage }
          />
          <Route
            path="/transactions"
            exact
            component={ TransactionsPage }
          />
          <Route
            path="/profile"
            // exact
            component={ ProfilePage }
          />
          <Route
            path=""
            render={() => {
              return (
                <ErrorPage type="not-found"/>
              )
            }}
          />
        </Switch>
      </div>
      <Route path="/">
        <UnderLaptopSmall>
          <MobileFooter/>
        </UnderLaptopSmall>
        <NonTablet>
          <Footer/>
        </NonTablet>
      </Route>
    </>
  )
}