import { curveNatural } from '@visx/curve'
import { LinearGradient } from '@visx/gradient'
import { Group } from '@visx/group'
import { scaleLinear, scaleTime } from '@visx/scale'
import { LinePath } from '@visx/shape'
import { extent, max } from 'd3-array'
import React from 'react'
import { MarkerArrow, MarkerCross, MarkerX, MarkerCircle, MarkerLine } from '@visx/marker'

export type CurveProps = {
  width: number
  height: number
  from: string
  to: string
  chartId: string
  data: any
}

interface DateValue {
  date: Date,
  value: number
}

export default function CurveChart({ width, height, from, to, chartId, data }: CurveProps) {
  const lineCount = 1
  
  const series = new Array(lineCount)
    .fill(null)
    .map((_) =>
      data.sort(
        (a: { date: Date, value: number }, b: { date: Date, value: number }) => a.date.getTime() - b.date.getTime()
      )
    )
  
  const allData = series.reduce((rec, d) => rec.concat(d), [])
  
  // data accessors
  const getX = (d: DateValue) => d.date
  const getY = (d: DateValue) => d.value
  
  // scales
  const xScale = scaleTime<number>({
    domain: extent(allData, getX) as [Date, Date]
  })
  const yScale = scaleLinear<number>({
    domain: [0, max(allData, getY) as number]
  })
  
  const svgHeight = height
  const lineHeight = svgHeight / lineCount
  
  // update scale output ranges
  xScale.range([2, width - 2])
  yScale.range([lineHeight, lineHeight / 5])
  
  return (
    <div
      className="visx-curves-demo"
    >
      <svg width={ width } height={ svgHeight }>
        <rect width={ width } height={ svgHeight } fill="transparent"/>
        { width > 8 &&
        series.map((lineData, i) => {
          return (
            <Group key={ `lines-${ i }` } top={ i * svgHeight } left={ 0 }>
              <LinearGradient id={ chartId } from={ from } to={ to } vertical={ false }/>
              <LinePath<DateValue>
                curve={ curveNatural }
                data={ allData }
                x={ (d) => xScale(getX(d)) ?? 0 }
                y={ (d) => yScale(getY(d)) ?? 0 }
                stroke={ `url(#${ chartId })` }
                strokeWidth={ 5 }
                strokeOpacity={ 1 }
                shapeRendering="geometricPrecision"
              />
            </Group>
          )
        }) }
      </svg>
    </div>
  )
}