import classNames from 'classnames'
import React from 'react'

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

interface iConfirmationInput {
  value: string
  onChange: any
  errors: any
}

export const ConfirmationInput = ({ value, onChange, errors }: iConfirmationInput) => {

  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'confirmation-input'
        )
      }
    >
      <input
        placeholder="Введите abc для проверки"
        className="confirmation-input--input"
        type="text"
        value={ value }
        onChange={ (e) => onChange(e.target.value) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}
