import classNames from 'classnames'
import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { toast } from 'react-toastify'
import { ReactComponent as CryptoIcon } from '../../assets/icons/payment/crypto.svg'
import { ReactComponent as CSPaymentIcon } from '../../assets/icons/payment/csgo.svg'
import { ReactComponent as DotaPaymentIcon } from '../../assets/icons/payment/dota.svg'
import { ReactComponent as G2AIcon } from '../../assets/icons/payment/g2a.svg'
import { ReactComponent as MastercardIcon } from '../../assets/icons/payment/mastercard.svg'
import { ReactComponent as PaypalIcon } from '../../assets/icons/payment/paypal.svg'
import { ReactComponent as QiwiIcon } from '../../assets/icons/payment/qiwi.svg'
import { ReactComponent as WebmoneyIcon } from '../../assets/icons/payment/webmoney.svg'
import { ReactComponent as YdIcon } from '../../assets/icons/payment/yd.svg'
import { ReactComponent as WarningIcon } from '../../assets/icons/warning.svg'
import { AmountSuggestion } from '../../components/AmountSuggestion/AmountSuggestion'
import { Button } from '../../components/Controls/Controls'
import { PaymentServices } from '../../components/PaymentServices/PaymentServices'
import { Laptop, Mobile, NonLaptop, Tablet } from '../../components/Responsive/Responsive'
import useCountries from '../../hooks/useCountries'
import { useBalance } from '../../newHooks/useBalance'
import './BalancePage.scss'
import { AmountInput } from './LocalComponents/AmountInput/AmountInput'
import { ConfirmationInput } from './LocalComponents/ConfirmationInput/ConfirmationInput'
import { ServiceNumber } from './LocalComponents/ServiceNumber/ServiceNumber'
import { SwitchButton } from './LocalComponents/SwitchButton/SwitchButton'
import { DropdownSelect } from '../OrdersPage/LocalComponents/DropdownSelect/DropdownSelect'

export const BalancePage = () => {
  const { countries } = useCountries
  const history = useHistory()
  
  const [inputAmountValue, setInputAmountValue] = useState<number>(0)
  const [isCustomAmount, setIsCustomAmount] = useState<boolean>(false)
  const [isDeposit, setIsDeposit] = useState<boolean>(true)
  const [withdrawStage, setWithdrawStage] = useState<boolean>(false)
  const [paymentServices] = useState<any>([
    {
      title: 'G2A Pay',
      value: 'g2a',
      icon: <G2AIcon/>
    },
    {
      title: 'Банковская карта',
      value: 'card',
      icon: <MastercardIcon/>
    },
    {
      title: 'ЮMoney',
      value: 'yoomoney',
      icon: <YdIcon/>
    },
    {
      title: 'WebMoney',
      value: 'wm',
      icon: <WebmoneyIcon/>
    },
    {
      title: 'QIWI',
      value: 'qiwi',
      icon: <QiwiIcon/>
    },
    {
      title: 'PayPal',
      value: 'paypal',
      icon: <PaypalIcon/>
    },
    {
      title: 'Автопродажа CS:GO',
      value: 'csgo',
      icon: <CSPaymentIcon/>
    },
    {
      title: 'Автопродажа Dota 2',
      value: 'dota',
      icon: <DotaPaymentIcon/>
    },
    {
      title: 'Криптовалюты',
      value: 'crypto',
      icon: <CryptoIcon/>
    }
  ])
  const amountSuggestions = [10, 25, 50, 100, 300, 500]
  
  const [service, setService] = useState<any>(null)
  const [amount, setAmount] = useState<number>(0)
  const [serviceNumber, setServiceNumber] = useState<string>('')
  const [confirmationCode, setConfirmationCode] = useState<string>('')
  
  const [country, setCountry] = useState({
    capital: 'Moscow',
    continent: 'EU',
    currency: 'RUB',
    emoji: '🇷🇺',
    emojiU: 'U+1F1F7 U+1F1FA',
    languages: ['ru'],
    name: 'Russia',
    native: 'Россия',
    phone: '7',
    title: 'Россия',
    value: 'RU',
  })
  const [serviceNumberError, setServiceNumberError] = useState<boolean>(false)
  const [amountError, setAmountError] = useState<boolean>(false)
  const [confirmationErrors, setConfirmationErrors] = useState<boolean>(false)
  const amountInputRules = (inputObj: { floatValue: number }) => {
    const { floatValue } = inputObj
    if (floatValue >= 0 && floatValue <= 500) {
      setAmountError(false)
      return inputObj
    } else {
      setAmountError(true)
      return inputObj
    }
  }
  const onServiceSelect = (service: any) => setService(service)
  const onAmountSelect = (amount: number) => {
    setIsCustomAmount(false)
    const amountFormatted = Math.round(amount * 100) / 100
    setAmount(amountFormatted)
  }
  const handleInputAmountChange = (val: number) => setInputAmountValue(val)
  const handleInputPhoneNumber = (phoneNumber: string) => setServiceNumber(phoneNumber)
  const handleCountryChange = (country: any) => setCountry(country)
  const handleConfirmationCode = (code: string) => {
    setConfirmationCode(code)
  }
  const handleInputAmountClick = () => setIsCustomAmount(true)
  
  const balance = 5000
  
  const onDeposit = () => {
    if (!country) toast.error('Страна не выбрана', { autoClose: 10000 })
    if (!service) toast.error('Не выбрана платежная система', { autoClose: 10000 })
    if (!serviceNumber) toast.error('Не указан счет', { autoClose: 10000 })
    if (!amount || !inputAmountValue) toast.error('Не указана сумма платежа', { autoClose: 10000 })
    const amountNumber = Number(amount)
    
    toast.success('Депозит успешно выполнен', { autoClose: 5000 })
    setTimeout(() => history.push('/profile/transactions'), 1000)
    
    console.log('Deposit data:', { country, service, serviceNumber, amount })
  }
  
  const onWithdraw = () => {
    if (!country) toast.error('Страна не выбрана', { autoClose: 10000 })
    if (!service) toast.error('Не выбрана платежная система', { autoClose: 10000 })
    if (!serviceNumber) toast.error('Не указан счет', { autoClose: 10000 })
    if (!amount || !inputAmountValue) toast.error('Не указана сумма платежа', { autoClose: 10000 })
    if (!confirmationCode) toast.error('Не указан 2fa код', { autoClose: 10000 })
    
    if (confirmationCode === 'abc') {
      toast.success('Вывод успешно выполнен', { autoClose: 5000 })
      setTimeout(() => history.push('/transactions'), 1000)
    }
    
    console.log('Withdraw data:', { country, service, serviceNumber, amount })
  }
  
  useEffect(() => {
    document.title = 'Balance | SkinsMart'
    if (isCustomAmount) {
      setAmount(inputAmountValue)
    } else {
      setAmount(amount)
    }
  }, [amount, inputAmountValue, isCustomAmount])
  
  return (
    <>
      <Mobile>
        <div className="balance-page mobile">
          <div className="balance-page-title">
            Баланс
          </div>
          <div className="balance-page-body">
            <SwitchButton
              active={ isDeposit }
              onChange={ (status) => setIsDeposit(status) }
            />
            <DropdownSelect
              title={ 'Платежная система' }
              selected={ service }
              onChange={ onServiceSelect }
              options={ paymentServices }
            />
            <DropdownSelect
              title={ 'Страна' }
              selected={ country }
              onChange={ handleCountryChange }
              options={ countries }
            />
            <div className="input-group">
              <div className="input-group-title">
                Введите сумму
              </div>
              <div className="input-group-description">
                Баланс на сайте: ${ balance }
              </div>
              <AmountInput
                value={ inputAmountValue }
                onChange={ handleInputAmountChange }
                onClick={ handleInputAmountClick }
                prepend="$"
                errors={ amountError }
                rules={ amountInputRules }
              />
            </div>
            <div className="input-group">
              <div className="input-group-title">
                Или выберете из предложенного:
              </div>
              <div className="input-group-content">
                <AmountSuggestion
                  active={ !isCustomAmount }
                  onSelect={ onAmountSelect }
                  suggestions={ amountSuggestions }
                  featuredAmount={ 50 }
                />
              </div>
            </div>
            <div className="input-group">
              <div className="input-group-title">
                Ваши реквизиты
              </div>
              <ServiceNumber
                selectedService={ service?.value }
                value={ serviceNumber }
                onChange={ handleInputPhoneNumber }
                errors={ '' }
                country={ country }
              />
            </div>
            <div className="balance-attention">
              Будет использован сторонний сервис, цены и комиссии которого мы <span className="danger-text">не регулируем</span>
            </div>
            <div className="balance-sum-block">
              <div className="balance-block-row sum-to-pay">
                <span>
                  Итого
                </span>
                <span className="balance-block-row-num">
                  $ { isCustomAmount ? inputAmountValue : amount }
                </span>
              </div>
              <div className="balance-block-row local">
                <span>
                  RUB
                </span>
                <span className="balance-block-row-num">
                  ₽ { isCustomAmount ? Math.round(inputAmountValue * 75) : Math.round(amount * 75) }
                </span>
              </div>
            </div>
            <div
              className={
                classNames(
                  'input-group',
                  'confirmation-input-group',
                  (!isDeposit && 'show')
                )
              }
            >
              <div className="input-group-title">
                Подтверждение
              </div>
              <ConfirmationInput
                value={ confirmationCode }
                onChange={ handleConfirmationCode }
                errors={ confirmationErrors }
              />
            </div>
            <Button
              size="large"
              color="primary"
              onClick={ isDeposit ? onDeposit : onWithdraw }
              disabled={ isDeposit ? !(!!service && !!country && !!inputAmountValue && !!serviceNumber) : !(!!service && !!country && !!inputAmountValue && !!serviceNumber && !!confirmationCode) }
            >
              { isDeposit ? 'Депозит' : 'Вывод' }
            </Button>
          </div>
        </div>
      </Mobile>
      <Tablet>
        <div className="balance-page tablet">
          <div className="balance-page-title">
            Баланс
          </div>
          <div className="balance-page-body">
            <SwitchButton
              active={ isDeposit }
              onChange={ (status) => setIsDeposit(status) }
            />
            <div className="balance-page-row">
              <DropdownSelect
                title={ 'Платежная система' }
                selected={ service }
                onChange={ onServiceSelect }
                options={ paymentServices }
              />
              <DropdownSelect
                title={ 'Страна' }
                selected={ country }
                onChange={ handleCountryChange }
                options={ countries }
              />
            </div>
            <div className="balance-page-row balance-page-row-group">
              <div className="input-group">
                <div className="input-group-title">
                  Введите сумму
                </div>
                <AmountInput
                  value={ inputAmountValue }
                  onChange={ handleInputAmountChange }
                  onClick={ handleInputAmountClick }
                  prepend="$"
                  errors={ amountError }
                  rules={ amountInputRules }
                />
              </div>
              <div className="input-group">
                <div className="input-group-title">
                  Ваши реквизиты
                </div>
                <ServiceNumber
                  selectedService={ service?.value }
                  value={ serviceNumber }
                  onChange={ handleInputPhoneNumber }
                  errors={ '' }
                  country={ country }
                />
              </div>
            </div>
            <div className="balance-page-row">
              <div className="input-group">
                <div className="input-group-title">
                  Или выберете из предложенного:
                </div>
                <div className="input-group-content">
                  <AmountSuggestion
                    active={ !isCustomAmount }
                    onSelect={ onAmountSelect }
                    suggestions={ amountSuggestions }
                    featuredAmount={ 50 }
                  />
                </div>
              </div>
            </div>
            <div className="balance-attention">
              Будет использован сторонний сервис, цены и комиссии которого мы <span className="danger-text">не регулируем</span>
            </div>
            <div className="balance-sum-block">
              <div className="balance-block-row sum-to-pay">
                <span>
                  Итого
                </span>
                <span className="balance-block-row-num">
                  $ { isCustomAmount ? inputAmountValue : amount }
                </span>
              </div>
              <div className="balance-block-row local">
                <span>
                  RUB
                </span>
                <span className="balance-block-row-num">
                  ₽ { isCustomAmount ? Math.round(inputAmountValue * 75) : Math.round(amount * 75) }
                </span>
              </div>
            </div>
            <div
              className={
                classNames(
                  'input-group',
                  'confirmation-input-group',
                  (!isDeposit && 'show')
                )
              }
            >
              <div className="input-group-title">
                Подтверждение
              </div>
              <ConfirmationInput
                value={ confirmationCode }
                onChange={ handleConfirmationCode }
                errors={ confirmationErrors }
              />
            </div>
            <div className="balance-page-row actions">
              <Button
                size="medium"
                color="primary"
                onClick={ isDeposit ? onDeposit : onWithdraw }
                disabled={ isDeposit ? !(!!service && !!country && !!inputAmountValue && !!serviceNumber) : !(!!service && !!country && !!inputAmountValue && !!serviceNumber && !!confirmationCode) }
              >
                { isDeposit ? 'Депозит' : 'Вывод' }
              </Button>
            </div>
          </div>
        </div>
      </Tablet>
      <Laptop>
        <div className="balance-page laptop page-spacing">
          <div className="balance-page-inner">
            <div className="balance-block action-part">
              <div className="balance-block">
                <div className="balance-row content-between title">
                  <div className={
                    classNames(
                      'balance-block-title',
                      isDeposit && 'active'
                    )
                  }>
                    <div
                      className="balance-btn deposit"
                      onClick={ () => setIsDeposit(true) }
                    >
                      Депозит
                    </div>
                    <div className="balance-divider"/>
                    <div
                      className="balance-btn withdraw"
                      onClick={ () => setIsDeposit(false) }
                    >
                      Вывод
                    </div>
                  </div>
                  <DropdownSelect
                    title={ 'Страна' }
                    selected={ country }
                    onChange={ handleCountryChange }
                    options={ countries }
                  />
                </div>
                <div className="balance-row">
                  <div className="balance-block payment-services">
                    <PaymentServices
                      services={ paymentServices }
                      onSelect={ onServiceSelect }
                    />
                  </div>
                </div>
                <div className="balance-row content-between">
                  <div className="balance-input balance-col">
                    <div className="balance-input-title">
                      Введите сумму
                    </div>
                    <AmountInput
                      value={ inputAmountValue }
                      onChange={ handleInputAmountChange }
                      onClick={ handleInputAmountClick }
                      prepend="$"
                      errors={ amountError }
                      rules={ amountInputRules }
                    />
                  </div>
                  <div className="balance-buttons balance-col">
                    <div className="balance-button-title">
                      Или выберете из предложенного:
                    </div>
                    <div className="balance-buttons-group">
                      <AmountSuggestion
                        active={ !isCustomAmount }
                        onSelect={ onAmountSelect }
                        suggestions={ amountSuggestions }
                        featuredAmount={ 50 }
                      />
                    </div>
                  </div>
                </div>
                <div className="balance-row">
                  <div className="balance-input balance-col">
                    <div className="balance-input-title">
                      Ваш кошелек:
                    </div>
                    <ServiceNumber
                      selectedService={ service?.value }
                      value={ serviceNumber }
                      onChange={ handleInputPhoneNumber }
                      errors={ '' }
                      country={ country }
                    />
                  </div>
                  <div className="balance-input balance-col balance-attention">
                    <div className="button-outline">
                      <div className="icon">
                        <WarningIcon/>
                      </div>
                      <div>
                        Будет использован сторонний сервис, цены и комиссии которого мы <span className="attention-text">не регулируем</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="balance-block">
                <div className="balance-row bottom-row">
                  <div className="balance-col sum">
                    <div className="title">Итого:</div>
                    <div className="amount"><span className="currency">$</span> { isCustomAmount ? inputAmountValue : amount }</div>
                  </div>
                  <div className="balance-col confirmation">
                    <div
                      className={
                        classNames(
                          'balance-input',
                          'balance-col',
                          (!isDeposit && 'show')
                        )
                      }
                    >
                      <div className="balance-input-title">
                        <div>
                          Подтверждение:
                        </div>
                        <div className="icon">
                          ?
                        </div>
                      </div>
                      <ConfirmationInput
                        value={ confirmationCode }
                        onChange={ handleConfirmationCode }
                        errors={ confirmationErrors }
                      />
                    </div>
                    <Button
                      size="large"
                      color="primary"
                      onClick={ isDeposit ? onDeposit : onWithdraw }
                      disabled={ isDeposit ? !(!!service && !!country && !!inputAmountValue && !!serviceNumber) : !(!!service && !!country && !!inputAmountValue && !!serviceNumber && !!confirmationCode) }
                    >
                      { isDeposit ? 'Депозит' : 'Вывод' }
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Laptop>
      <NonLaptop>
        <div className="balance-page non-laptop page-spacing">
          <div className="balance-page-inner">
            <div className="balance-block payment-services">
              <PaymentServices onSelect={ onServiceSelect } services={ paymentServices }/>
            </div>
            <div className="balance-block action-part">
              <div className="balance-block">
                <div className="balance-row content-between title">
                  <div className={
                    classNames(
                      'balance-block-title',
                      isDeposit && 'active'
                    )
                  }
                  >
                    <div
                      className="balance-btn deposit"
                      onClick={ () => setIsDeposit(true) }
                    >
                      Депозит
                    </div>
                    <div className="balance-divider"/>
                    <div
                      className="balance-btn withdraw"
                      onClick={ () => setIsDeposit(false) }
                    >
                      Вывод
                    </div>
                  </div>
                  <div className="promo-code-button">
                    <DropdownSelect
                      title={ 'Страна' }
                      selected={ country }
                      onChange={ handleCountryChange }
                      options={ countries }
                    />
                  </div>
                </div>
                <div className="balance-row content-between">
                  <div className="balance-input balance-col">
                    <div className="balance-input-title">
                      Введите сумму
                    </div>
                    <AmountInput
                      value={ inputAmountValue }
                      onChange={ handleInputAmountChange }
                      onClick={ handleInputAmountClick }
                      prepend="$"
                      errors={ amountError }
                      rules={ amountInputRules }
                    />
                  </div>
                  <div className="balance-buttons balance-col">
                    <div className="balance-button-title">
                      Или выберете из предложенного:
                    </div>
                    <div className="balance-buttons-group">
                      <AmountSuggestion
                        active={ !isCustomAmount }
                        onSelect={ onAmountSelect }
                        suggestions={ amountSuggestions }
                        featuredAmount={ 50 }
                      />
                    </div>
                  </div>
                </div>
                <div className="balance-row">
                  <div className="balance-input balance-col">
                    <div className="balance-input-title">
                      Ваш кошелек:
                    </div>
                    <ServiceNumber
                      selectedService={ service?.value }
                      value={ serviceNumber }
                      onChange={ handleInputPhoneNumber }
                      errors={ '' }
                      country={ country }
                    />
                  </div>
                  <div className="balance-input balance-col balance-attention">
                    <div className="button-outline">
                      <div className="icon">
                        <WarningIcon/>
                      </div>
                      <div>
                        Будет использован сторонний сервис, цены и комиссии которого мы <span className="attention-text">не регулируем</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="balance-block">
                <div className="balance-row bottom-row mb-0">
                  <div className="sum">
                    <div className="title">Итого:</div>
                    <div className="amount"><span className="currency">$</span> { isCustomAmount ? inputAmountValue : amount }</div>
                  </div>
                  <div className="confirmation">
                    <div className={
                      classNames(
                        'balance-input',
                        'balance-col',
                        'confirmation-input-group',
                        (!isDeposit && 'show')
                      )
                    }
                    >
                      <div className="balance-input-title">
                        <div>
                          Подтверждение:
                        </div>
                        <div className="icon">
                          ?
                        </div>
                      </div>
                      <ConfirmationInput
                        value={ confirmationCode }
                        onChange={ handleConfirmationCode }
                        errors={ confirmationErrors }
                      />
                    </div>
                  </div>
                  <Button
                    size="large"
                    color="primary"
                    onClick={ isDeposit ? onDeposit : onWithdraw }
                    disabled={ isDeposit ? !(!!service && !!country && !!inputAmountValue && !!serviceNumber) : !(!!service && !!country && !!inputAmountValue && !!serviceNumber && !!confirmationCode) }
                  >
                    { isDeposit ? 'Депозит' : 'Вывод' }
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </NonLaptop>
    </>
  )
}