import React from 'react'
import './PaymentService.scss'
import { PaymentServiceInterface } from './PaymentService.interface'
import classNames from 'classnames'

export const PaymentService = ({classes, paymentId, onClick, icon, title}: PaymentServiceInterface) => {
  return (
    <div
      className={
        classNames(
          'payment-service',
          classes
        )
      }
      onClick={onClick}
      data-payment-id={paymentId}
    >
      <div className="payment-icon">
        { icon }
      </div>
      <div className="payment-name">
        { title }
      </div>
    </div>
  )
}