const handleFocus = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlur = (e: any) => e.target.parentNode.classList.remove('input-focus')

interface ProfileInputInterface2 {
  btnType?: 'save' | 'copy' | 'save-copy'
  placeholder?: string
  onFocus: any
  onBlur: any
  onChange?: any
  onSave?: any
  onCopy?: any
}

const ProfileInput2 = ({ btnType, placeholder, onFocus, onBlur, onChange, onSave, onCopy }: ProfileInputInterface2) => {
  
  return (
    <div
      className="input"
      onFocus={ onFocus }
      onBlur={ onBlur }
    >
      <input
        type="text"
        placeholder={ placeholder }
        onChange={ (e) => onChange(e.target.value) }
      />
      {
        btnType && <div className="icons">
          {
            btnType === 'save-copy'
              ?
              <>
                <div
                  className="input-btn"
                  onClick={ onSave }
                >
                  <SaveIcon/>
                </div>
                <div
                  className="input-btn"
                  onClick={ onCopy }
                >
                  <CopyIcon/>
                </div>
              </>
              : btnType === 'save'
              ? <div className="input-btn"><SaveIcon/></div>
              : <div className="input-btn"><CopyIcon/></div>
          }
        </div>
      }
    </div>
  )
}

interface TradeLinkInputInterface {
  placeholder?: string
  onFocus: any
  onBlur: any
  onSave?: any
  val: string
  onChange: any
}

const TradeLinkInput = ({ val, onChange, placeholder, onFocus, onBlur, onSave }: TradeLinkInputInterface) => {
  const [tradeLink, setTradeLink] = useState<string>('')
  const [errors, setErrors] = useState<boolean>(false)
  
  const pattern = new RegExp(/steamcommunity\.com\/tradeoffer\/new\/\?partner=(\d+)&token=(.{8})$/ig)
  
  const handleInput = (e: { target: { value: any } }) => {
    const { value } = e.target
    
    onChange(value)
    
    if (pattern.test(value)) {
      setErrors(false)
      setTradeLink(value)
    } else {
      setErrors(true)
      setTradeLink('')
    }
  }
  
  useEffect(() => {
    const tradeLinkFromLocalStorage = localStorage.getItem('tradeLink')
    tradeLinkFromLocalStorage && setTradeLink(tradeLinkFromLocalStorage)
    tradeLinkFromLocalStorage && onChange(tradeLinkFromLocalStorage)
  }, [])
  
  return (
    <div
      className={
        classNames(
          'input',
          errors && 'input-error'
        )
      }
      onFocus={ onFocus }
      onBlur={ onBlur }
    >
      <input
        type="text"
        placeholder={ placeholder }
        onChange={ handleInput }
        value={ val }
        onKeyDown={ (event) => event.key === 'Enter' && onSave(tradeLink) }
      />
      <div className="icons">
        <div
          className="input-btn"
          onClick={ () => onSave(tradeLink) }
        >
          <SaveIcon/>
        </div>
      </div>
    </div>
  )
}

