import classNames from 'classnames'
import React, { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react'
import { createPortal } from 'react-dom'
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close.svg'
import { Button } from '../../../../components/Controls/Controls'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import './SubscriptionModal.scss'

interface iSubscriptionModal {
  defaultOpened?: boolean
  title: string
  description: string
  Icon?: any
  handleSubscribe?: any
}

export const SubscriptionModal = (
  {
    handleSubscribe,
    defaultOpened = false,
    title,
    description,
    Icon
  }: iSubscriptionModal,
  ref: any
) => {
  const modalElement: any = document.getElementById('modal-root')
  
  const modalBlockRef = useRef<any>(null)
  
  const [isOpen, setOpen] = useState(defaultOpened)
  
  const close = useCallback(() => setOpen(false), [])
  
  const handleClickOutside = () => close()
  
  const handleEscape = useCallback((event) => {
    if (event.keyCode === 27) close()
  }, [close])
  
  useDetectOutsideClick(modalBlockRef, handleClickOutside)
  
  useImperativeHandle(ref, () => ({
    open: () => setOpen(true),
    close
  }), [close])
  
  useEffect(() => {
    if (isOpen) document.addEventListener('keydown', handleEscape, false)
    
    return (
      document.removeEventListener('keydown', handleEscape, false)
    )
  }, [handleEscape, isOpen])
  
  return createPortal(
    isOpen ? (
      <div
        className={
          classNames(
            'subscription-modal-wrapper',
            isOpen && 'active'
          )
        }
      >
        <div
          className={
            classNames(
              'modal-block'
            )
          }
          ref={ modalBlockRef }
        >
          <div
            className="close-button"
            onClick={ close }
          >
            <CloseIcon/>
          </div>
          <div className="modal-body">
            <div className="subscription-icon">
              { Icon }
            </div>
            <div className="subscription-title">
              { title }
            </div>
            <div className="subscription-description">
              { description }
            </div>
            <div className="modal-buttons">
              <Button
                size="normal"
                color="secondary"
                onClick={ close }
              >
                Отмена
              </Button>
              <Button
                size="normal"
                color="primary"
                onClick={ () => {
                  handleSubscribe()
                  close()
                } }
              >
                Подписаться
              </Button>
            </div>
          </div>
        </div>
      </div>
    ) : null,
    modalElement
  )
}

export default forwardRef(SubscriptionModal)