import React from 'react'
import { useTranslation } from 'react-i18next'
import { ReactComponent as ArrowIcon } from '../../../assets/icons/arrow.svg'
import { ReactComponent as MarketIcon } from '../../../assets/icons/home.svg'
import { ReactComponent as SearchIcon } from '../../../assets/icons/search.svg'
import { GameSelector } from '../../../newComponents/GameSelector/GameSelector'
import { Button, Input } from '../../Controls/Controls'
import { AdvancedSearch } from './AdvancedSearch/AdvancedSearch'
import './FilterBar.scss'

export const FilterBar = () => {
  const { t } = useTranslation()
  
  return (
    <div className="filter-bar content-between">
      <div className="container-left">
        <div className="filter-title">
          { t('header.filter.title') } :
        </div>
        <GameSelector/>
        <Button classes="button market-button" size="medium" color="secondary" href="/market">
          <div className="icon">
            <MarketIcon/>
          </div>
          <span>{ t('header.filter.market-button') }</span>
          <div className="arrow">
            <ArrowIcon/>
          </div>
        </Button>
        <div className="divider m50"/>
        <Input
          id="filter-bar-search-input"
          classes="filter-bar-search"
          size="medium"
          showBorder={ false }
        >
          <Input.Prepend>
            <SearchIcon/>
          </Input.Prepend>
          <Input.Field
            type="text"
            placeholder={ t('header.filter.search-placeholder') }
          />
        </Input>
      </div>
      <div className="container-right">
        <div className="divider m50"/>
        <AdvancedSearch/>
      </div>
    </div>
  )
}