import React from 'react'
import classNames from 'classnames'
import NumberFormat from 'react-number-format'

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

interface iAmountInput {
  rules: any
  prepend: string
  value: number
  onChange: any
  onClick: any
  errors: any
}

export const AmountInput = ({ rules, value, onChange, onClick, prepend, errors }: iAmountInput) => {
  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'amount-input'
        )
      }
      onClick={ onClick }
    >
      <div className="amount-input--prepend">
        { prepend }
      </div>
      <NumberFormat
        className="amount-input--input"
        isAllowed={ rules }
        isNumericString={ true }
        allowNegative={ false }
        allowLeadingZeros={ false }
        decimalScale={ 2 }
        fixedDecimalScale={ true }
        allowEmptyFormatting={ false }
        thousandSeparator=" "
        value={ value }
        onValueChange={ (number) => onChange(number.value) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}
