import classNames from 'classnames'
import React, { createContext, useState } from 'react'
import { BrowserRouter as Router, NavLink } from 'react-router-dom'
import './App.scss'
import { ReactComponent as ArrowIcon } from './assets/icons/arrow.svg'
import { useAuth } from './newHooks/useAuth'
import { useRoutes } from './routes'

const DevNavigation = () => {
  const [isOpen, setIsOpen] = useState<boolean>(false)
  
  return (
    <div className="dev-menu">
      <div
        className={
          classNames(
            'dev-menu-toggle',
            isOpen && 'active'
          )
        }
        onClick={ () => setIsOpen(!isOpen) }
      >
        <ArrowIcon/>
      </div>
      <div
        className={
          classNames(
            'dev-menu-links',
            isOpen && 'active'
          )
        }
      >
        <NavLink activeClassName="active" className="done" to="/faq">1. FAQ</NavLink>
        <NavLink to="/auth" className="done">2.Авторизация</NavLink>
        <NavLink to="/balance" className="done">3. Баланс</NavLink>
        <NavLink to="/home" className="done">4. Главная</NavLink>
        <NavLink to="/inventory" className="done">5. Инвентарь</NavLink>
        <NavLink to="/cart" className="done">6. Корзина</NavLink>
        <NavLink to="/market" className="done">7. Маркет</NavLink>
        <NavLink to="/orders/1" className="done">8. Ордера</NavLink>
        <NavLink to="/support" className="done">9. Поддержка</NavLink>
        <NavLink to="/item" className="done">10. Предмет</NavLink>
        <NavLink to="/seller" className="done">11. Продавец</NavLink>
        <NavLink to="/profile/main" className="done">12. Профиль</NavLink>
        <NavLink to="/ticket" className="done">13. Тикет</NavLink>
        <NavLink to="/transactions" className="done">14. Транзакции</NavLink>
      </div>
    </div>
  )
}

export const AuthContext = createContext<any>({
  token: null,
  userId: null,
  login: () => {
  },
  logout: () => {
  },
  isAuthenticated: false
})

function App() {
  const { token, login, logout, userId, ready } = useAuth()
  const isAuthenticated = !!token
  const routes = useRoutes(isAuthenticated)
  
  if (!ready) {
    return <>
      Загружается ...
    </>
  }
  
  return (
    <>
      <AuthContext.Provider value={ {
        token, login, logout, userId, isAuthenticated
      } }>
        <Router>
          { routes }
          <DevNavigation/>
        </Router>
      </AuthContext.Provider>
    </>
  )
}

export default App
