import React, { useEffect, useRef, useState } from 'react'
import { ReactComponent as AddIcon } from '../../../../assets/icons/add.svg'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import classNames from 'classnames'
import './MultiDropdownSelect.scss'
import { Checkbox } from '../../../../newComponents/CheckboxWithLabel/CheckboxWithLabel'

interface iMultiDropdownSelect {
  options: any
  title: string
  onChange: any
  onReset: () => void
}

export const MultiDropdownSelect = ({ options, title, onChange, onReset }: iMultiDropdownSelect) => {
  const [count, setCount] = useState<number>(0)
  const dropdownRef = useRef<any>(null)
  const [isShow, setShow] = useState<boolean>(false)
  const handleClickOutside = () => setShow(false)
  useDetectOutsideClick(dropdownRef, handleClickOutside)
  
  const onSelect = (title: string, value: boolean) => {
    onChange(title, !value)
  }
  
  useEffect(() => {
    const length = options?.filter((item: { selected: boolean }) => item.selected).length
    setCount(length)
  }, [options])
  
  return (
    <div
      className="sidebar-filter-dropdown-menu"
      ref={ dropdownRef }
    >
      <div
        className="dropdown-menu--head"
        onClick={
          (e: any) => {
            if (e.target.classList.contains('dropdown-menu--reset')) {
              e.preventDefault()
              onReset()
            } else {
              setShow(!isShow)
            }
          }
        }
      >
        <div className="dropdown-menu--title">
          { title }
          <span>({ count })</span>
        </div>
        <div className="dropdown-menu--controls">
          { count !== 0 &&
          <div
            className="dropdown-menu--reset">
            <AddIcon/>
          </div>
          }
          <div className={
            classNames(
              'dropdown-menu--arrow',
              isShow && 'active'
            )
          }
          />
        </div>
      </div>
      {
        isShow && <div className="dropdown-menu--options">
          {
            options.length > 0 && options.map((option: { title: string, selected: boolean }) => {
              const { title, selected } = option
              
              return (
                <Checkbox
                  className={
                    classNames(
                      'dropdown-menu--option',
                      selected && 'selected'
                    )
                  }
                  key={ `dropdown-item-${ option }` }
                  checked={ selected }
                  name={ title }
                  onChange={ () => onSelect(title, selected) }
                  title={ title }
                />
              )
            })
          }
        </div>
      }
    </div>
  )
}
