import React, { useState } from 'react'
import { SwitchWithLabel } from '../../../../newComponents/SwitchWithLabel/SwitchWithLabel'
import { SidebarFilterContainer } from '../SidebarFilterContainer/SidebarFilterContainer'
import './OtherFilter.scss'

interface iOtherFilter {
  isCollapsed: boolean
  onCollapse: any
  onReset: any
  onSwitch: any
  data: any
}

export const OtherFilter = ({ isCollapsed, onCollapse, onReset, data, onSwitch }: iOtherFilter) => {

  return (
    <SidebarFilterContainer
      title="Другое"
      className="other-filter"
      isCollapsed={ isCollapsed }
      onCollapse={ onCollapse }
      onReset={ onReset }
    >
      <div className="switches">
        {
          Object.entries(data).map((obj, i) => {
            const title = obj[0]
            const value = !!obj[1]

            return (
              <SwitchWithLabel
                name={ title }
                checked={ value }
                title={ title }
                onChange={ () => {
                  onSwitch(title, !value)
                } }
              />
            )
          })
        }
      </div>
    </SidebarFilterContainer>
  )
}