import React from 'react'

interface iCheckbox {
  className?: string
  title: string
  name: string
  checked: boolean
  indeterminate?: boolean
  disabled?: boolean
  onChange: any
}

export const Checkbox = ({ className, title, name, checked, indeterminate = false, disabled = false, onChange }: iCheckbox) => {
  return (
    <label className={ className }>
      <input
        name={ name }
        className="checkbox"
        type="checkbox"
        ref={ input => {
          if (input) {
            input.checked = checked
            input.indeterminate = indeterminate as boolean
            input.disabled = disabled as boolean
          }
        } }
        onChange={ onChange }
      />
      <span>{ title }</span>
    </label>
  )
}