import React from 'react'
import './SalesTable.scss'

interface iSalesTable {
  data: any[]
}

export const SalesTable = ({ data }: iSalesTable) => {
  
  return (
    <div className="item-sales-table">
      {
        data
          ? (
            <>
              <div className="item-sales-table-head">
                <div className="item-sales-table-row">
                  <div className="item-sales-table-cell">
                    Дата
                  </div>
                  <div className="item-sales-table-cell">
                    Флоат
                  </div>
                  <div className="item-sales-table-cell">
                    Стоимость
                  </div>
                </div>
              </div>
              <div className="item-sales-table-body">
                {
                  data.map((row) => {
                    const { date, float, price } = row
                    
                    return (
                      <div className="item-sales-table-row">
                        <div className="item-sales-table-cell">
                          { date }
                        </div>
                        <div className="item-sales-table-cell">
                          { float }
                        </div>
                        <div className="item-sales-table-cell">
                          { price }
                        </div>
                      </div>
                    )
                  })
                }
              </div>
            </>
          ) :
          (
            <div>У этого предмета пока нет продаж</div>
          )
      }
    </div>
  )
}