import classNames from 'classnames'
import React from 'react'
import { NavLink } from 'react-router-dom'
import { ButtonInterface } from './Button.interface'
import './Button.scss'

export const Button = ({classes, href, btnType, disabled, size, color, onClick, square, children, ...restProps}: ButtonInterface) => {
  if (href) {
    return (
      <NavLink
        to={ href }
        className={
          classNames(
            'btn',
            size ? `btn-${ size ? size : '' }` : '',
            color ? `btn-${ color ? color : '' }` : '',
            square ? `btn-${ square ? 'square' : '' }` : '',
            classes
          )
        }
        onClick={ onClick }
      >
        { children }
      </NavLink>
    )
  }
  
  return (
    <button
      className={
        classNames(
          'btn',
          size ? `btn-${ size ? size : '' }` : '',
          color ? `btn-${ color ? color : '' }` : '',
          square ? `btn-${ square ? 'square' : '' }` : '',
          classes
        )
      }
      onClick={ onClick }
      type={ btnType }
      disabled={ disabled }
    >
      { children }
    </button>
  )
}