const useDimensions = () => {
  const ref = useRef<any>(null)
  const [dimensions, setDimensions] = useState<any>({})
  
  useLayoutEffect(() => {
    setDimensions({ height: ref.current.clientHeight, width: ref.current.clientWidth })
  }, [])
  
  return [ref, dimensions]
}

