import React from 'react'
import { PhoneNumberInput } from '../PhoneNumberInput/PhoneNumberInput'
import './ServiceNumber.scss'

interface iServiceNumber {
  selectedService: string
  value: string
  onChange: any
  errors: any
  country: any
}

export const ServiceNumber = ({ selectedService, value, onChange, errors, country }: iServiceNumber) => {
  switch (selectedService) {
    case 'qiwi':
      return (
        <PhoneNumberInput
          value={ value }
          onChange={ onChange }
          errors={ errors }
          country={ country.value.toLowerCase() }
          onlyCountries={ ['ru', 'ua', 'kz', 'by'] }
        />
      )
    case 'card':
      return (
        <div className="phone-number-input">
          <input
            placeholder="Введите номер вашей карты"
            type="text"
            value={ value }
            onChange={ (e) => onChange(e.target.value) }
          />
        </div>
      )
    default:
      return (
        <div className="phone-number-input">
          <input
            placeholder="Введите счет"
            type="text"
            value={ value }
            onChange={ (e) => onChange(e.target.value) }
          />
        </div>
      )
  }
}