import classNames from 'classnames'
import React from 'react'
import { NavLink, useHistory } from 'react-router-dom'
import { Button } from '../../components/Controls/Controls'
import './ErrorPage.scss'
import awpLeft from './items/awp_left.png'
import awpRight from './items/awp_right.png'
import { Desktop, DesktopLarge, FromLargeDesktop, FullScreen, Laptop, Mobile, Tablet } from '../../components/Responsive/Responsive'

interface iErrorPage {
  type?: string
}

export const ErrorPage = ({ type }: iErrorPage) => {
  const history = useHistory()
  
  switch (type) {
    case 'not-found':
      return (
        <>
          <Mobile>
            <div
              className={
                classNames(
                  'error-page',
                  'mobile',
                  'not-found'
                )
              }
            >
              
              <div className="error">
                <div className="title">
                  Ошибка
                </div>
                <div className="status-code">
                  404
                </div>
                <div className="error-description">
                  Мы не нашли запрашиваемую вами страницу
                </div>
                <div className="buttons">
                  <NavLink to="/home">
                    <Button size="small" color="primary">
                      На главную
                    </Button>
                  </NavLink>
                  <NavLink to="#">
                    <Button size="small" color="secondary" onClick={ () => history.goBack() }>
                      Вернуться обратно
                    </Button>
                  </NavLink>
                </div>
              </div>
            </div>
          </Mobile>
          <Tablet>
            <div
              className={
                classNames(
                  'error-page',
                  'tablet',
                  'not-found'
                )
              }
            >
              <div className="left-image">
                <img src={ awpLeft } alt=""/>
              </div>
              <div className="error">
                <div className="title">
                  Ошибка
                </div>
                <div className="status-code">
                  404
                </div>
                <div className="error-description">
                  Мы не нашли запрашиваемую вами страницу
                </div>
                <div className="buttons">
                  <NavLink to="/home">
                    <Button size="normal" color="primary">
                      На главную
                    </Button>
                  </NavLink>
                  <NavLink to="#">
                    <Button size="normal" color="secondary" onClick={ () => history.goBack() }>
                      Вернуться обратно
                    </Button>
                  </NavLink>
                </div>
              </div>
              <div className="right-image">
                <img src={ awpRight } alt=""/>
              </div>
            </div>
          </Tablet>
          <Laptop>
            <div
              className={
                classNames(
                  'error-page',
                  'laptop',
                  'not-found'
                )
              }
            >
              <div className="left-image">
                <img src={ awpLeft } alt=""/>
              </div>
              <div className="error">
                <div className="title">
                  Ошибка
                </div>
                <div className="status-code">
                  404
                </div>
                <div className="error-description">
                  Мы не нашли запрашиваемую вами страницу
                </div>
                <div className="buttons">
                  <NavLink to="/home">
                    <Button size="normal" color="primary">
                      На главную
                    </Button>
                  </NavLink>
                  <NavLink to="#">
                    <Button size="normal" color="secondary" onClick={ () => history.goBack() }>
                      Вернуться обратно
                    </Button>
                  </NavLink>
                </div>
              </div>
              <div className="right-image">
                <img src={ awpRight } alt=""/>
              </div>
            </div>
          </Laptop>
          <Desktop>
            <div
              className={
                classNames(
                  'error-page',
                  'desktop',
                  'not-found'
                )
              }
            >
              <div className="left-image">
                <img src={ awpLeft } alt=""/>
              </div>
              <div className="error">
                <div className="title">
                  Ошибка
                </div>
                <div className="status-code">
                  404
                </div>
                <div className="error-description">
                  Мы не нашли запрашиваемую вами страницу
                </div>
                <div className="buttons">
                  <NavLink to="/home">
                    <Button size="normal" color="primary">
                      На главную
                    </Button>
                  </NavLink>
                  <NavLink to="#">
                    <Button size="normal" color="secondary" onClick={ () => history.goBack() }>
                      Вернуться обратно
                    </Button>
                  </NavLink>
                </div>
              </div>
              <div className="right-image">
                <img src={ awpRight } alt=""/>
              </div>
            </div>
          </Desktop>
          <FromLargeDesktop>
            <div
              className={
                classNames(
                  'error-page',
                  'from-large-desktop',
                  'not-found'
                )
              }
            >
              <div className="left-image">
                <img src={ awpLeft } alt=""/>
              </div>
              <div className="error">
                <div className="title">
                  Ошибка
                </div>
                <div className="status-code">
                  404
                </div>
                <div className="error-description">
                  Мы не нашли запрашиваемую вами страницу
                </div>
                <div className="buttons">
                  <NavLink to="/home">
                    <Button size="normal" color="primary">
                      На главную
                    </Button>
                  </NavLink>
                  <NavLink to="#">
                    <Button size="normal" color="secondary" onClick={ () => history.goBack() }>
                      Вернуться обратно
                    </Button>
                  </NavLink>
                </div>
              </div>
              <div className="right-image">
                <img src={ awpRight } alt=""/>
              </div>
            </div>
          </FromLargeDesktop>
        </>
      )
    default:
      return (
        <div
          className={
            classNames(
              'error-page',
              'default'
            )
          }
        >
          Ошибка!
        </div>
      )
  }
}