import classNames from 'classnames'
import React, { useState } from 'react'
import { ReactComponent as SaveIcon } from '../../../../assets/icons/save.svg'
import NumberFormat from 'react-number-format'
import './ItemSellByEditOnClick.scss'

interface iItemSellByEditOnClick {
  wrapperClass?: string
  inputClass?: string
  textClass?: string
  initialValue: number
  startEditing?: () => void
  endEditing?: (value: number) => void
}

export const ItemSellByEditOnClick = ({ initialValue, startEditing, endEditing }: iItemSellByEditOnClick) => {
  const [value, setValue] = useState<number>(initialValue)
  const [isEditMode, setEditMode] = useState<boolean>(false)
  
  const getIntoEditMode = () => {
    if (startEditing && !isEditMode) {
      startEditing()
    }
    setEditMode(true)
  }
  
  const getOffEditMode = () => {
    setEditMode(false)
    if (endEditing) {
      endEditing(value)
    }
  }
  
  const handleChange = (e: any) => {
    setValue(e.floatValue)
  }
  
  const handleKeyPress = (e: { keyCode: number; charCode: number }) => {
    if (e.keyCode === 13 || e.charCode === 13 || e.keyCode === 27 || e.charCode === 27) {
      getOffEditMode()
    }
  }
  
  return (
    <div className={ classNames('item-sell-by-edit-on-click', 'price') }>
      {
        isEditMode ? (
          <>
            <span className="currency">$</span>
            <NumberFormat
              className={
                classNames(
                  'item-sell-by-edit-on-click--input'
                )
              }
              inputmode="numeric"
              autoFocus={ true }
              allowNegative={ false }
              allowEmptyFormatting={ true }
              decimalScale={ 2 }
              step={ 0.25 }
              thousandSeparator=" "
              value={ value }
              onValueChange={ handleChange }
              onBlur={ getOffEditMode }
              onKeyPress={ handleKeyPress }
            />
            <span className="edit-btn">
              <SaveIcon/>
            </span>
          </>
        ) : (
          <>
            <span
              className="edit-btn sell-item"
              onClick={ getIntoEditMode }
            >
              Продать
            </span>
          </>
        )
      }
    </div>
  )
}
