import classNames from 'classnames'
import React, { useContext } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { useHistory } from 'react-router-dom'
import { toast } from 'react-toastify'
import { AuthContext } from '../../App'
import { ReactComponent as SteamIcon } from '../../assets/icons/socials/steam.svg'
import { Button } from '../../components/Controls/Controls'
import { Mobile, NonMobile } from '../../components/Responsive/Responsive'
import './AuthPage.scss'
import axios from 'axios'

interface FormValues {
  email: string
  password: string
}

export const AuthPage = () => {
  const auth = useContext(AuthContext)
  
  const { register, handleSubmit, errors, formState } = useForm({
    mode: 'onChange',
    reValidateMode: 'onChange',
    defaultValues: {
      email: '',
      password: ''
    },
    criteriaMode: 'all',
    shouldFocusError: true,
    shouldUnregister: true
  })
  
  const history = useHistory()
  
  const handleSteamLogin = () => {
    toast.success('Вход через Steam', { autoClose: 10000 })
  }
  
  const onSubmit: SubmitHandler<FormValues> = async (data) => {
    if (errors.email || errors.password) toast.error('Заполните форму', { autoClose: 10000 })
    
    const req = await axios.post('/auth/login', data)
    // auth.login(req.token, req.userId)
    
    // добавить routes с флагом isAuthenticated, закрыть доступ
    
    // if (!errors.email && !errors.password) {
    //   const userDataFromLocalStorage = localStorage.getItem('user')
    //   const userData = userDataFromLocalStorage && JSON.parse(userDataFromLocalStorage)
    //
    //   if (userData) {
    //     if (userData.email !== data.email || userData.password !== data.password) {
    //       toast.error('Данные неверны', { autoClose: 10000 })
    //     } else {
    //       toast.success('Вход успешно выполнен', { autoClose: 10000 })
    //       setTimeout(() => history.push('/home'), 1500)
    //     }
    //   } else {
    //
    //     toast.success('Аккаунт успешно создан', { autoClose: 10000 })
    //     localStorage.setItem('user', JSON.stringify({ email: data.email, password: data.password }))
    //     setTimeout(() => history.go(0), 2000)
    //   }
    // }
  }
  
  const handleFocus = (e: any) => e.target.classList.add('input-focus')
  const handleBlur = (e: any) => e.target.classList.remove('input-focus')
  
  return (
    <>
      <Mobile>
        <div className="auth-page mobile">
          <div className="auth-page-inner">
            <div className="auth-page-form">
              <div className="auth-form-head">
                <div className="auth-form-head-title">
                  Авторизация
                </div>
                <div className="auth-form-head-description">
                  Для регистрации нового аккаунта просто войдите через сервис <span>Steam</span>
                </div>
              </div>
              <div className="auth-form-body">
                <Button size="normal" classes="steam-button" color="success">
                  <span>Войти через Steam</span>
                </Button>
                <div className="auth-form-body-divider">или</div>
                <form onSubmit={ handleSubmit(onSubmit) }>
                  <div className="auth-form-body-inputs">
                    <input
                      type="email"
                      name="email"
                      ref={ register({
                        required: true,
                      }) }
                      className={
                        classNames(
                          'input',
                          'input-normal',
                          errors.email && 'input-error'
                        )
                      }
                      placeholder="E-mail"
                      onFocus={ handleFocus }
                      onBlur={ handleBlur }
                    />
                    <input
                      type="password"
                      name="password"
                      ref={ register({
                        required: true,
                        minLength: 8,
                        maxLength: 24,
                      }) }
                      className={
                        classNames(
                          'input',
                          'input-normal',
                          errors.password && 'input-error'
                        )
                      }
                      placeholder="Пароль"
                      onFocus={ handleFocus }
                      onBlur={ handleBlur }
                    />
                  </div>
                  <Button
                    size="normal"
                    color="primary"
                    btnType="submit"
                    disabled={ !formState.isValid }
                    classes={
                      classNames(
                        'auth-form-button',
                        !formState.isValid && 'disabled'
                      )
                    }
                  >
                    Войти
                  </Button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </Mobile>
      <NonMobile>
        <div className="auth-page non-mobile page-spacing">
          <div className="auth-form">
            <div className="auth-form-head">
              <div className="auth-form-head-title">
                Авторизация
              </div>
              <div className="auth-form-head-description">
                Для регистрации нового аккаунта просто войдите через сервис <span>Steam</span>
              </div>
            </div>
            <div className="auth-form-body">
              <Button
                size="normal"
                classes="steam-button"
                color="success"
                onClick={ handleSteamLogin }
              >
                <span>Войти через</span>
                <SteamIcon/>
              </Button>
              <div className="auth-form-body-divider">или</div>
              <form onSubmit={ handleSubmit(onSubmit) }>
                <div className="auth-form-body-inputs">
                  <input
                    type="email"
                    name="email"
                    ref={ register({
                      required: true,
                    }) }
                    className={
                      classNames(
                        'input',
                        'input-normal',
                        errors.email && 'input-error'
                      )
                    }
                    placeholder="E-mail"
                    onFocus={ handleFocus }
                    onBlur={ handleBlur }
                  />
                  <input
                    type="password"
                    name="password"
                    ref={ register({
                      required: true,
                      minLength: 8,
                      maxLength: 24,
                    }) }
                    className={
                      classNames(
                        'input',
                        'input-normal',
                        errors.password && 'input-error'
                      )
                    }
                    placeholder="Пароль"
                    onFocus={ handleFocus }
                    onBlur={ handleBlur }
                  />
                </div>
                <Button
                  size="normal"
                  color="primary"
                  btnType="submit"
                  disabled={ !formState.isValid }
                  classes={
                    classNames(
                      'auth-form-button',
                      !formState.isValid && 'disabled'
                    )
                  }
                >
                  Войти
                </Button>
              </form>
            </div>
          </div>
        </div>
      </NonMobile>
    </>
  )
}