import React, { useState } from 'react'
import { PaymentService } from './PaymentService/PaymentService'
import { PaymentServicesInterface } from './PaymentServices.interface'
import './PaymentServices.scss'

export const PaymentServices = ({services, onSelect}: PaymentServicesInterface) => {
  const [activeIndex, setActiveIndex] = useState<number>(-1)
  
  
  const onClick = (index: number) => {
    const selectedService = services[activeIndex]
    setActiveIndex(index)
    
    if (services[index]?.value === selectedService?.value) {
      setActiveIndex(-1)
    } else {
      onSelect(services[index])
    }
  }
  
  return (
    <div className="payment-services">
      {
        services.map((service: { title: string; icon: React.ReactNode; id: string }, index: number) => (
          <PaymentService
            title={ service.title }
            icon={ service.icon }
            classes={ activeIndex === index ? 'active' : '' }
            onClick={ () => onClick(index) }
            key={ index }
            paymentId={ service.id }
          />
        ))
      }
    </div>
  )
}