import classNames from 'classnames'
import React from 'react'
import { ReactComponent as ClockIcon } from '../../../../assets/icons/clock.svg'
import { ReactComponent as LifebuoyIcon } from '../../../../assets/icons/lifebuoy.svg'
import { ReactComponent as LockIcon } from '../../../../assets/icons/lock.svg'
import { ReactComponent as StarFilledIcon } from '../../../../assets/icons/star-filled.svg'
import { ReactComponent as StarIcon } from '../../../../assets/icons/star.svg'
import { getTimePassedSince } from '../../../../utilities/getTimePassedSince'
import './Ticket.scss'

interface iTicket {
  id: number
  status: string
  isFavorite: boolean
  openChat: any
  title: string
  lastMessage: any
  createTime: number
  toggleFavorite: any
}

export const Ticket = ({ toggleFavorite, id, title, status, lastMessage, isFavorite, openChat, createTime }: iTicket) => {
  
  const timePassed = getTimePassedSince(createTime)
  const words = timePassed.unit === 'days' ? ['день', 'дня', 'дней'] : timePassed.unit === 'hours' ? ['час', 'часа', 'часов'] : ['минута', 'минуты', 'минут']
  
  return (
    <div
      className="ticket"
      onClick={ (e: any) => {
        if (e.target.classList.contains('favorite-icon')) {
          e.preventDefault()
        } else {
          openChat(id)
        }
      } }
    >
      <div
        className={
          classNames(
            'icon',
            status === 'customer' ? 'icon-customer' : status === 'closed' ? 'icon-closed' : status === 'support' && 'icon-support'
          )
        }
      >
        {
          status === 'customer' ? <ClockIcon/> : status === 'closed' ? <LockIcon/> : status === 'support' &&
            <LifebuoyIcon/>
        }
      </div>
      <div className="ticket-info">
        <div className="ticket-title">{ title }</div>
        <div className="ticket-description">
          { lastMessage }
        </div>
        <div className="ticket-id">
          Номер тикета: <span className="attention-text">{ id }</span>
        </div>
      </div>
      <div className="additional">
        <div className="time">
          <span>
            { timePassed.duration > 0 && timePassed.duration }
          </span>
          <span>
            {
              timePassed.unit === 'future'
                ? 'Error time'
                : timePassed.unit === 'minutes'
                  ? 'мин.'
                  : timePassed.unit === 'hours'
                    ? 'ч.'
                    : 'дн.'
            }
          </span>
          { timePassed.duration > 0 && 'назад' }
        </div>
        <div
          className={
            classNames(
              'favorite-icon',
              isFavorite ? 'favorite-active' : 'favorite-inactive'
            )
          }
          onClick={ () => toggleFavorite(id) }
        >
          { isFavorite ? <StarFilledIcon/> : <StarIcon/> }
        </div>
      </div>
    </div>
  )
}