import classNames from 'classnames'
import React, { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react'
import { createPortal } from 'react-dom'
import { FileDrop } from 'react-file-drop'
import { toast } from 'react-toastify'
import { useDetectOutsideClick } from '../../../hooks/useDetectOutsideClick'
import { ReactComponent as CloseIcon } from '../../../assets/icons/close.svg'
import { ReactComponent as FileIcon } from '../../../assets/icons/file.svg'
import { Button } from '../../Controls/Controls'
import { ModalInterface } from '../Modal.interface'
import './FileModalUpload.scss'

const modalElement: any = document.getElementById('modal-root')

export const FileModalUpload = ({children, fade = false, defaultOpened = false, onUpload}: ModalInterface, ref: any) => {
  const [isOpen, setIsOpen] = useState(defaultOpened)
  
  const close = useCallback(() => setIsOpen(false), [])
  
  useImperativeHandle(ref, () => ({
    open: () => setIsOpen(true),
    close
  }), [close])
  
  const handleEscape = useCallback((event) => {
    if (event.keyCode === 27) close()
  }, [close])
  
  useEffect(() => {
    if (isOpen) document.addEventListener('keydown', handleEscape, false)
    
    return (
      document.removeEventListener('keydown', handleEscape, false)
    )
  }, [handleEscape, isOpen])
  
  const modalBlockRef = useRef<any>(null)
  
  const handleClickOutside = () => {
    close()
  }
  
  useDetectOutsideClick(modalBlockRef, handleClickOutside)
  
  const hiddenFileInput = useRef<any>(null)
  const handleUploadClick = () => hiddenFileInput.current.click()
  const showError = (err: string) => {
    switch (err) {
      case 'length':
        toast.error('Максимальное количество файлов - 3', {autoClose: 10000})
        break
      case 'size':
        toast.error('Максимальный размер файла - 5 МБ', {autoClose: 10000})
        break
      case 'type':
        toast.error('Поддерживаемые расширения файлов - png, jpeg, jpg, pdf', {autoClose: 10000})
        break
    }
  }

  const handleUploadChange = (event: any) => {
    const {files} = event.target
    const filesLength = files.length
    const supportedFileTypes = ['image/png', 'application/pdf', 'image/jpeg', 'image/jpg']
    
    if (filesLength <= 3) {
      files && Object.keys(files).map((file, index) => {
        
        // 5 MB
        if (files[file].size <= 5242880) {
          const isSupported = !!supportedFileTypes.filter((fileType) => fileType === files[file].type).length
          
          if (isSupported) {
            onUpload(event)
            close()
          } else {
            showError('type')
          }
          
        } else {
          showError('size')
        }
      })
    } else {
      showError('length')
    }
  }
  
  const [isOver, setIsOver] = useState<boolean>(false)
  
  return createPortal(
    isOpen ? (
      <div
        className={
          classNames(
            'modal-wrapper',
            isOpen && 'active'
          )
        }
      >
        <div
          className={
            classNames(
              'modal-block',
              isOver && 'modal-block--hover'
            )
          }
          ref={ modalBlockRef }
        >
          <FileDrop
            onDrop={ (files, event) => {
              handleUploadChange(files)
              setIsOver(false)
            } }
            onDragOver={ (event) => {
              setIsOver(true)
            } }
            onDragLeave={ (event) => {
              setIsOver(false)
            } }
          >
            <div className="close-button" onClick={ close }>
              <CloseIcon/>
            </div>
            {
              isOver
                ? <div className="modal-body">
                  <div className="icon">
                    <FileIcon/>
                  </div>
                  <div className="text">
                    <div className="main-text">
                      Отпустите чтобы загрузить
                    </div>
                  </div>
                  <input
                    id="file"
                    type="file"
                    ref={ hiddenFileInput }
                    onChange={ handleUploadChange }
                    multiple
                  />
                </div>
                : <div className="modal-body">
                  <div className="icon">
                    <FileIcon/>
                  </div>
                  <div className="text">
                    <div className="main-text">Перетащите файл сюда</div>
                    <div className="sub-text">или</div>
                  </div>
                  <Button
                    size="normal"
                    color="gray-light-outline"
                    classes="button-open-file"
                    onClick={ handleUploadClick }
                  >
                    Открыть
                  </Button>
                  <input
                    id="file"
                    type="file"
                    ref={ hiddenFileInput }
                    onChange={ handleUploadChange }
                    accept=".pdf, .png, .jpg, .jpeg"
                    multiple
                  />
                  <div className="max-file-size">
                    Максимальный размер файла <span className="attention-text">5 МБ</span>
                  </div>
                  <div className="max-files">
                    Максимальное количество файлов <span className="attention-text">3</span>
                  </div>
                  <div className="supported-file-types">
                    Поддерживаемые форматы <span className="attention-text">png, pdf, jpeg, jpg</span>
                  </div>
                </div>
            }
          </FileDrop>
        </div>
      </div>
    ) : null,
    modalElement
  )
}

export default forwardRef(FileModalUpload)