import React, { useContext, useState } from 'react'
import './Input.scss'

import { InputAppendInterface, InputFieldInterface, InputInterface, InputPrependInterface } from './Input.interface'
import { InputContext } from '../../../context/InputContext'
import classNames from 'classnames'

const useInputContext = () => {
  const context = useContext(InputContext)
  
  if (!context) throw new Error('InputContext used outside Input component')
  
  return context
}

export const Input = ({id, classes, children, showBorder = true, ref, onClick, onChange, size, color, disabled, ...restProps}: InputInterface) => {
  const [focus, setFocus] = useState(false)
  
  return (
    <InputContext.Provider value={ {focus, setFocus} }>
      <div
        className={
          classNames(
            'input',
            size ? `input-${ size ? size : '' }` : '',
            color ? `input-${ color ? color : '' }` : '',
            showBorder ? (focus ? 'input-focus' : '') : '',
            classes
          )
        }
      >
        { children }
      </div>
    </InputContext.Provider>
  )
}

const InputPrepend = ({children}: InputPrependInterface) => {
  return (
    <div className="input-prepend">
      { children }
    </div>
  )
}

const InputAppend = ({children}: InputAppendInterface) => {
  return (
    <div className="input-append">
      { children }
    </div>
  )
}

const InputField = ({classes, type = 'text', ref, name, placeholder, onChange, style}: InputFieldInterface) => {
  const {focus, setFocus} = useInputContext()

  return (
    <input
      className={ classes }
      placeholder={ placeholder }
      onFocus={ () => setFocus(true) }
      onBlur={ () => setFocus(false) }
      onChange={ onChange }
      type={ type }
      name={ name }
      style={ style }
      ref={ ref }
    />
  )
}

Input.Prepend = InputPrepend
Input.Append = InputAppend
Input.Field = InputField