import React, { ReactNode, ReactNodeArray } from 'react'
import './FAQBlockQuestion.scss'

interface iFAQBlockQuestion {
  question: any
  answer: any
  id: string
}

export const FAQBlockQuestion = ({ question, answer, id }: iFAQBlockQuestion) => {
  return (
    <div
      className="faq-block-question"
      id={ `question-${id}` }
    >
      <div className="question">
        { question }
      </div>
      <div className="answer">
        { answer }
      </div>
    </div>
  )
}
