import classNames from 'classnames'
import React from 'react'
import { ReactComponent as ClockIcon } from '../../../../assets/icons/clock.svg'
import { ReactComponent as EyeIcon } from '../../../../assets/icons/eye.svg'
import { ReactComponent as LockIcon } from '../../../../assets/icons/lock.svg'
import { ReactComponent as SteamIcon } from '../../../../assets/icons/socials/steam.svg'
import { ReactComponent as TrashIcon } from '../../../../assets/icons/trash.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { Floatbar } from '../../../../components/Floatbar/Floatbar'
import { ItemSticker } from '../../../../newComponents/ItemSticker/ItemSticker'
import { ReactComponent as TagIcon } from '../../../../assets/icons/tag.svg'
import './MobileCard.scss'

interface iCartItem {
  id: number | string
  image: string
  visual: [
    {
      icon: string
      title: string
      wear: number
    }
  ]
  mainName: string
  additionalName?: string
  secondaryName: string
  price: number
  steamPrice: number
  tag?: string
  float: number
  exterior: string
  lock: number
  inspectLink: string
  onRemove: (id: number | string) => void
}

export const MobileCard = (
  {
    id,
    image,
    visual,
    mainName,
    secondaryName,
    price,
    steamPrice,
    tag,
    float,
    exterior,
    lock,
    inspectLink,
    onRemove
  }: iCartItem) => {
  const discount = Math.round(((price / steamPrice) - 1) * 100)
  
  return (
    <div className="cart-item mobile">
      <div className="cart-item--stickers">
        {
          visual && Array.from({ length: visual.length }, (_, i) => {
              return (
                <ItemSticker
                  sticker={ visual[i].icon }
                  stickerTitle={ visual[i].title }
                  stickerWear={ visual[i].wear }
                />
              )
            }
          )
        }
      </div>
      <div className="cart-item--info">
        <div className="cart-item--main-name">
          { mainName } | { exterior }
        </div>
        <div className="cart-item--secondary-name">
          { secondaryName }
        </div>
      </div>
      <div className="cart-item--image">
        <img
          src={ image }
          alt={ `${ mainName } ${ secondaryName }` }
        />
      </div>
      <div className="cart-item--additional">
        <div
          className={
            classNames(
              'cart-item--lock-status',
              lock === 0 ? 'unlocked' : 'locked'
            )
          }
        >
          {
            lock === 0
              ? <LockIcon/>
              : <ClockIcon/>
          }
          {
            lock !== 0 && (
              <span>{ lock } дн.</span>
            )
          }
        </div>
        {
          tag && (
            <div className="cart-item--tag">
              <TagIcon/>
            </div>
          )
        }
        <div className="cart-item--float">
          { Math.round(float * 10000) / 10000 }
        </div>
      </div>
      <div className="cart-item--price cart-item--remove">
        <div className="cart-item--our-price">
          ${ price } <span className="cart-item--discount">{ discount }%</span>
        </div>
        <Button
          classes="cart-item--remove-item"
          size="small"
          color="secondary"
          square={ true }
          onClick={ () => onRemove(id) }
        >
          <TrashIcon/>
        </Button>
      </div>
    </div>
  )
}