import classNames from 'classnames'
import React, { useState } from 'react'
import NumberFormat from 'react-number-format'
import { NavLink } from 'react-router-dom'
import { ReactComponent as PlusIcon } from '../../../../assets/icons/add_square.svg'
import { ReactComponent as CartIcon } from '../../../../assets/icons/cart.svg'
import { ReactComponent as ClockIcon } from '../../../../assets/icons/clock.svg'
import { ReactComponent as LockIcon } from '../../../../assets/icons/open-lock.svg'
import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg'
import { ReactComponent as TagIcon } from '../../../../assets/icons/tag-item.svg'
import { Button } from '../../../../components/Controls/Controls'
import { Floatbar } from '../../../../components/Floatbar/Floatbar'
import { ItemSticker } from '../../../../newComponents/ItemSticker/ItemSticker'
import { useCart } from '../../../../newHooks/useCart'
import { useOrder } from '../../../../newHooks/useOrder'
import { declensionOfNumber } from '../../../../utilities/declensionOfNumber'
import { getUnlockTime } from '../../../../utilities/getUnlockTime'
import './ItemContainer.scss'

interface iItemContainer {
  size: string
  id: number
  name: string
  item: string
  link: string
  tag: string
  lock: number
  float?: number
  exterior?: string
  price: number
  steamPrice: number
  image: string
  stickers?: any[]
  className?: string
}

interface iPriceInput {
  prepend: string
  value: number
  onChange: any
  errors: any
}

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

const CountInput = ({ prepend, value, onChange, errors }: iPriceInput) => {
  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'count-input-group'
        )
      }
    >
      <div className="count-input-prepend">
        { prepend }
      </div>
      <NumberFormat
        className="count-input"
        isNumericString={ true }
        allowNegative={ false }
        allowLeadingZeros={ false }
        decimalScale={ 0 }
        fixedDecimalScale={ true }
        allowEmptyFormatting={ false }
        thousandSeparator=" "
        prefix={ '' }
        value={ value }
        onValueChange={ (number: { value: any }) => onChange(number.value) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}

const PriceInput = ({ prepend, value, onChange, errors }: iPriceInput) => {
  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'price-input-group'
        )
      }
    >
      <div className="price-input-prepend">
        { prepend }
      </div>
      <NumberFormat
        className="price-input"
        isNumericString={ true }
        allowNegative={ false }
        allowLeadingZeros={ false }
        decimalScale={ 2 }
        fixedDecimalScale={ true }
        allowEmptyFormatting={ false }
        thousandSeparator=" "
        prefix={ '$ ' }
        value={ value }
        onValueChange={ (number: { value: any }) => onChange(number.value) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}

