import React, { useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { NavLink } from 'react-router-dom'
import { ReactComponent as BellIcon } from '../../../assets/icons/bell.svg'
import { ReactComponent as CartIcon } from '../../../assets/icons/cart.svg'
import { ReactComponent as DepositIcon } from '../../../assets/icons/deposit.svg'
import { ReactComponent as InventoryIcon } from '../../../assets/icons/inventory.svg'
import { ReactComponent as ItemsOnSaleIcon } from '../../../assets/icons/items-on-sale.svg'
import { ReactComponent as LogoutIcon } from '../../../assets/icons/logout.svg'
import { ReactComponent as OrdersIcon } from '../../../assets/icons/orders.svg'
import { ReactComponent as SecurityIcon } from '../../../assets/icons/security.svg'
import { ReactComponent as SupportIcon } from '../../../assets/icons/support.svg'
import { ReactComponent as TransactionsIcon } from '../../../assets/icons/transactions.svg'
import { ReactComponent as UserIcon } from '../../../assets/icons/user.svg'
import { ReactComponent as WithdrawIcon } from '../../../assets/icons/withdraw.svg'
import { useBalance } from '../../../newHooks/useBalance'
import { useCart } from '../../../newHooks/useCart'
import { Button } from '../../Controls/Controls'
import { Dropdown } from '../../Controls/Dropdown/Dropdown'
import { LanguageSelector } from './LanguageSelector/LanguageSelector'
import './NavigationBar.scss'
import { observer } from 'mobx-react-lite'

interface NavigationBar {
  authenticated: boolean | null
}

export const NavigationBar = observer(
  ({ authenticated }: NavigationBar) => {
    const { t } = useTranslation()
    const dropdownRef = useRef(null)
    const [isActive, setIsActive] = useState(false)
    const onClick = () => setIsActive(!isActive)
    
    const { balance } = useBalance()
    const userBalance = balance.amount
    
    // @ts-ignore
    const { itemsCount, changeItemsCount } = useCart()
    
    const randomCount = Math.floor(Math.random() * 99) + 1
    
    return (
      <div className="navigation-bar content-between">
        <div className="container-left">
          <NavLink className="logo" to="/home">SKINS<span>MART</span></NavLink>
          <LanguageSelector/>
          <div className="sections d-flex align-center">
            {
              authenticated
                ? <NavLink className="navigation-section support" to="/support">
                  <SupportIcon className="navigation-section-icon"/>
                  <span>{ t('header.navigation.support') }</span>
                </NavLink>
                : null
            }
            {
              authenticated
                ? <NavLink className="navigation-section inventory" to="/inventory">
                  <InventoryIcon className="navigation-section-icon"/>
                  <span>{ t('header.navigation.inventory') }</span>
                  <span className="inventory-badge">14</span>
                </NavLink>
                : null
            }
          </div>
        </div>
        <div className="container-right">
          {
            authenticated ? <Button
              classes="notifications-button"
              size="medium"
              square={ true }
              color="secondary"
            >
              <BellIcon className="icon"/>
              <div className="notifications-badge"/>
            </Button> : null
          }
          <Button
            classes="sell-button"
            size="medium"
            color="outline"
            href="inventory"
          >
            { t('header.navigation.sell-button') }
          </Button>
          <div className="divider"/>
          <Button
            classes="cart-button"
            size="medium"
            color="secondary"
            href="cart"
            onClick={ () => changeItemsCount(randomCount) }
          >
            <CartIcon className="icon"/>
            <div className="cart-badge">{ itemsCount }</div>
          </Button>
          {
            authenticated ? <Button
              classes="balance-button"
              size="medium"
              color="secondary"
              href="balance"
            >
              <div className="currency">$</div>
              <div className="amount">{ userBalance.toFixed(2) }</div>
              <Button classes="deposit-button" size="small" square={ true }>
                +
              </Button>
            </Button> : null
          }
          <div className="divider"/>
          {
            authenticated
              ? <Dropdown relative={ true }>
                <Dropdown.Toggle classes="profile-button">
                  <img src="https://thispersondoesnotexist.com/image" alt="" className="profile-image"/>
                  <div className="profile-name">John Doe</div>
                  <div className="arrow"/>
                </Dropdown.Toggle>
                <Dropdown.Menu classes="navigation-menu active">
                  <Dropdown.Item classes="nav-link">
                    <UserIcon className="icon"/>
                    <NavLink to="/profile">{ t('header.navigation.menu.profile') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Item classes="nav-link">
                    <OrdersIcon className="icon"/>
                    <NavLink to="/orders">{ t('header.navigation.menu.my-orders') }</NavLink>
                    <span className="inventory-badge">8</span>
                  </Dropdown.Item>
                  <Dropdown.Item classes="nav-link">
                    <InventoryIcon className="icon"/>
                    <NavLink to="/inventory">{ t('header.navigation.menu.my-items') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Divider/>
                  <Dropdown.Item classes="nav-link">
                    <WithdrawIcon className="icon"/>
                    <NavLink to="/balance">{ t('header.navigation.menu.withdraw-money') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Item classes="nav-link">
                    <DepositIcon className="icon"/>
                    <NavLink to="/balance">{ t('header.navigation.menu.deposit-money') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Divider/>
                  <Dropdown.Item classes="nav-link">
                    <ItemsOnSaleIcon className="icon"/>
                    <NavLink to="on-sale">{ t('header.navigation.menu.items-on-sale') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Item classes="nav-link">
                    <TransactionsIcon className="icon"/>
                    <NavLink to="/transactions">{ t('header.navigation.menu.transactions') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Item classes="nav-link">
                    <SecurityIcon className="icon"/>
                    <NavLink to="/security">{ t('header.navigation.menu.security') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Item classes="nav-link support-icon">
                    <SupportIcon className="icon"/>
                    <NavLink to="/support">{ t('header.navigation.support') }</NavLink>
                  </Dropdown.Item>
                  <Dropdown.Item>
                    <Button size="medium" color="primary" classes="logout-button">
                      <LogoutIcon className="icon"/>
                      <NavLink to="/logout">{ t('header.navigation.menu.logout') }</NavLink>
                    </Button>
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
              : <NavLink to="/auth"><Button size="medium" color="primary">Войти / Зарегистрироваться</Button></NavLink>
          }
        </div>
      </div>
    )
  }
)