interface iClickToEdit {
  wrapperClass?: string
  inputClass?: string
  textClass?: string
  initialValue: string
  startEditing?: () => {}
  endEditing?: (value: string) => {}
}

const ClickToEdit = ({ wrapperClass, inputClass, textClass, initialValue, startEditing, endEditing }: iClickToEdit) => {
  const [value, setValue] = useState<string>(initialValue)
  const [isEditMode, setEditMode] = useState<boolean>(false)
  
  const getIntoEditMode = () => {
    if (startEditing && !isEditMode) {
      startEditing()
    }
    setEditMode(true)
  }
  
  const getOffEditMode = () => {
    setEditMode(false)
    if (endEditing) {
      endEditing(value)
    }
  }
  // TODO: type for "e"
  const handleChange = (e: any) => {
    setValue(e.target.value)
  }
  
  const handleKeyPress = (e: { keyCode: number; charCode: number }) => {
    if (e.keyCode === 13 || e.charCode === 13 || e.keyCode === 27 || e.charCode === 27) {
      getOffEditMode()
    }
  }
  
  return (
    <div className={ classNames('click-to-edit', wrapperClass) }>
      {
        isEditMode ? (
          <input
            type="text"
            autoFocus
            value={ value }
            className={ classNames('click-to-edit--input', inputClass) }
            onChange={ handleChange }
            onKeyPress={ handleKeyPress }
            onBlur={ getOffEditMode }
          />
        ) : (
          <span
            className={ classNames('click-to-edit--text', textClass) }
            onClick={ getIntoEditMode }
          >
            { value }
          </span>
        )
      }
    </div>
  )
}

interface iSelectDropdownWithSearch {
  title: string
  options: any
  startEditing?: () => {}
  endEditing?: (value: string) => {}
}

const SelectDropdownWithSearch = ({ title, options, startEditing, endEditing }: iSelectDropdownWithSearch) => {
  const [value, setValue] = useState<string>('')
  const [amount, setAmount] = useState<number>(0)
  const [isEditMode, setEditMode] = useState<boolean>(false)
  const [isShow, setShow] = useState<boolean>(false)
  
  const [availableOptions, setAvailableOptions] = useState<any>([])
  
  const getIntoEditMode = () => {
    if (startEditing && !isEditMode) {
      startEditing()
    }
    setEditMode(true)
  }
  
  const getOffEditMode = () => {
    setEditMode(false)
    if (endEditing) {
      endEditing(value)
    }
  }
  
  const handleChange = (e: any) => {
    setValue(e.target.value)
  }
  
  const selectItem = (item: any) => {
    const isSelected = availableOptions.filter((option: { value: string }) => option.value === item)[0].selected
    setAvailableOptions(options.map((option: any) => option.value === item ? { ...option, selected: !isSelected } : option))
  }
  
  useEffect(() => {
    setAvailableOptions(options.map((option: any) => {
      return { ...option, selected: false }
    }))
  }, [])
  
  return (
    <div className="select-dropdown-menu">
      <div
        className="select-dropdown--head"
        onClick={ () => {
          getIntoEditMode()
          setShow(true)
        } }
      >
        {
          isEditMode
            ? <input
              type="text"
              autoFocus
              value={ value }
              className={ classNames('select-dropdown--input') }
              onChange={ handleChange }
              onBlur={ getOffEditMode }
            />
            : <div
              className="select-dropdown--title"
            >
              <span>{ title } ({ amount })</span>
            </div>
        }
        <div className="select-dropdown--controls">
          {
            amount > 0 && <div className="select-dropdown--remove">X</div>
          }
          <span className="select-dropdown--arrow"/>
        </div>
      </div>
      { isShow && <div className="select-dropdown--items">
        {
          availableOptions.map((item: { title: string, value: string, selected?: boolean }, index: number) => {
            return (
              <div
                className={
                  classNames(
                    'select-dropdown--item',
                    item.selected && 'selected'
                  )
                }
                onClick={ () => selectItem(item.value) }
                key={ index }
              >
                { item.title }
              </div>
            )
          })
        }
      </div> }
    </div>
  )
}

interface iDropdownSelect {
  options: any
  title: string
  onChange: any
  selected: any
}

const DropdownSelect = ({ options, title, selected, onChange }: iDropdownSelect) => {
  const dropdownRef = useRef<any>(null)
  
  const [isShow, setShow] = useState<boolean>(false)
  
  const toggleOption = (item: any) => {
    onChange(item.value === selected?.value ? null : item)
  }
  
  const handleClickOutside = () => setShow(false)
  useDetectOutsideClick(dropdownRef, handleClickOutside)
  
  return (
    <div
      className="dropdown-menu"
      ref={ dropdownRef }
    >
      <div
        className="dropdown-menu--head"
        onClick={
          (e: any) => {
            if (e.target.classList.contains('dropdown-menu--reset')) {
              e.preventDefault()
              toggleOption('')
            } else {
              setShow(!isShow)
            }
          }
        }
      >
        <div className="dropdown-menu--title">
          { selected ? selected.title : title }
        </div>
        <div className="dropdown-menu--controls">
          { selected &&
          <div
            className="dropdown-menu--reset">
            <AddIcon/>
          </div>
          }
          <div className={
            classNames(
              'dropdown-menu--arrow',
              isShow && 'active'
            )
          }
          />
        </div>
      </div>
      {
        isShow && <div className="dropdown-menu--options">
          {
            options.map((option: { title: string, value: string }) => {
              const isSelected = selected?.value === option.value
              
              return (
                <div
                  className={
                    classNames(
                      'dropdown-menu--option',
                      isSelected && 'selected'
                    )
                  }
                  key={ option.value }
                  onClick={ () => toggleOption(option) }
                >
                  { option.title }
                </div>
              )
            })
          }
        </div>
      }
    </div>
  )
}

