import React from 'react'
import { ItemCard } from '../ItemCard/ItemCard'
import './ItemsList.scss'

type iItemsList = {
  size: 'mobile' | 'non-mobile'
  items: any
  buyHandler: any
  addToCartHandler: any
}

export const ItemsList = ({ size, items, buyHandler, addToCartHandler }: iItemsList) => {
  
  return (
    <div className="items">
      {
        items.length <= 0 && 'Предметов нет'
      }
      
      {
        items.length > 0 && items.map((item: any) => {
          const { id, shortExterior, inspectLink, icon, stickers, type, name, price, steamPrice, tag, float, lock } = item
          const discount = Math.round(((price / steamPrice) - 1) * 100)
          
          return (
            <ItemCard
              id={ id }
              size={ size }
              game="cs:go"
              shortExterior={ shortExterior }
              inspectLink={ inspectLink }
              image={ icon }
              stickers={ stickers }
              mainName={ type }
              secondaryName={ name }
              price={ price }
              steamPrice={ steamPrice }
              discount={ discount }
              tag={ tag }
              float={ float }
              lock={ lock }
              buyHandler={ buyHandler }
              cartHandler={ addToCartHandler }
            />
          )
        })
      }
    </div>
  )
}