import React from 'react'
import './MobileFooter.scss'
import { NavLink } from 'react-router-dom'
import ScrollContainer from 'react-indiana-drag-scroll'
import { ReactComponent as VKIcon } from '../../assets/icons/socials/vk.svg'
import { ReactComponent as FBIcon } from '../../assets/icons/socials/facebook.svg'
import { ReactComponent as IGIcon } from '../../assets/icons/socials/instagram.svg'
import { ReactComponent as TGIcon } from '../../assets/icons/socials/telegram.svg'
import { ReactComponent as TWIcon } from '../../assets/icons/socials/twitter.svg'
import { ReactComponent as DiscordIcon } from '../../assets/icons/socials/discord.svg'
import { ReactComponent as SteamIcon } from '../../assets/icons/socials/steam.svg'
import { ReactComponent as G2AIcon } from '../../assets/icons/payment/g2a.svg'
import { ReactComponent as MCIcon } from '../../assets/icons/payment/mastercard.svg'
import { ReactComponent as VisaIcon } from '../../assets/icons/payment/visa.svg'
import { ReactComponent as PPIcon } from '../../assets/icons/payment/paypal.svg'
import { ReactComponent as QiwiIcon } from '../../assets/icons/payment/qiwi.svg'
import { ReactComponent as YMIcon } from '../../assets/icons/payment/yd.svg'
import { ReactComponent as CryptoIcon } from '../../assets/icons/payment/crypto.svg'
import { ReactComponent as WMIcon } from '../../assets/icons/payment/webmoney.svg'

export const MobileFooter = () => {
  return (
    <div className="mobile-footer">
      <div className="footer-links">
        <NavLink to="/prices">
          Цены
        </NavLink>
        <NavLink to="/market">
          Маркет
        </NavLink>
        <NavLink to="/faq">
          FAQ
        </NavLink>
        <NavLink to="/api">
          API
        </NavLink>
        <NavLink to="/support">
          Поддержка
        </NavLink>
        <NavLink to="/contacts">
          Контакты
        </NavLink>
        <NavLink to="/tos">
          Условия использования
        </NavLink>
        <NavLink to="/privacy-policy">
          Политика конфиденциальности
        </NavLink>
      </div>
      <div className="footer-block">
        <div className="footer-block-header">
          Мы в социальных сетях
        </div>
        <ScrollContainer className="footer-block-content scroll-container">
          <a href="https://vk.com" target="_blank" className="social">
            <VKIcon/>
          </a>
          <a href="https://facebook.com" target="_blank" className="social">
            <FBIcon/>
          </a>
          <a href="https://instagram.com" target="_blank" className="social">
            <IGIcon/>
          </a>
          <a href="https://telegram.org" target="_blank" className="social">
            <TGIcon/>
          </a>
          <a href="https://twitter.com" target="_blank" className="social">
            <TWIcon/>
          </a>
          <a href="https://discord.com" target="_blank" className="social">
            <DiscordIcon/>
          </a>
          <a href="https://store.steampowered.com" target="_blank" className="social">
            <SteamIcon/>
          </a>
        </ScrollContainer>
      </div>
      <div className="footer-block">
        <div className="footer-block-header">
          Мы принимаем
        </div>
        <ScrollContainer className="footer-block-content scroll-container">
          <div className="payment">
            <G2AIcon/>
          </div>
          <div className="payment">
            <MCIcon/>
          </div>
          <div className="payment">
            <VisaIcon/>
          </div>
          <div className="payment">
            <PPIcon/>
          </div>
          <div className="payment">
            <QiwiIcon/>
          </div>
          <div className="payment">
            <YMIcon/>
          </div>
          <div className="payment">
            <CryptoIcon/>
          </div>
          <div className="payment">
            <WMIcon/>
          </div>
        </ScrollContainer>
      </div>
      <div className="footer-logo">
        <NavLink className="logo" to="/">SKINS<span>MART</span></NavLink>
        <span>© 2020. Skinsmart. All Rights Reserved</span>
      </div>
    </div>
  )
}