import React from 'react'
import './PriceInput.scss'
import classNames from 'classnames'
import NumberFormat from 'react-number-format'

export const PriceInput = ({ value, onChange, onFocus, onBlur }: any) => {
  return (
    <NumberFormat
      className={
        classNames(
          'orders-form-item-search-price'
        )
      }
      allowNegative={ false }
      allowEmptyFormatting={ true }
      decimalScale={ 2 }
      thousandSeparator=" "
      prefix={ '$ ' }
      inputmode="numeric"
      value={ value }
      onValueChange={ (number) => onChange(number) }
      onBlur={ onBlur }
      onFocus={ onFocus }
    />
  )
}