import axios from 'axios'

export const cartStore = () => {
  return {
    items: [] as any,
    itemsCount: 0,
    addItem(item: any) {
      this.items.push(item)
      
    },
    removeItem(id: number) {
      this.items.filter((item: { id: number }) => item.id !== id)
    },
    setItemsCount(count: number) {
      this.itemsCount = count
    }
  }
}