import classNames from 'classnames'
import React, { useState } from 'react'
import './ContentTabs.scss'

interface iRowTabs {
  items: any
  onSelect: any
  defaultState: any
  className: string
}

export const ContentTabs = ({ className, items, defaultState, onSelect }: iRowTabs) => {
  const [activeIndex, setActiveIndex] = useState(items.map((item: number) => item === defaultState).indexOf(true))
  
  const onClick = (index: number) => {
    setActiveIndex(index)
    onSelect(items[index])
  }
  
  return (
    <div
      className={
        classNames(
          'content-tabs',
          className
        )
      }
    >
      {
        items.map((item: number, index: number) => {
          return (
            <div
              className={
                classNames(
                  (activeIndex === index && 'active'),
                  'content-tab'
                )
              }
              key={ index }
              onClick={ () => onClick(index) }
            >
              { item }
            </div>
          )
        })
      }
    </div>
  )
}