import React, { useState } from 'react'
import './MobileHeader.scss'
import { ReactComponent as UserIcon } from '../../assets/icons/user.svg'
import { ReactComponent as BellIcon } from '../../assets/icons/bell.svg'
import { ReactComponent as CartIcon } from '../../assets/icons/cart.svg'
import { ReactComponent as SearchIcon } from '../../assets/icons/search.svg'
import { ReactComponent as CloseIcon } from '../../assets/icons/close.svg'
import { NavLink } from 'react-router-dom'
import { Button } from '../Controls/Controls'
import { LanguageSelector } from '../Header/NavigationBar/LanguageSelector/LanguageSelector'

export const MobileHeader = () => {
  const [isOpen, setIsOpen] = useState(false)
  const toggleSidebar = () => setIsOpen(!isOpen)
  
  return (
    <div className={ `mobile-header ${ isOpen ? 'active' : 'inactive' }` }>
      <div className="main-bar">
        <div className="sidebar-toggler" onClick={ toggleSidebar }>
          <div/>
          <div/>
          <div/>
        </div>
        <NavLink to="/" className="logo">
          S<span>M</span>
        </NavLink>
        <div className="navigation">
          <NavLink to="/user">
            <UserIcon/>
          </NavLink>
          <div className="notification-btn">
            <BellIcon/>
            <div className="notifications-badge"/>
          </div>
          <NavLink to="/cart">
            <CartIcon/>
          </NavLink>
          <div className="search-btn">
            <SearchIcon/>
          </div>
        </div>
      </div>
      <div className="sidebar">
        <div className="sidebar-header">
          <div className="game-filter-mobile">
            CS:GO
          </div>
          <div className="sidebar-header-close-btn" onClick={ toggleSidebar }>
            <CloseIcon/>
          </div>
        </div>
        <div className="sidebar-content">
          <div className="sidebar-links">
            <NavLink to="/support" className="link-row">
              Поддержка
            </NavLink>
            <NavLink to="/inventory" className="link-row">
              Инвентарь
            </NavLink>
            <NavLink to="/market" className="link-row">
              Маркет
            </NavLink>
            <Button size="normal" color="primary" classes="link-row">
              Продать предметы
            </Button>
            <LanguageSelector/>
          </div>
          <div className="sidebar-footer">
            <NavLink to="/market">Цены</NavLink>
            <NavLink to="/market">Маркет</NavLink>
            <NavLink to="/market">FAQ</NavLink>
            <NavLink to="/market">API</NavLink>
            <NavLink to="/market">Поддержка</NavLink>
            <NavLink to="/market">Контакты</NavLink>
            <NavLink to="/market">Условия использования</NavLink>
            <NavLink to="/market">Политика конфиденциальности</NavLink>
          </div>
        </div>
      </div>
    </div>
  )
}