import React from 'react'
import { NavLink } from 'react-router-dom'
import { ReactComponent as CartIcon } from '../../../../assets/icons/cart.svg'
import { ReactComponent as ClockIcon } from '../../../../assets/icons/clock.svg'
import { ReactComponent as EyeIcon } from '../../../../assets/icons/eye.svg'
import { ReactComponent as OpenLockIcon } from '../../../../assets/icons/open-lock.svg'
import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg'
import { ReactComponent as SteamIcon } from '../../../../assets/icons/socials/steam.svg'
import { ReactComponent as TagIcon } from '../../../../assets/icons/tag-item.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { Floatbar } from '../../../../components/Floatbar/Floatbar'
import { ItemSticker } from '../../../../newComponents/ItemSticker/ItemSticker'
import './ItemCard.scss'
import { useCart } from '../../../../newHooks/useCart'

type ItemCard = {
  id: number
  size: 'mobile' | 'non-mobile'
  game: string
  shortExterior?: string
  inspectLink?: string
  image: string
  stickers: {
    title: string
    wear: number
    icon: any
  }[]
  mainName: string
  secondaryName: string
  price: number
  steamPrice: number
  discount: number
  tag?: string
  float?: number
  lock: number
  buyHandler: any
  cartHandler: any
}

export const ItemCard = (
  {
    id,
    size,
    game,
    shortExterior,
    inspectLink,
    image,
    stickers,
    mainName,
    secondaryName,
    price,
    steamPrice,
    discount,
    tag,
    float,
    lock,
    buyHandler,
    cartHandler
  }: ItemCard) => {
  const beautifulFloat = float?.toFixed(3)
  const declOfNum = (number: number, words: string[]) => words[(number % 100 > 4 && number % 100 < 20) ? 2 : [2, 0, 1, 1, 1, 2][(number % 10 < 5) ? number % 10 : 5]]
  
  const { addItemToCart } = useCart()
  
  const addToCart = (item: any) => {
    addItemToCart(item)
    cartHandler()
  }
  
  switch (size) {
    case 'mobile':
      return (
        <div className="item-card mobile">
          <div className="item-top-container">
            <div className="item-game">
              { game }
            </div>
            <div className="item-exterior">
              { shortExterior }
            </div>
            <div className="item-links">
              <a href={ inspectLink } className="inspect-link">
                <EyeIcon/>
              </a>
              <NavLink to="/market/cs/" className="search-link">
                <SearchIcon/>
              </NavLink>
            </div>
          </div>
          <div className="item-visual">
            <div className="item-image">
              <img src={ image } alt=""/>
            </div>
            <div className="item-stickers">
              {
                stickers && stickers.map((sticker) => {
                  const { title, wear, icon } = sticker
                  
                  return (
                    <ItemSticker sticker={ icon } stickerTitle={ title } stickerWear={ wear }/>
                  )
                })
              }
            </div>
          </div>
          <div className="item-info-container">
            { tag &&
            <div className="item-tag">
              <div className="tag">
                { tag }
              </div>
              <div className="icon">
                <TagIcon/>
              </div>
            </div>
            }
            <div className="item-name-info">
              <div className="main-name">
                { mainName }
              </div>
              <div className="secondary-name">
                { secondaryName }
              </div>
            </div>
            <div className="item-price-info">
              <div className="price">
                <div className="currency">$</div>
                { price }
                <span>{ discount }%</span>
              </div>
              <div className="steam-price">
                <SteamIcon/>
                Цена в Steam: ${ steamPrice }
              </div>
            </div>
          </div>
          <div className="item-float-container">
            {
              float && (
                <>
                  <div className="float">{ beautifulFloat }</div>
                  <Floatbar fv={ float }/>
                </>
              )
            }
          </div>
          <div className="item-lock-status-container">
            <span>Блокировка:</span>
            {
              lock === 0 && (
                <div className="unlocked">
                  <OpenLockIcon/> Разблок.
                </div>
              )
            }
            {
              lock > 0 && (
                <div className="locked">
                  <ClockIcon/>
                  { lock } { declOfNum(lock, ['день', 'дня', 'дней']) }
                </div>
              )
            }
          </div>
          <div className="action-buttons">
            <Button
              size="normal"
              color="primary"
              classes="buy-btn"
              onClick={ buyHandler }
            >
              Купить
            </Button>
            <Button
              size="normal"
              color="dark-outline"
              classes="cart-btn"
              square={ true }
              onClick={ cartHandler }
            >
              <CartIcon/>
            </Button>
          </div>
        </div>
      )
    case 'non-mobile':
      return (
        <div className="item-card non-mobile">
          <div className="item-wrapper">
            <div className="item-top-container">
              <div className="item-game">
                { game }
              </div>
              <div className="item-exterior">
                { shortExterior }
              </div>
              <div className="item-links">
                <a href={ inspectLink } className="inspect-link">
                  <EyeIcon/>
                </a>
                <NavLink to="/market/cs/" className="search-link">
                  <SearchIcon/>
                </NavLink>
              </div>
            </div>
            <div className="item-visual">
              <div className="item-image">
                <img src={ image } alt=""/>
              </div>
              <div className="item-stickers">
                {
                  stickers && stickers.map((sticker) => {
                    const { title, wear, icon } = sticker
                    
                    return (
                      <ItemSticker sticker={ icon } stickerTitle={ title } stickerWear={ wear }/>
                    )
                  })
                }
              </div>
            </div>
            <div className="item-info-container">
              { tag &&
              <div className="item-tag">
                <div className="tag">
                  { tag }
                </div>
                <div className="icon">
                  <TagIcon/>
                </div>
              </div>
              }
              <div className="item-name-info">
                <div className="main-name">
                  { mainName }
                </div>
                <div className="secondary-name">
                  { secondaryName }
                </div>
              </div>
              <div className="item-price-info">
                <div className="price">
                  <div className="currency">$</div>
                  { price }
                  <span>{ discount }%</span>
                </div>
                <div className="steam-price">
                  <SteamIcon/>
                  Цена в Steam: ${ steamPrice }
                </div>
              </div>
            </div>
            <div className="item-float-container">
              {
                float && (
                  <>
                    <div className="float">{ beautifulFloat }</div>
                    <Floatbar fv={ float }/>
                  </>
                )
              }
            </div>
            <div className="item-lock-status-container">
              <span>Блокировка:</span>
              {
                lock === 0 && (
                  <div className="unlocked">
                    <OpenLockIcon/> Разблок.
                  </div>
                )
              }
              {
                lock > 0 && (
                  <div className="locked">
                    <ClockIcon/>
                    { lock } { declOfNum(lock, ['день', 'дня', 'дней']) }
                  </div>
                )
              }
            </div>
          </div>
          <div className="action-buttons">
            <Button
              size="normal"
              color="primary"
              classes="buy-btn"
              onClick={ buyHandler }
            >
              Купить
            </Button>
            <Button
              size="normal"
              color="dark-outline"
              classes="cart-btn"
              square={ true }
              onClick={ () => addToCart({
                id,
                game,
                shortExterior,
                inspectLink,
                image,
                stickers,
                mainName,
                secondaryName,
                price,
                steamPrice,
                discount,
                tag,
                float,
                lock
              }) }
            >
              <CartIcon/>
            </Button>
          </div>
        </div>
      )
    default:
      return (
        <div className="item-card non-mobile">
          <div className="item-wrapper">
            <div className="item-top-container">
              <div className="item-game">
                { game }
              </div>
              <div className="item-exterior">
                { shortExterior }
              </div>
              <div className="item-links">
                <a href={ inspectLink } className="inspect-link">
                  <EyeIcon/>
                </a>
                <NavLink to="/market/cs/" className="search-link">
                  <SearchIcon/>
                </NavLink>
              </div>
            </div>
            <div className="item-visual">
              <div className="item-image">
                <img src={ image } alt=""/>
              </div>
              <div className="item-stickers">
                {
                  stickers && stickers.map((sticker) => {
                    const { title, wear, icon } = sticker
                    
                    return (
                      <ItemSticker sticker={ icon } stickerTitle={ title } stickerWear={ wear }/>
                    )
                  })
                }
              </div>
            </div>
            <div className="item-info-container">
              { tag &&
              <div className="item-tag">
                <div className="tag">
                  { tag }
                </div>
                <div className="icon">
                  <TagIcon/>
                </div>
              </div>
              }
              <div className="item-name-info">
                <div className="main-name">
                  { mainName }
                </div>
                <div className="secondary-name">
                  { secondaryName }
                </div>
              </div>
              <div className="item-price-info">
                <div className="price">
                  <div className="currency">$</div>
                  { price }
                  <span>{ discount }%</span>
                </div>
                <div className="steam-price">
                  <SteamIcon/>
                  Цена в Steam: ${ steamPrice }
                </div>
              </div>
            </div>
            <div className="item-float-container">
              {
                float && (
                  <>
                    <div className="float">{ beautifulFloat }</div>
                    <Floatbar fv={ float }/>
                  </>
                )
              }
            </div>
            <div className="item-lock-status-container">
              <span>Блокировка:</span>
              {
                lock === 0 && (
                  <div className="unlocked">
                    <OpenLockIcon/> Разблок.
                  </div>
                )
              }
              {
                lock > 0 && (
                  <div className="locked">
                    <ClockIcon/>
                    { lock } { declOfNum(lock, ['день', 'дня', 'дней']) }
                  </div>
                )
              }
            </div>
          </div>
          <div className="action-buttons">
            <Button
              size="normal"
              color="primary"
              classes="buy-btn"
              onClick={ buyHandler }
            >
              Купить
            </Button>
            <Button
              size="normal"
              color="dark-outline"
              classes="cart-btn"
              square={ true }
              onClick={ cartHandler }
            >
              <CartIcon/>
            </Button>
          </div>
        </div>
      )
  }
}