import classNames from 'classnames'
import React, { useRef, useState } from 'react'
import './GameSelector.scss'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import { useGameIcon } from '../../../../newHooks/useGameIcon'

export const GameSelector = ({game, games, onGameChange}: any) => {
  const gameSelectorRef = useRef<any>(null)
  const [isOpened, setIsOpened] = useState<boolean>(false)
  const handleClickOutside = () => setIsOpened(false)
  useDetectOutsideClick(gameSelectorRef, handleClickOutside)
  
  return (
    <div
      className="orders-game-selector"
      ref={ gameSelectorRef }
    >
      <div
        className="orders-game-selector--selected-game"
        onClick={ () => setIsOpened(!isOpened) }
      >
        { useGameIcon(game) }
      </div>
      {
        isOpened && (
          <div className="orders-game-selector--games">
            {
              games.map((option: any) => {
                const isSelected = game === option
                
                return (
                  <div
                    className={
                      classNames(
                        'orders-game-selector--game',
                        isSelected && 'selected'
                      )
                    }
                    key={ option }
                    onClick={ () => {
                      onGameChange(option)
                      setIsOpened(false)
                    } }
                  >
                    {/* eslint-disable-next-line react-hooks/rules-of-hooks */}
                    { useGameIcon(option) }
                  </div>
                )
              })
            }
          </div>
        )
      }
    </div>
  )
}