import { nanoid } from 'nanoid'
import React from 'react'
import ReactTooltip from 'react-tooltip'
import { ReactComponent as StickerPlaceholder } from '../../assets/icons/placeholder/sticker.svg'

interface iItemSticker {
  sticker: string
  stickerTitle: string
  stickerWear: number
}

export const ItemSticker = ({ sticker, stickerTitle, stickerWear }: iItemSticker) => {
  const id = nanoid()
  
  return (
    <div
      className="item-sticker"
    >
      {
        sticker === undefined
          ? <StickerPlaceholder/>
          : <img
            src={ sticker }
            alt=""
            data-for={ `sticker-info-${ id }` }
            data-tip={ [stickerWear, stickerTitle] }
          />
      }
      <ReactTooltip
        id={ `sticker-info-${ id }` }
        effect="solid"
      >
        <div className="sticker-title">{ stickerTitle }</div>
        <div className="sticker-wear">Wear: { stickerWear }%</div>
      </ReactTooltip>
    </div>
  )
}