import React from 'react'
import './Floatbar.scss'
import { FloatbarInterface } from './Floatbar.interface'
import classNames from 'classnames'
import { ReactComponent as ArrowIcon } from '../../assets/icons/arrow.svg'

export const Floatbar = ({classes, fv}: FloatbarInterface) => {
  const floatValue = fv * 100

  return (
    <div
      className={
        classNames(
          'floatbar',
          classes
        )
      }
    >
      <div
        className="float-arrow"
        style={
          {
            left: `calc(${ floatValue }% - 3%)`
          }
        }
      >
        <ArrowIcon/>
      </div>
      <div className="fn"/>
      <div className="mw"/>
      <div className="ft"/>
      <div className="ww"/>
      <div className="bs"/>
    </div>
  )
}