import classNames from 'classnames'
import React, { ReactNode, useEffect, useRef, useState } from 'react'
import { ReactComponent as ArrowIcon } from '../../../../assets/icons/arrow.svg'
import { Button } from '../../../../components/Controls/Button/Button'
import { FAQBlockQuestion } from '../FAQBlockQuestion/FAQBlockQuestion'

interface iFAQBlock {
  title: string
  questions: any
}

export const FAQBlock = ({ title, questions }: iFAQBlock) => {
  const [isOpen, setOpen] = useState<boolean>(true)
  const rContent = useRef<any>(null)
  
  useEffect(() => {
    if (isOpen && rContent.current.scrollHeight === 0) {
      rContent.current.style.maxHeight = '5000px'
      rContent.current.style.transition = '250ms cubic-bezier(0, 0, 1, 1) max-height'
    } else if (isOpen) {
      rContent.current.style.maxHeight = `${ rContent.current.scrollHeight }px`
      rContent.current.style.transition = '250ms cubic-bezier(0, 0, 1, 1) all'
    } else if (!isOpen || rContent.current.scrollHeight === 0) {
      rContent.current.style.maxHeight = '0px'
      rContent.current.style.transition = '250ms cubic-bezier(0, 0, 1, 1) all'
    }
  }, [rContent, isOpen])
  
  const toggle = () => setOpen(!isOpen)
  
  return (
    <div
      className={
        classNames(
          'faq-block',
        )
      }
    >
      <div className="faq-block-title">
        <div>{ title }</div>
        <Button
          size="normal"
          color="secondary"
          square={ true }
          classes={
            classNames(
              'collapse-button',
              isOpen && 'open'
            )
          }
          onClick={ toggle }
        >
          <ArrowIcon/>
        </Button>
      </div>
      <div
        className='faq-block-body'
        ref={ rContent }
      >
        {
          questions.map((item: { questionId: any; question: any; answer: any }) => {
            const { questionId, question, answer } = item
            return (
              <FAQBlockQuestion
                id={ questionId }
                question={ question }
                answer={ answer }
              />
            )
          })
        }
      </div>
    </div>
  )
}