import { curveCardinal } from '@visx/curve'
import { LinearGradient } from '@visx/gradient'
import { AnimationTrajectory } from '@visx/react-spring/lib/types'
import { AnimatedAxis, AnimatedGrid, BarSeries, buildChartTheme, LineSeries, Tooltip, XYChart, } from '@visx/xychart'

import React, { useMemo, useState } from 'react'
import './ItemChart.scss'
import Moment from 'react-moment'
import moment from 'moment'

interface iItemChart {
  range: string
  data: any[]
  height: number
  nTicks?: number
  width?: number
}

type Day = {
  date: string
  price: number
  volume: number
}

export const ItemChart = ({ data, height, width, range, nTicks = 4 }: iItemChart) => {
  const [animationTrajectory, setAnimationTrajectory] = useState<AnimationTrajectory | undefined>('center')
  const [showGridRows, setShowGridRows] = useState<boolean>(true)
  const [showGridColumns, setShowGridColumns] = useState<boolean>(true)
  const [numTicks, setNumTicks] = useState<number>(nTicks)
  const [xAxisOrientation, setXAxisOrientation] = useState<'top' | 'bottom'>('bottom')
  const [yAxisOrientation, setyAxisOrientation] = useState<'left' | 'right'>('left')
  const [priceLabel, setPriceLabel] = useState<string>('Стоимость')
  const [stackOffset, setStackOffset] = useState<'wiggle' | 'expand' | 'diverging' | 'silhouette'>('wiggle')
  
  const getDate = (d: Day) => d.date
  const getPrice = (d: Day) => d.price
  const getVolume = (d: Day) => d.volume
  
  const accessors = useMemo(
    () => ({
      x: {
        price: getDate
      },
      y: {
        volume: getVolume,
        price: getPrice
      },
      date: getDate
    }),
    []
  )
  
  const theme = buildChartTheme({
    backgroundColor: '#272E3D',
    colors: ['#242B39', '#fff', '#fff'],
    gridColor: '#212835',
    gridColorDark: '#212835',
    tickLength: 1
  })
  
  return (
    <XYChart
      theme={ theme }
      height={ Math.min(648, height) }
      width={Math.min(280, width ? width : 500)}
      xScale={ { type: 'band', paddingInner: 0.4 } as const }
      yScale={ { type: 'linear' } as const }
    >
      <AnimatedGrid
        key={ `grid-${ animationTrajectory }` }
        rows={ showGridRows }
        columns={ showGridColumns }
        animationTrajectory={ animationTrajectory }
        numTicks={ numTicks }
      />
      <BarSeries
        dataKey="Volume"
        data={ data }
        xAccessor={ accessors.x['price'] }
        yAccessor={ accessors.y['volume'] }
      />
      <LinearGradient
        id={ 'chart-id' }
        from={ '#5191ed' }
        to={ '#a039fb' }
        vertical={ false }
      />
      <LineSeries
        stroke={ `url(#chart-id)` }
        strokeWidth={ range === 'week' ? 10 : range === 'month' ? 5 : range === '6 months' ? 3 : 2 }
        dataKey="Price"
        data={ data }
        xAccessor={ accessors.x['price'] }
        yAccessor={ accessors.y['price'] }
        curve={ curveCardinal }
      />
      <AnimatedAxis
        hideTicks={ true }
        hideAxisLine={ true }
        label={ priceLabel }
        key={ `price-axis-${ animationTrajectory }` }
        orientation={ yAxisOrientation }
        numTicks={ numTicks }
        animationTrajectory={ animationTrajectory }
        hideZero={ true }
        tickLabelProps={ () => ({ dx: 10 }) }
      />
      <AnimatedAxis
        hideTicks={ true }
        hideAxisLine={ true }
        key={ `time-axis-${ animationTrajectory }` }
        orientation={ xAxisOrientation }
        numTicks={ numTicks }
        animationTrajectory={ animationTrajectory }
      />
      <Tooltip<Day>
        showHorizontalCrosshair={ true }
        showVerticalCrosshair={ true }
        snapTooltipToDatumX
        snapTooltipToDatumY
        showSeriesGlyphs
        glyphStyle={ { stroke: 'white', fill: 'transparent', strokeWidth: 1 } }
        verticalCrosshairStyle={ { stroke: '#6d768150' } }
        horizontalCrosshairStyle={ { stroke: '#6d768150' } }
        renderTooltip={ ({ tooltipData }) => (
          <div className="item-chart-tooltip">
            <div className="date">
              {
                (tooltipData?.nearestDatum?.datum && moment(accessors.date(tooltipData?.nearestDatum?.datum)))?.format('MMMM DD, yyyy') || 'No Date'
              }
            </div>
            <div className="price">
              <span>Цена:</span> ${ tooltipData?.nearestDatum?.datum && (
              Math.round((accessors.y.price(tooltipData?.nearestDatum?.datum)) * 100) / 100
            ) }
            </div>
            <div className="volume">
              <span>Количество:</span> { tooltipData?.nearestDatum?.datum && (
              accessors.y.volume(tooltipData?.nearestDatum?.datum)
            ) }
            </div>
          </div>
        ) }
      />
    </XYChart>
  )
}