import React, { useState } from 'react'
import { ContentTabs } from '../../../../newComponents/ContentTabs/ContentTabs'
import { SalesTable } from '../SalesTable/SalesTable'
import './SalesContainer.scss'

interface iSalesContainer {
  title: string
  sales: any
}

export const SalesContainer = ({ title, sales }: iSalesContainer) => {
  const [tab, setTab] = useState<number>(20)
  const filteredSales = sales.slice(Math.max(sales.length - tab, 0)).sort().reverse()
  
  return (
    <div className="item-container item-sales">
      <div className="item-container-title">
        { title }
        <ContentTabs
          className="item-sales-tabs"
          items={ [20, 40, 60] }
          defaultState={ tab }
          onSelect={ (x: any) => setTab(x) }
        />
      </div>
      <SalesTable data={ filteredSales }/>
    </div>
  )
}