import React, { useRef, useState } from 'react'
import { ReactComponent as AddIcon } from '../../../../assets/icons/add.svg'
import { useDetectOutsideClick } from '../../../../hooks/useDetectOutsideClick'
import classNames from 'classnames'
import './DropdownSelect.scss'

interface iDropdownSelect {
  options: any
  title: string
  onChange: any
  selected: any
  className?: string | string[]
}

export const DropdownSelect = ({ options, title, selected, onChange, className }: iDropdownSelect) => {
  const dropdownRef = useRef<any>(null)
  
  const [isShow, setShow] = useState<boolean>(false)
  
  const toggleOption = (item: any) => {
    onChange(item.value === selected?.value ? null : item)
  }
  
  const handleClickOutside = () => setShow(false)
  useDetectOutsideClick(dropdownRef, handleClickOutside)
  
  return (
    <div
      className={
        classNames(
          'dropdown-menu',
          className
        )
      }
      ref={ dropdownRef }
    >
      <div
        className="dropdown-menu--head"
        onClick={
          (e: any) => {
            if (e.target.classList.contains('dropdown-menu--reset')) {
              e.preventDefault()
              toggleOption('')
            } else {
              setShow(!isShow)
            }
          }
        }
      >
        <div className="dropdown-menu--title">
          { selected ? selected.title : title }
        </div>
        <div className="dropdown-menu--controls">
          { selected &&
          <div
            className="dropdown-menu--reset">
            <AddIcon/>
          </div>
          }
          <div className={
            classNames(
              'dropdown-menu--arrow',
              isShow && 'active'
            )
          }
          />
        </div>
      </div>
      {
        isShow && <div className="dropdown-menu--options">
          {
            options.map((option: { title: string, value: string }) => {
              const isSelected = selected?.value === option.value
              
              return (
                <div
                  className={
                    classNames(
                      'dropdown-menu--option',
                      isSelected && 'selected'
                    )
                  }
                  key={ option.value }
                  onClick={ () => toggleOption(option) }
                >
                  { option.title }
                </div>
              )
            })
          }
        </div>
      }
    </div>
  )
}
