type iSidebarFilter = {
  isOpen: boolean
  toggleSidebar: any
  type: 'small' | 'normal'
}

interface iFilters {
  searchVal: string
  price: {
    collapsed: boolean
    min: number
    max: number
  }
  exterior: {
    collapsed: boolean
    min: number
    max: number
    checkboxes: {
      fn: true | false | 'indeterminate'
      mw: true | false | 'indeterminate'
      ft: true | false | 'indeterminate'
      ww: true | false | 'indeterminate'
      bs: true | false | 'indeterminate'
      none: true | false | 'indeterminate'
    }
  }
  weaponType: any
  phase: any
  collection: any
  other: {
    collapsed: boolean
    switches: {
      stattrak: boolean
      souvenir: boolean
      stickers: boolean
      nametag: boolean
      onlyUnlocked: boolean
    }
  }
  stickers: {
    collapsed: boolean
    search: string
    items: any[]
  }
  lock: {
    collapsed: boolean
    min: number
    max: number
  }
  
  [rarity: string]: any
}

interface iDropdownSelect {
  options: any
  title: string
  onChange: (title: string, value: boolean) => void
  onReset: () => void
}

interface iDropdownsData {
  title: string
  options: iOption[]
}

interface iOption {
  title: string
  selected: boolean
}

const DropdownSelect = ({ options, title, onChange, onReset }: iDropdownSelect) => {
  const [count, setCount] = useState<number>(0)
  const dropdownRef = useRef<any>(null)
  const [isShow, setShow] = useState<boolean>(false)
  const handleClickOutside = () => setShow(false)
  useDetectOutsideClick(dropdownRef, handleClickOutside)
  
  const onSelect = (title: string, value: boolean) => {
    onChange(title, !value)
  }
  
  useEffect(() => {
    const length = options.filter((item: { selected: boolean }) => item.selected).length
    setCount(length)
  }, [options])
  
  return (
    <div
      className="sidebar-filter-dropdown-menu"
      ref={ dropdownRef }
    >
      <div
        className="dropdown-menu--head"
        onClick={
          (e: any) => {
            if (e.target.classList.contains('dropdown-menu--reset')) {
              e.preventDefault()
              onReset()
            } else {
              setShow(!isShow)
            }
          }
        }
      >
        <div className="dropdown-menu--title">
          { title }
          <span>({ count })</span>
        </div>
        <div className="dropdown-menu--controls">
          { count !== 0 &&
          <div
            className="dropdown-menu--reset">
            <AddIcon/>
          </div>
          }
          <div className={
            classNames(
              'dropdown-menu--arrow',
              isShow && 'active'
            )
          }
          />
        </div>
      </div>
      {
        isShow && <div className="dropdown-menu--options">
          {
            options.length > 0 && options.map((option: { title: string, selected: boolean }) => {
              const { title, selected } = option
              
              return (
                <Checkbox
                  className={
                    classNames(
                      'dropdown-menu--option',
                      selected && 'selected'
                    )
                  }
                  key={ `dropdown-item-${ option }` }
                  checked={ selected }
                  name={ title }
                  onChange={ () => onSelect(title, selected) }
                  title={ title }
                />
              )
            })
          }
        </div>
      }
    </div>
  )
}

