import classNames from 'classnames'
import React, { useRef, useState } from 'react'
import { NavLink } from 'react-router-dom'
import Switch from 'react-switch'
import { toast } from 'react-toastify'
import { ReactComponent as RemoveIcon } from '../../assets/icons/close.svg'
import { ReactComponent as ImageIcon } from '../../assets/icons/image.svg'
import { Button } from '../../components/Controls/Button/Button'
import { Input } from '../../components/Controls/Controls'
import { Dropdown } from '../../components/Controls/Dropdown/Dropdown'
import FileModalUpload from '../../components/Modal/FileModalUpload/FileModalUpload'
import { Mobile, NonTablet, Tablet } from '../../components/Responsive/Responsive'
import './TicketPage.scss'

const Files = ({files, onRemove}: any) => {
  return (
    <div className="attached">
      { files && files.map((file: any) => {
        return (
          <div className="file" key={ Math.random() }>
            <img src={ URL.createObjectURL(file) } alt=""/>
            <span>{ file.name }</span>
            <div className="remove" onClick={ () => onRemove(file) }><RemoveIcon/></div>
          </div>
        )
      }) }
    </div>
  )
}

export const TicketPage = () => {
  const [check, setCheck] = useState<boolean>(false)
  const handleChecked = (e: any) => setCheck(!check)
  
  const [isOpen, setIsOpen] = useState<boolean>(false)
  const changeIsOpen = () => setIsOpen(!isOpen)
  
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false)
  const changeIsModalOpen = () => {
    modal.current.open()
    setIsModalOpen(!isModalOpen)
  }
  
  const modal = useRef<any>(null)
  
  const [files, setFiles] = useState<any>()
  const handleUploadChange = (event: any) => {
    const files = Array.from(event?.target.files)
    
    setFiles(files)
  }
  
  const removeFileFromMessage = (file: any) => {
    const newFiles = files.filter((f: any) => f !== file)
    
    setFiles(newFiles)
  }
  
  const [errors, setErrors] = useState<boolean>(true)
  
  const submitForm = () => {
    if (selectedCategory === 'Выберите категорию') {
      setErrors(true)
      toast.error('Выберите категорию тикета', {autoClose: 10000})
    } else {
      setErrors(false)
    }
    if (!subject) {
      setErrors(true)
      toast.error('Заполните тему тикета', {autoClose: 10000})
    } else {
      setErrors(false)
    }
    if (!message) {
      setErrors(true)
      toast.error('Заполните сообщение тикета', {autoClose: 10000})
    } else {
      setErrors(false)
    }
    
    if (!errors) {
      toast.success('Тикет успешно создан', {autoClose: 10000 * Math.random() * 5})
      const ticket = {
        category: selectedCategory,
        subject,
        message,
        files: files
      }
      console.log('Тикет успешно создан', ticket)
    }
  }
  
  const categories = ['Ошибки сайта', 'Проблемы с выводом', 'Другое']
  const [selectedCategory, setSelectedCategory] = useState<any>('Выберите категорию')
  const selectCategory = (index: number) => setSelectedCategory(categories[index])
  
  const [subject, setSubject] = useState<any>(null)
  const handleSubject = (e: { target: { value: any } }) => setSubject(e.target.value)
  
  const [message, setMessage] = useState<any>(null)
  const handleMessage = (e: { target: { value: any } }) => setMessage(e.target.value)
  
  const handleFocus = (e: any) => e.target.classList.add('input-focus')
  const handleBlur = (e: any) => e.target.classList.remove('input-focus')
  
  return (
    <>
      <FileModalUpload ref={ modal } onUpload={ handleUploadChange }/>
      <Mobile>
        <div className="ticket-page mobile">
          <div className="ticket-page-head">
            <div className="ticket-page-title">
              Создать новый тикет
            </div>
            <div className="ticket-page-description">
              Обратите внимание на то, что ответы на тикеты осуществляются только на <span
              className="danger-text">русском</span> и <span
              className="danger-text">английском</span> языках
            </div>
          </div>
          <div className="ticket-page-body">
            <div
              className={
                classNames(
                  'category-selector',
                  isOpen && 'active'
                )
              }
              onClick={ changeIsOpen }
            >
              <div className="title">{ selectedCategory }</div>
              <div className="arrow"/>
              <div className="category-dropdown">
                {
                  categories.map((category: any, index) => {
                    return (
                      <div className="category-title" onClick={ () => selectCategory(index) }>
                        { category }
                      </div>
                    )
                  })
                }
              </div>
            </div>
            <input type="text" placeholder="Тема тикета" onFocus={ handleFocus } onBlur={ handleBlur }/>
            <div className="ticket-body-input">
              <div className="image-button" onClick={ changeIsModalOpen }>
                <ImageIcon/>
              </div>
              <textarea name="" className="" placeholder="Опишите суть вашей проблемы"/>
            </div>
            <Button size="normal" color="primary">Открыть тикет</Button>
            <Button size="medium" color="secondary" href="/support">Мои тикеты</Button>
          </div>
        </div>
      </Mobile>
      <Tablet>
        <div className="ticket-page tablet">
          <div className="ticket-page-head">
            <div className="ticket-page-title">
              Создать новый тикет
            </div>
            <div className="ticket-page-description">
              Обратите внимание на то, что ответы на тикеты осуществляются только на
              <span className="danger-text">русском</span> и
              <span className="danger-text">английском</span> языках
            </div>
          </div>
          <div className="ticket-page-body">
            <div className="ticket-page-body-top">
              <div
                className={
                  classNames(
                    'category-selector',
                    isOpen && 'active'
                  )
                }
                onClick={ changeIsOpen }
              >
                <div className="title">{ selectedCategory }</div>
                <div className="arrow"/>
                <div className="category-dropdown">
                  {
                    categories.map((category: any, index) => {
                      return (
                        <div className="category-title" onClick={ () => selectCategory(index) }>
                          { category }
                        </div>
                      )
                    })
                  }
                </div>
              </div>
              <input type="text" placeholder="Тема тикета" onFocus={ handleFocus } onBlur={ handleBlur }/>
            </div>
            <div className="ticket-body-input">
              <div className="image-button" onClick={ changeIsModalOpen }>
                <ImageIcon/>
              </div>
              <textarea name="" className="" placeholder="Опишите суть вашей проблемы"/>
            </div>
            <div className="ticket-body-buttons">
              <Button size="normal" color="secondary" href="/support">Мои тикеты</Button>
              <Button size="normal" color="primary">Открыть тикет</Button>
            </div>
          </div>
        </div>
      </Tablet>
      <NonTablet>
        <div className="ticket-page non-tablet page-spacing">
          <div className="ticket-page-inner">
            <div className="ticket-page-head">
              <div className="ticket-page-title">
                Создать новый тикет
              </div>
              <div className="ticket-page-description">
                Обратите внимание на то, что ответы на тикеты осуществляются только на <span
                className="attention-text">русском</span> и <span
                className="attention-text">английском</span> языках.
              </div>
              <NavLink to="/support" className="my-tickets-button">
                <Button size="normal" color="secondary">
                  Мои тикеты
                </Button>
              </NavLink>
            </div>
            <div className="ticket-page-body">
              <div className="ticket-page-form-head">
                <div
                  className={
                    classNames(
                      'category-selector',
                      isOpen && 'active'
                    )
                  }
                  onClick={ changeIsOpen }
                >
                  <div className="title">{ selectedCategory }</div>
                  <div className="arrow"/>
                  <div className="category-dropdown">
                    {
                      categories.map((category: any, index) => {
                        return (
                          <div className="category-title" onClick={ () => selectCategory(index) }>
                            { category }
                          </div>
                        )
                      })
                    }
                  </div>
                </div>
                <input
                  type="text"
                  placeholder="Заголовок"
                  onFocus={ handleFocus }
                  onBlur={ handleBlur }
                  onChange={ (event) => handleSubject(event) }
                />
              </div>
              <div className="ticket-page-form-body">
                <textarea
                  id="ticket-page-form-body"
                  placeholder="Опишите суть вашей проблемы..."
                  onChange={ (event) => handleMessage(event) }
                />
                <Files files={ files } onRemove={ (file: any) => removeFileFromMessage(file) }/>
              </div>
              <div className="ticket-page-form-footer">
                <div className="switch-read">
                  <label htmlFor="faq" className="d-flex align-center">
                    <Switch
                      id="faq"
                      onChange={ handleChecked }
                      checked={ check }
                      width={ 68 }
                      height={ 30 }
                      offColor={ '#272E3D' }
                      offHandleColor={ '#6D7681' }
                      onColor={ '#4A99EB' }
                      onHandleColor={ '#FFFFFF' }
                      handleDiameter={ 20 }
                      uncheckedIcon={ false }
                      checkedIcon={ false }
                    />
                    <span>Я прочитал раздел <NavLink to="/faq" className="attention-text">FAQ</NavLink></span>
                  </label>
                </div>
                <div className="buttons-group">
                  <div className="image-button" onClick={ () => modal.current.open() }>
                    <ImageIcon/>
                  </div>
                  <Button color="primary" size="normal" classes="create-ticket-button" onClick={ submitForm }>
                    Создать
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </NonTablet>
    </>
  )
}