import React, { useEffect, useState } from 'react'
import './AutocompleteInput.scss'
import classNames from 'classnames'
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close.svg'
import { useDebounce } from 'use-debounce/lib'

const stickerItems = [
  'Natus Vincere (Holo)',
  'Natus Vincere (Foil)',
  'Crown (Foil)',
  'Howling Dawn',
  'Flammable (Foil)',
  'Swag (Foil)'
]

export const AutocompleteInput = ({ selected, onItemSelect, value, onChange, onFocus, onBlur }: any) => {
  const [val] = useDebounce(value, 500)
  const [searchFilter, setSearchFilter] = useState<string[]>(stickerItems)

  const removeInputData = () => {
    onChange('')
    onItemSelect('')
  }
  
  useEffect(() => {
    const results = stickerItems.filter((sticker) => {
      return sticker.toLowerCase().includes(val)
    })
    setSearchFilter(results)
  }, [val])
  
  return (
    <div className="autocomplete-input">
      {
        selected
          ? (
            <div className="autocomplete-input--selected-item">
              <img src="https://steamcdn-a.akamaihd.net/apps/730/icons/econ/default_generated/weapon_ak47_gs_ak47_professional_light_large.d09d623d0a725c63e8a3905f66bba41ba2ed59e8.png" alt=""/>
              { selected }
            </div>
          )
          : (
            <input
              type="text"
              className={
                classNames(
                  'autocomplete-input--input'
                )
              }
              placeholder="Название предмета"
              value={ value }
              onChange={ (e) => onChange(e.target.value) }
              onFocus={ onFocus }
              onBlur={ onBlur }
            />
          )
      }
      {
        value.length > 0 &&
        <div
          className="remove-input-value"
          onClick={ removeInputData }
        >
          <CloseIcon/>
        </div>
      }
      {
        val.length > 0 && value.length > 0 && !selected &&
        <div className="suggestions-container">
          {
            searchFilter.length > 0 ?
              searchFilter.map((item: string) => {
                return (
                  <div
                    className="suggestions"
                    onClick={ () => onItemSelect(item) }
                  >
                    <img src="https://steamcdn-a.akamaihd.net/apps/730/icons/econ/default_generated/weapon_usp_silencer_gs_usps_hangedman_light_large.a01890fdeb8335437df7af6641a7b4f6e2fcca34.png" alt=""/>
                    { item }
                  </div>
                )
              })
              : (
                <div className="suggestions-not-found">
                  Мы не нашли такой предмет 🙁
                </div>
              )
          }
        </div>
      }
    </div>
  )
}