import classNames from 'classnames'
import React, { useState } from 'react'
import PhoneInput from 'react-phone-input-2'
import './PhoneNumberInput.scss'

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

interface iPhoneNumberInput {
  country: string
  onlyCountries: string[]
  value: string
  onChange: any
  errors: any
}

export const PhoneNumberInput = ({ onlyCountries, country, value, onChange, errors }: iPhoneNumberInput) => {

  return (
    <div
      className={
        classNames(
          errors && 'input-error',
          'phone-number-input'
        )
      }
    >
      <PhoneInput
        autoFormat={ true }
        disableSearchIcon={ true }
        enableSearch={ false }
        disableDropdown={ true }
        onlyCountries={ onlyCountries }
        country={ country }
        specialLabel={ '' }
        value={ value }
        onChange={ (phoneNumber: string) => onChange(phoneNumber) }
        onFocus={ handleFocusParent }
        onBlur={ handleBlurParent }
      />
    </div>
  )
}
