import React, { useEffect, useState } from 'react'
import { useDebounce } from 'use-debounce'
import { ReactComponent as CloseIcon } from '../../../../assets/icons/close.svg'
import { ReactComponent as StickerPlaceholder } from '../../../../assets/icons/placeholder/sticker.svg'
import { ReactComponent as SearchIcon } from '../../../../assets/icons/search.svg'
import { SidebarFilterContainer } from '../SidebarFilterContainer/SidebarFilterContainer'
import './StickersFilter.scss'

interface iStickersFilter {
  isCollapsed: boolean
  onCollapse: () => void
  onReset: () => void
  value: string
  stickers: any[]
  onChange: any
  onRemove: any
  onSelect: any
}

const handleFocusParent = (e: any) => e.target.parentNode.classList.add('input-focus')
const handleBlurParent = (e: any) => e.target.parentNode.classList.remove('input-focus')

const stickerItems = [
  'Natus Vincere (Holo)',
  'Natus Vincere (Foil)',
  'Crown (Foil)',
  'Howling Dawn',
  'Flammable (Foil)',
  'Swag (Foil)'
]

export const StickersFilter = ({ isCollapsed, onCollapse, onReset, value, stickers, onChange, onRemove, onSelect }: iStickersFilter) => {
  const [val] = useDebounce(value, 500)
  
  const [searchFilter, setSearchFilter] = useState<string[]>(stickerItems)
  
  useEffect(() => {
    const results = stickerItems.filter((sticker) => {
      return sticker.toLowerCase().includes(val)
    })
    setSearchFilter(results)
  }, [val])
  
  return (
    <SidebarFilterContainer
      title="Стикеры"
      isCollapsed={ isCollapsed }
      onCollapse={ onCollapse }
      onReset={ onReset }
      className="stickers-filter"
    >
      <div className="sticker-search-input">
        <div className="input-container">
          <SearchIcon/>
          <input
            type="text"
            placeholder="Введите название"
            value={ value }
            onChange={ (e) => onChange(e.target.value) }
            onFocus={ handleFocusParent }
            onBlur={ handleBlurParent }
          />
          {
            value.length > 0 &&
            <div
              className="remove-input-value"
              onClick={ () => onChange('') }
            >
              <CloseIcon/>
            </div>
          }
        </div>
        { val.length > 0 &&
        <div className="suggestions-container">
          {
            searchFilter.length > 0 ?
              searchFilter.map((item: string) => {
                
                return (
                  <div
                    className="suggestion"
                    onClick={ () => onSelect(item) }
                  >
                    { item }
                  </div>
                )
              })
              : (
                <div className="suggestion">
                  Мы не нашли такого стикера 🙁
                </div>
              )
          }
        </div>
        }
      </div>
      <div className="stickers-filter--stickers">
        {
          stickers && Array.from({ length: 4 }, (_, i) =>
            <div className="sticker" key={ `${ stickers[i]?.title }-${ i }` }>
              {
                stickers[i]?.icon === undefined
                  ? (
                    <div className="placeholder">
                      { stickers[i]?.title }
                      <StickerPlaceholder/>
                      <span>{ i + 1 }</span>
                    </div>
                  )
                  :
                  <div className="icon">
                    <span
                      className="remove-icon"
                      onClick={ () => onRemove(i) }
                      // onClick={ () => onRemove({ title: stickers[i]?.title, icon: stickers[i]?.icon }) }
                    >
                      <CloseIcon/>
                    </span>
                    <img src={ stickers[i]?.icon } alt=""/>
                  </div>
              }
            </div>
          )
        }
      </div>
    </SidebarFilterContainer>
  )
}