import { useLocalObservable } from 'mobx-react-lite'
import React, { Suspense } from 'react'
import ReactDOM from 'react-dom'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import App from './App'
import { Loader } from './components/Loader/Loader'
import './i18n'
import './index.scss'
import * as serviceWorker from './serviceWorker'
import { balanceStore } from './stores/balanceStore'
import { cartStore } from './stores/cartStore'
import { GameStore } from './stores/gameStore'
import { languageStore } from './stores/languageStore'

const AppContext = React.createContext<any>(null)

export const AppProvider = ({ children }: any) => {
  const gameStore = useLocalObservable(GameStore)
  const balance = useLocalObservable(balanceStore)
  const storeLanguage = useLocalObservable(languageStore)
  const storeCart = useLocalObservable(cartStore)
  
  return (
    <AppContext.Provider value={ { gameStore, balance, storeLanguage, storeCart } }>
      { children }
    </AppContext.Provider>
  )
}

export const useAppStore = () => React.useContext(AppContext)

ReactDOM.render(
  <Suspense fallback={ (<Loader/>) }>
    <AppProvider>
      <App/>
      <ToastContainer autoClose={ 10000 }/>
    </AppProvider>
  </Suspense>,
  document.getElementById('root')
)

serviceWorker.unregister()
