import React from 'react'
import './Tickets.scss'
import { Ticket } from '../Ticket/Ticket'

interface iTickets {
  tickets: any
  openChat: any
  toggleFavorite: any
}

export const Tickets = ({ tickets, openChat, toggleFavorite }: iTickets) => {
  
  if (!tickets) return (<div>У вас еще нет тикетов</div>)
  
  return tickets.map((ticket: { id: any; status: any; isFavorite: any; title: any; createTime: any; messages: any }) => {
    const { id, status, isFavorite, title, createTime, messages } = ticket
    const lastMessage = messages ? messages[messages.length - 1].message : ''
    return (
      <Ticket
        key={ id }
        toggleFavorite={ toggleFavorite }
        status={ status }
        id={ id }
        title={ title }
        createTime={ createTime }
        isFavorite={ isFavorite }
        lastMessage={ lastMessage }
        openChat={ openChat }
      />
    )
  })
}