import React from 'react'
import Scrollspy from 'react-scrollspy'

interface iFAQNavigationBlock {
  title: string
  questions: any
}

export const FAQNavigationBlock = ({ title, questions }: iFAQNavigationBlock) => {
  const questionIDs = questions.map((item: { questionId: number }) => `question-${ item.questionId }`)
  
  return (
    <div className="faq-navigation-block">
      
      <div className="faq-navigation-block-title">
        { title }
      </div>
      <div className="faq-navigation-block-links">
        <Scrollspy
          currentClassName="scroll-active"
          items={ questionIDs }
        >
          {
            questions.map((item: { questionId: any; question: any }) => {
              const { questionId, question } = item
              
              return (
                
                <a
                  href={ `#question-${ questionId }` }
                  className="faq-navigation-block-link"
                >
                  { question }
                </a>
              
              )
            })
          }
        </Scrollspy>
      </div>
    </div>
  )
}