import React from 'react'
import './DataTable.scss'
import moment from 'moment'

interface iDataTable {
  data: any[]
  size?: any
}

export const DataTable = ({ data, size }: iDataTable) => {
  switch (size) {
    case 'mobile': {
      return (
        <div className="sidebar-order-table mobile">
          <table>
            <thead>
            <tr>
              <th>
                Дата
              </th>
              <th>
                Цена
              </th>
              <th>Флоат</th>
            </tr>
            </thead>
            <tbody>
            {
              data
                ? (data.map((order: { date: any; price: any; float: any }) => {
                  const { date, price, float } = order
                  const beautifulDate = moment.unix(date).format('DD.MM.YY')
                  const beautifulTime = moment.unix(date).format('HH:mm')
                  
                  return (
                    <tr>
                      <td>
                        { beautifulDate } <span>в</span> { beautifulTime }
                      </td>
                      <td>
                        $ { price }
                      </td>
                      <td>
                        { float.toFixed(12) }
                      </td>
                    </tr>
                  )
                }))
                : (
                  <div className="empty">
                    У этого предмета еще не было заявок
                  </div>
                )
            }
            </tbody>
          </table>
        </div>
      )
    }
    default: {
      return (
        <div className="sidebar-order-table">
          <div className="sidebar-order-table-head">
            <div className="sidebar-order-table-row">
              <div className="sidebar-order-table-cell">
                Дата
              </div>
              <div className="sidebar-order-table-cell">
                Цена
              </div>
              <div className="sidebar-order-table-cell">
                Флоат
              </div>
            </div>
          </div>
          <div className="sidebar-order-table-body">
            {
              data
                ? (data.map((order: { date: any; price: any; float: any }) => {
                  const { date, price, float } = order
                  const beautifulDate = moment.unix(date).format('DD.MM.YY')
                  const beautifulTime = moment.unix(date).format('HH:mm')
                  
                  return (
                    <div className="sidebar-order-table-row">
                      <div className="sidebar-order-table-cell date">
                        { beautifulDate } <span>в</span> { beautifulTime }
                      </div>
                      <div className="sidebar-order-table-cell price">
                        $ { price }
                      </div>
                      <div className="sidebar-order-table-cell float">
                        { float.toFixed(12) }
                      </div>
                    </div>
                  )
                }))
                : (
                  <div className="empty">
                    У этого предмета еще не было заявок
                  </div>
                )
            }
          </div>
        </div>
      )
    }
  }
}