import classNames from 'classnames'
import React, { useState } from 'react'
import { ContentTabs } from '../../../../newComponents/ContentTabs/ContentTabs'
import './TableContainer.scss'
import { DataTable } from '../DataTable/DataTable'

interface iTableContainer {
  title: string
  data: any[]
  className: string | string[]
  size?: any
}

export const TableContainer = ({ title, data, className, size }: iTableContainer) => {
  const [tab, setTab] = useState<number>(10)
  // @ts-ignore
  const filteredData = data?.slice(Math.max(data.length - tab, 0)).sort().reverse()
  
  switch (size) {
    case 'tablet': {
      return (
        <div
          className={
            classNames(
              'sidebar-order-container',
              className
            )
          }
        >
          <div className="sidebar-order-container-title">
            <span>{ title }</span>
            <ContentTabs
              className="item-sales-tabs"
              items={ [10, 20, 30] }
              defaultState={ tab }
              onSelect={ (x: number) => setTab(x) }
            />
          </div>
          <DataTable data={ filteredData }/>
        </div>
      )
    }
    default: {
      return (
        <div
          className={
            classNames(
              'sidebar-order-container',
              className
            )
          }
        >
          <div className="sidebar-order-container-title">
            <span>{ title }</span>
            <ContentTabs
              className="item-sales-tabs"
              items={ [10, 20, 30] }
              defaultState={ tab }
              onSelect={ (x: number) => setTab(x) }
            />
          </div>
          <DataTable size={size} data={ filteredData }/>
        </div>
      )
    }
  }
}