import React from 'react'
import './SwitchWithLabel.scss'

interface iSwitchWithLabel {
  title: string
  name: string
  checked: boolean
  disabled?: boolean
  onChange: any
}

export const SwitchWithLabel = ({ title, name, checked, disabled = false, onChange }: iSwitchWithLabel) => {
  return (
    <label
      className="switch-with-label"
    >
      <input
        name={ name }
        type="checkbox"
        disabled={ disabled }
        checked={ checked }
        onChange={ onChange }
      />
      <span>{ title }</span>
    </label>
  )
}