import React from 'react'
import './SessionCard.scss'
import chromeIcon from '../../assets/icons/chrome-icon.png'
import { ReactComponent as CloseIcon } from '../../assets/icons/close.svg'

export const SessionCard = ({data, onClick}: any) => {
  return (
    <div className="session-card">
      <div className="session-info">
        <div className="session-title">
          <div className="session-browser-icon">
            <img src={ chromeIcon } alt=""/>
          </div>
          <div className="session-browser-name">
            {data.browser.name} {data.browser.version}
          </div>
        </div>
        <div className="session-activity">
          Последняя активность: <span>{data.activity}</span>
        </div>
        <div className="session-location">
          {data.ip} - {data.location.city}, {data.location.country}
        </div>
      </div>
      <div className="close-icon" onClick={onClick}>
        <CloseIcon/>
      </div>
    </div>
  )
}