import React from 'react'
import './SalesTable.scss'

interface iSalesTable {
  data: any[]
  size?: string
}

export const SalesTable = ({ data, size }: iSalesTable) => {
  
  switch (size) {
    case 'mobile': {
      return (
        <div className="item-sales-table">
          <table>
            <thead>
            <tr>
              <th>
                Дата
              </th>
              <th>
                Цена
              </th>
              <th>Флоат</th>
            </tr>
            </thead>
            <tbody>
            {
              data
                ? (
                  data.map((item) => {
                    const { date, float, price } = item
                    
                    return (
                      <tr>
                        <td>
                          { date }
                        </td>
                        <td>
                          { float.toFixed(12) }
                        </td>
                        <td>
                          ${ price }
                        </td>
                      </tr>
                    )
                  })
                )
                : (<div>У этого предмета пока нет продаж</div>)
            }
            </tbody>
          </table>
        </div>
      )
    }
    default: {
      return (
        <div className="item-sales-table">
          {
            data
              ? (
                <>
                  <div className="item-sales-table-head">
                    <div className="item-sales-table-row">
                      <div className="item-sales-table-cell">
                        Дата
                      </div>
                      <div className="item-sales-table-cell">
                        Флоат
                      </div>
                      <div className="item-sales-table-cell">
                        Стоимость
                      </div>
                    </div>
                  </div>
                  <div className="item-sales-table-body">
                    {
                      data.map((row) => {
                        const { date, float, price } = row
                        
                        return (
                          <div className="item-sales-table-row">
                            <div className="item-sales-table-cell">
                              { date }
                            </div>
                            <div className="item-sales-table-cell">
                              { float }
                            </div>
                            <div className="item-sales-table-cell">
                              { price }
                            </div>
                          </div>
                        )
                      })
                    }
                  </div>
                </>
              ) :
              (
                <div>У этого предмета пока нет продаж</div>
              )
          }
        </div>
      )
    }
  }
}